import {Component, AfterViewInit, OnInit, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {JSONBuilder} from '../../../../../services/JSONBuilder.service';
import { Integrations } from './../../../../../../../shared/models/integrations';
import {IntegrationService} from './../../../../../../../shared/services/integration.service';
import { CookieService, SubDomainService, FeatureAuthService} from './../../../../../../../shared/services/index';
import {ConfigIntegrationsComponent } from './../integrations.component';
declare var jQuery: any;
declare var window: any;

@Component({
  selector :'og-mailchimp-component',
  templateUrl: './mailchimp.component.html',
  styleUrls: ['./../../../assets/css/component_config.style.css', './../../../../../../../../assets/css/sahil-hover.css']
})

export class MailchimpComponent implements OnInit   {
    mailChimpallList : any = [];
    isMailChimpError: boolean = false;
    mailChimpForm : FormGroup ;
    mailchimp:boolean = false;
    mailChimpErrorMsg:string;
    isLoading: boolean = false;
    isMailchimpListExist:boolean = false;
    calcAllFileds: any= [];
    crmAllFileds: any= [];
    insertFields :any =[];
    maperror:boolean = false;
    errorMessage : string ='';
    maperrorMessage:string ;
    listId : any ;
    isListSelected :boolean = false;
    mailchimpKey:any ;
    isMailchimpConnected : boolean = false ;
    listName: string;
    selectedList :any ;
    integrations: any = new Integrations({});
    isLimited  :Boolean = false;
    isTested :Boolean = false;
    calcType: string ;
    mapFields: any = [];
    isViewed :boolean = false;
    isMapped : boolean = false;
    dataList: any= [];
    dataSend: any;
    @Output() notify = new EventEmitter();
    company :any;
    constructor(
        private  _integrationService : IntegrationService,
        private _subdomainService: SubDomainService,
        private jsonBuilderHelper: JSONBuilder,
        private fb: FormBuilder,
        private _featureAuthService : FeatureAuthService  ) {
        this.company = this._subdomainService.subDomain.company_id;
        this.mailChimpForm = this.fb.group({
           apiKey : [this.integrations.api_key, Validators.compose([Validators.required])]
       });
    }

    ngOnInit() {
      this.isListSelected = false;
    }

    connectMailChimp() {
     jQuery('#btnMailChimp').html('Please Wait...');
     let data = {
            api_key : this.mailChimpForm.value.apiKey,
            company : this.company
        };
        this._integrationService.authorization(data, 'mailchimp')
        .subscribe((response) => {
            if(response.status === 401){
              this.isMailChimpError = true;
              jQuery('#btnMailChimp').html('Connect');
              this.mailChimpErrorMsg = 'Invalid Credentials - Please re-enter your Mailchimp Api key';
            }else if(response.account_id){
              //jQuery('#add-mailchimp').modal('hide');
              jQuery('#btnMailChimp').html('Connect');
              this.isMailchimpConnected = true ;
              this.isListSelected= false;
              this.mailchimpKey = data.api_key;
              window.toastNotification('You have successfully added Mailchimp for integration.');
              //this.getAllConfiguration();
              this.getMailChimpList();

              this.notify.emit({
                 flag:true,
                 action:'connect',
                 type:'mailchimp'
              });
            }
        }, (error) => {
            this.isMailChimpError = true;
            jQuery('#btnMailChimp').html('Connect');
            if(error.error.code ==='ENOTFOUND'){
                this.mailChimpErrorMsg = 'Invalid Credentials - Please re-enter your Mailchimp Api key';
            }
        });
   }

    addIsfocusedKey() {
      jQuery('#mailChimp_apiKey').addClass('is-focused');
      this.isMailChimpError = false;
      this.mailChimpErrorMsg ='';
    }
    removeIsfocusedKey() {
      jQuery('#mailChimp_apiKey').removeClass('is-focused');
    }

    getMailChimpList(){
     this.isLoading = true ;
     this.isMailchimpConnected = true;
     this.listId = this.selectedList ;
     this._integrationService.getList('mailchimp')
        .subscribe((response) => {
          if(response.total_items!==0){
            this.mailChimpallList = [];
            this.isMailchimpListExist = true ;
            this.isLoading = false;
            let mailchimpLists = response.lists;
            for (let key in mailchimpLists) {
              if (mailchimpLists.hasOwnProperty(key)) {
                let obj = {};
                obj['list_name'] = mailchimpLists[key].name;
                obj['list_id'] = mailchimpLists[key].id;
                this.mailChimpallList.push(obj);
              }
            }
          }else{
            this.isMailchimpListExist = false;
          }
        }, (error) => {
          console.log('error',error);
            //this.isMailChimpError = true;
        });
   }

    viewTestData() {
       this.maperror = false;
       if(!this.listId || this.listId === '0'){
         // this.error = true;
          this.mailChimpErrorMsg = 'Please select the list where you want to save leads';
        }else{
          //this.error = false;
           jQuery('#btnAweberList').html('Please wait...');
           this.isListSelected = true;
           let data = {
             'list_id' : this.listId,
             'list_name' : this.listName
           }
           this._integrationService.saveList('mailchimp',this.jsonBuilderHelper.getJSONBuilt()._id,data)
            .subscribe((response:any ) => {
              this.getFields();
              //this.getCalcMapFields();    
            }
            , (error) => {
              console.log('error',error);
            });
       }
     }

    testSaveLead() {
      if(this.listId){
        this.isMailChimpError = false;
        jQuery('#btn-map').html('PLEASE WAIT...').attr('disabled', true);
        let data = {
            'map_fields' : this.insertFields,
            'list_id'    : this.listId
        }
        this._integrationService.testSaveLead('mailchimp', this.jsonBuilderHelper.getJSONBuilt()._id,data)
                .subscribe((response) => {
                if (response.status.success) {
                    this.sendMapFields();
                    window.toastNotification('Connection Test Successful');
                    this.getCalTestData(response.dataSent);
                    this.isMailchimpConnected= true;
                    this.isListSelected = false;
                    this.isViewed = true;
                    this.maperror = false;
                    this.isMapped = true ;
                    this.isTested = true;
                    if(this.isLimited){
                      jQuery('#mailchimp').modal('hide');
                    }
                    //this.updateIntegrationStaus('mailchimp', true);
                }else {
                        jQuery('#btn-map').html('Test Connection').attr('disabled', false);
                }
                }, (error) => {
                      jQuery('#btn-map').html('Test Connection').attr('disabled', false);
                      this.maperror = true ;
                      this.errorMessage = 'Invalid Credentials - Please re-enter your Mailchimp Api key';
                });
      }else{
          this.maperror = true;
          this.maperrorMessage = 'Please reconfigure & select the list where you want to save leads';
      }
    }

    getFields(){
    let isIntegrationLimited : Boolean = this._featureAuthService.features.integrations['mailchimp_limited'];
    let isIntegrationFull: Boolean = this._featureAuthService.features.integrations['mailchimp'];
    //console.log(isIntegrationFull,'LIMTED%%%%%%%%%%%%%%%%%%%%%%%',isIntegrationLimited);
    if(isIntegrationLimited && !isIntegrationFull){
       this.isLimited = true;
       this.getCalcBasicMapFields();
     }else{
       this.getCalcMapFields();
     }
   } 

   getCalTestData(data: any) {
          this.dataSend = data;
          this.dataList = [];
          for (let key in data) {
            if (data.hasOwnProperty(key)) {
              let obj = {};
              obj['key'] = key;
              obj['value'] = data[key];
              this.dataList.push(obj);
            }
          }
        //console.log(this.dataList,'dataaaaaaaaaaaaaaaaa');  
  }

    getCalcBasicMapFields() {
        this.isLoading = true;
        this._integrationService.getCalcBasicFields('mailchimp', this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          this.calcAllFileds = [];
          this.insertFields =[];
          let calcfiedls = result;
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              for(let i = 0; i <calcfiedls[key].length; i++) {
                let obj = {};
                let insertObj = {};
                obj['calc_name'] = calcfiedls[key][i].name;
                obj['crm_key'] = calcfiedls[key][i].api_key;
                obj['calc_value'] = calcfiedls[key][i].value;

                insertObj['calc'] = calcfiedls[key][i].name;
                insertObj['crm'] = calcfiedls[key][i].api_key;
                this.calcAllFileds.push(obj);
                this.insertFields.push(insertObj);
              }
            }
          }
        },(error) => {
            console.log(error,'erro');
        });
    }

    getCalcMapFields() {
     let calc_type = this.jsonBuilderHelper.getJSONBuilt().templateType;
     if (calc_type === 'Numerical') {
       this.calcType = 'Calculator';
     }else {
        this.calcType = 'Quiz';
     }
     this.isLoading = true;
     this.maperror = false ;
     this.maperrorMessage ='';
     if(this.listId!==''){
        this._integrationService.getCalcMapFields('mailchimp', this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          let calcfiedls = result.calc;
          let crmfields = result.crm;
          this.calcAllFileds = [];
          this.crmAllFileds = [];
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              let obj = {};
              obj['calc_name'] = calcfiedls[key].name;
              obj['calc_key'] = calcfiedls[key].api_key;
              obj['calc_value'] = 'Sample : ' + calcfiedls[key].value;
              this.calcAllFileds.push(obj);
            }
          }
          for (let key in crmfields) {
              let obj = {};
              obj['crm_name'] = crmfields[key].api_key;
              obj['crm_key'] = crmfields[key].api_key;
              let selectedMapFields = this.mapFields;
              let mpFiledLength = selectedMapFields.length;
              for (let i = 0 ; i < mpFiledLength; i++ ) {
                if (crmfields[key].api_key === selectedMapFields[i].crm) {
                  obj['calc_key'] =  selectedMapFields[i].calc;
                  let insertObj = {};
                  insertObj['calc'] = selectedMapFields[i].calc;
                  insertObj['crm'] = crmfields[key].api_key;
                  this.insertFields.push(insertObj);
                  break;
                }else {
                  obj['calc_key'] =  '';
                }
              }
              this.crmAllFileds.push(obj);
            }
        }, (error) => {
          this.maperror = true;
          this.isLoading = false;
          this.maperrorMessage =  error.error.err_message;
        });
     }else{
        this.isLoading= false;
        this.maperror = true;
        this.maperrorMessage = 'You have not selected the list yet. Please reconfigure.';
     }
    }
    

    sendMapFields() {
      jQuery('#btn-map').html('PLEASE WAIT').attr('disabled', false);
      let data = {
            'map_fields' : this.insertFields
           // 'list_id'    : this.listId
        }
       if(this.isLimited){
            this._integrationService.sendMapFieldsBasic('mailchimp', this.jsonBuilderHelper.getJSONBuilt()._id,data)
          .subscribe((result) => {
            this.notify.emit({
                  flag:true,
                  action:'activate',
                  type:'mailchimp'
            });
          }, (error) => {
              jQuery('#btn-map').html('Test Connection').attr('disabled', false);
          });
       }else{
            this._integrationService.sendMapFields('mailchimp', this.jsonBuilderHelper.getJSONBuilt()._id,this.insertFields)
          .subscribe((result) => {
            this.notify.emit({
                  flag:true,
                  action:'activate',
                  type:'mailchimp'
            });
          }, (error) => {
              jQuery('#btn-map').html('Test Connection').attr('disabled', false);
          });
       }  
      
    }

     getListID(list_id) {
       this.listName = jQuery('#listName :selected').text();
       if(this.listId!='' || !this.listId) {
            this.listId = '';
            this.listId = list_id;
            this.insertFields.splice(3,1);
        }else{
          this.listId  = list_id;
        }
     }

      insertMapData(calcKey: string, event: any ) {
      let obj = {};
      obj['calc'] = calcKey;
      obj['crm'] = event.target.value;
      let fieldslength = this.insertFields.length;
      if (fieldslength > 0) {
        let flagPush = true;
        for (let i = 0; i < fieldslength; i++) {
          if (calcKey === this.insertFields[i].calc) {
               this.insertFields[i]['crm'] = event.target.value;
               flagPush = false;
               //console.log('sdfsdfsfsfd',this.insertFields[i]);
               if(this.insertFields[i]['crm'] == '0') {
                 //console.log('hello');
                 this.insertFields.splice(i,1);
                 return;
               }
               break;
          }
        }
        //console.log('insertttttttttttttt', this.insertFields);
        if (flagPush) {
          this.insertFields.push(obj);
        }
      }else {
          this.insertFields.push(obj);
      }
    }

     viewMailchimp() {
       if(!this.listId || this.listId === '0'){
          this.isMailChimpError = true;
          this.mailChimpErrorMsg = 'Please select the list where you want to save leads';
        }else{
           jQuery('#btnMailChimpList').html('Please wait...');
           this.isListSelected = true;
            let data = {
             'list_id' : this.listId,
             'list_name' : this.listName
           }
           this._integrationService.saveList('mailchimp',this.jsonBuilderHelper.getJSONBuilt()._id,data)
            .subscribe((response:any ) => {
              this.getFields();
              //this.getCalcMapFields();    
            }
            , (error) => {
              console.log('error',error);
            });
           //this.getCalcMapFields();
       }
     }

    getconfig(api_key:any) {
      this.isTested = false;
      this.isListSelected= false;
      this.isMailchimpConnected = false;
      this.mailchimpKey = api_key ;
      this.integrations.api_key = this.mailchimpKey;

    }
    getMappedFields(fields :any){
      this.selectedList = fields.list_id;
      this.listName = fields.list_name;
      this.mapFields = fields.map_fields;
    }

     testView(){
        this.maperror = false ;
        this.listId = this.selectedList;
        this.isListSelected = true;
        this.isTested = true;
        this.isMapped= false;
        this.isViewed = false;
        this.getFields();
        jQuery('#mailchimp').modal('show');
    }

     close(){
      jQuery('#mailchimp').modal('hide');
      this.crmAllFileds = [];
      this.calcAllFileds = [];
      this.insertFields = [];
    }


}




