//TODO changes have not been made.
import { Component, AfterViewInit, ViewEncapsulation, OnInit, DoCheck, } from '@angular/core';
import { CalcEmail } from "../../../../models/calc_email.model";
import { JSONBuilder } from "../../../../services/JSONBuilder.service";
import { FormulaService } from "../../../../services/formula.service";
import { BuilderService } from "../../../../services/builder.service";
import { FormBuilder, Validators, FormGroup } from "@angular/forms";
import { CustomValidator } from "../../../../../templates/services/customValidation";
import { CookieService } from './../../../../../../shared/services/cookie.service';
import { FeatureAuthService } from "../../../../../../shared/services/feature-access.service";
declare var jQuery: any;

@Component({
  selector: 'config-email',
  // template:'hello this is email',
  templateUrl: './assets/html/email.template.html',
  styleUrls: ['./assets/css/wysiwyg.css'],
  encapsulation: ViewEncapsulation.None,
})

export class ConfigEmailComponent implements AfterViewInit, OnInit, DoCheck {
  options: any;
  recommendCalcEmail: CalcEmail;
  finishCalcEmail: CalcEmail;
  emailForm: FormGroup;
  currentCount: number;
  happened: boolean = false;
  isSendToUser: Boolean = false;
  isSendToSelf: Boolean = false;

  constructor(private jsonBuilderHelper: JSONBuilder,
    private formulaService: FormulaService,
    private _builderService: BuilderService, fb: FormBuilder,
    private _cookieService: CookieService,
    private _featureAuthService: FeatureAuthService
  ) {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    /* email default Text*/
    if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical') {
      var emailMessage = `<p>Hi {fullName},
                    Thank you for completing our `+ this.jsonBuilderHelper.getJSONBuilt().name + ` calculator. 
                    We wanted to send you a summary of your results.
                     Your result was {R1} and you can find more information below.</p>
                     <p>Feel free to reply to this email directly with any questions.</p>
                     <p>All the best!</p>
                     <p>` + storage.company.name + ` Team</p><p>Summary:</p><p>R1 title: {R1}</p>
                     <p>R2 title: {R2}</p><p>.</p><p>Q1 title: {Q1}</p><p>Q2 title: {Q2}</p>`;
    } else {
      var emailMessage = `<p>Hi {fullName} ,
                          </p><p>Thank you for completing our ` + this.jsonBuilderHelper.getJSONBuilt().name + ` quiz. 
                          We wanted to send you a summary of your quiz. 
                          Your outcome was {Outcome}</p><p>and you can find more information below.</p>
                          <p>Feel free to reply to this email directly with any questions.</p>
                          <p>All the best!</p><p>` + storage.company.name + `</p><p>Team</p>
                          <p>Summary:<br>Outcome: {Outcome}<br><br><br>Q1 title: {Q1}<br>Q2 title: {Q2}</p>`;

    }

    this.finishCalcEmail = new CalcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id, type: 'Finish', email: storage.user.emails[0].email, subject: this.jsonBuilderHelper.getJSONBuilt().name, message: emailMessage });
    let validators: any[] = [];
    validators.push(Validators.required);
    validators.push(CustomValidator.emailFormat);
    this.emailForm = fb.group({
      email: [this.finishCalcEmail.email, Validators.compose([Validators.required])],
    });
  }

  ngDoCheck() {
    let self = this;
    if (this.currentCount === 0 || this.currentCount != this.formulaService.allValidVariables().length) {
      if (jQuery('textarea#froala-editorFinish').data('froala.editor'))
        jQuery('textarea#froala-editorFinish').froalaEditor('destroy');

      // if (jQuery('textarea#froala-editorRecommend').data('froala.editor'))
      //     jQuery('textarea#froala-editorRecommend').froalaEditor('destroy');

      this.currentCount = this.formulaService.allValidVariables().length;
      this.updateOptions();
      jQuery('textarea#froala-editorFinish').on('froalaEditor.buttons.refresh', function (e: any, editor: any) {
        self.updateOptions();
      });

      jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
      jQuery.FroalaEditor.RegisterCommand('questions', {
        title: 'Questions list',
        type: 'dropdown',
        focus: true,
        undo: true,
        icon: 'questions',
        refreshAfterCallback: true,
        options: this.options,
        callback: function (cmd: any, val: any) {
          this.html.insert(val);
        },
        refresh: function ($btn: any) { },
        refreshOnShow: function ($btn: any, $dropdown: any) { }
      });

      if (this.jsonBuilderHelper.getJSONBuilt().template != 'sound-cloud') {
        jQuery('textarea#froala-editorFinish').froalaEditor({
          toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'color', '|', 'fontSize', '|', 'insertLink', '|', 'questions'],
          pastePlain: true,
        });
      } else {
        jQuery('textarea#froala-editorFinish').froalaEditor({
          toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'color', '|', 'fontSize', '|', 'insertLink'],
          pastePlain: true,
        });
      }

      if (!this._featureAuthService.features.confirmation_emails.active) {
        jQuery('textarea#froala-editorFinish').on('froalaEditor.mouseup', function (e: any, editor: any, mouseupEvent: any) {
          this.onClick();
        });
        jQuery('textarea#froala-editorFinish').froalaEditor('edit.off');
      }

      jQuery('textarea#froala-editorFinish').on('froalaEditor.contentChanged', function (e: any, editor: any) {
        self.finishCalcEmail.message = e.currentTarget.value;
        if (!self.happened) {
          self.happened = true;
          self.onFieldBlur(self.finishCalcEmail);
        }
      });

      // jQuery('textarea#froala-editorRecommend').froalaEditor({
      //     toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'color', '|', 'fontSize', '|', 'questions'],
      //     pastePlain: true,
      // });

      // jQuery('textarea#froala-editorRecommend').on('froalaEditor.contentChanged', function (e: any, editor: any) {
      //     self.recommendCalcEmail.message = e.currentTarget.value;
      //     self.onFieldBlur(self.recommendCalcEmail);
      // });

    }
  }
  ngOnInit() {
    this.updateOptions();
    this.jsonBuilderHelper.updateTemplateQuestionare();
    this.isSendToUser = this._featureAuthService.features.confirmation_emails.to_user;
    this.isSendToSelf = this._featureAuthService.features.confirmation_emails.to_self;
    /** read cookie */
    this.recommendCalcEmail = new CalcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id, type: 'Recommend' });
    this._builderService.calcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id })
      .subscribe(

      (response: any[]) => {
        response.forEach((res) => {
          (res.type == 'Finish') ? this.finishCalcEmail = new CalcEmail(res)
            : this.recommendCalcEmail = new CalcEmail(res);
          jQuery('textarea#froala-editorFinish').froalaEditor('html.set', (this.finishCalcEmail.message));
          jQuery('textarea#froala-editorRecommend').froalaEditor('html.set', (this.recommendCalcEmail.message));
          return;
        });
      },
      (error: any) => { console.log(error); }
      );
  }

  activateToggled() {
    this.isSendToUser = this._featureAuthService.features.confirmation_emails.to_user && this._featureAuthService.features.confirmation_emails.active;
    if (this.isSendToUser) {
      this.finishCalcEmail.sendEmail = !this.finishCalcEmail.sendEmail;
      this.onFieldBlur(this.finishCalcEmail);
    }
    else {
      this._featureAuthService.setSelectedFeature('confirmation_emails','to_user');
      jQuery('.confirmation_emails').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }

  notifyMeToggled() {
    this.isSendToSelf = this._featureAuthService.features.confirmation_emails.to_self && this._featureAuthService.features.confirmation_emails.active;
    if (this.isSendToSelf) {
      this.finishCalcEmail.notifyMe = !this.finishCalcEmail.notifyMe;
      this.onFieldBlur(this.finishCalcEmail);
    }
    else {
      this._featureAuthService.setSelectedFeature('confirmation_emails','to_self');
      jQuery('.confirmation_emails').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }

  ngAfterViewInit() {
    jQuery('textarea#froala-editorFinish').froalaEditor('html.set', (this.finishCalcEmail.message));
    /* selectize js */
    let self = this;
    jQuery('.email-selectize').selectize();
    jQuery('.email-replyto-selectize').selectize({
      plugins: ['remove_button'],
      delimiter: ',',
      persist: false,
      create: function (input: any) {
        return {
          value: input,
          text: input
        };
      }
    });
    this.currentCount = 0;
  }

  onFieldBlur(value: CalcEmail) {
    if (!this.emailForm.valid)
      this.finishCalcEmail.sendEmail = false;
    let self = this;

    this._builderService.saveCalcEmail(value)
      .subscribe(
      (response: any) => {
        self.happened = false;
        self.jsonBuilderHelper.getJSONBuilt().changed = true;
        value._id = response._id;
        self.jsonBuilderHelper.debounce(self.jsonBuilderHelper.animLoad(), 1800);
      },
      (error: any) => { console.log(error); }
      );
  }
  updateOptions() {
    this.options = '{ ';
    for (var variable in this.formulaService.allValidVariables())
      this.options += '" ' + this.formulaService.allValidVariables()[variable] + ' ":" ' + this.formulaService.allValidVariablesWysiywigList()[variable] + ' ",';

    this.options = this.options.slice(0, -1);
    this.options += '}';

    this.options = JSON.parse(this.options);
  }

  onClick() {
    if (!this._featureAuthService.features.confirmation_emails.active) {
      this._featureAuthService.setSelectedFeature('confirmation_emails');
      jQuery('.confirmation_emails').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }

}



