
import {Component, AfterViewInit, OnInit, ViewEncapsulation, ViewChild} from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {JSONBuilder} from '../../../../services/JSONBuilder.service';
import {Script} from '../../../../../../shared/services/index';
import { IntegrationService} from './../../../../../../shared/services/integration.service'
import { Integrations } from './../../../../../../shared/models/integrations';
import { CookieService, SubDomainService, FeatureAuthService} from './../../../../../../shared/services/index';
import {MailchimpComponent} from './mailchimp/mailchimp.component';
import { GetResponseComponent } from './getresponse/getresponse.component';
import {ActiveCampaignComponent} from './activeCampaign/activeCampaign.component';
import {AweberComponent} from './aweber/aweber.component';
import {HubspotComponent} from './hubspot/hubspot.component';

declare var jQuery: any;
declare var window: any;

@Component({
  selector :'config-integrations',
  templateUrl: './assets/html/integrations.template.html',
  styleUrls: ['./../../assets/css/component_config.style.css', './../../../../../../../assets/css/sahil-hover.css']
})

export class ConfigIntegrationsComponent implements AfterViewInit, OnInit {
  @ViewChild(MailchimpComponent) mailchimpComp:MailchimpComponent;
  @ViewChild(GetResponseComponent) getresponseComp:GetResponseComponent;
  @ViewChild(ActiveCampaignComponent) activeCampaignComp:ActiveCampaignComponent;
  @ViewChild(AweberComponent) awebercomp : AweberComponent;
  @ViewChild(HubspotComponent) hubspotComp : HubspotComponent;
  salesforce: boolean = false;
  marketo: boolean = false;
  activeCampaign: boolean = false;
  mailchimp:boolean = false;
  getresponse:boolean = false;
  aweber : boolean = false ;
  hubspot : boolean = false ;
  marketType: string = '';
  pendingLeadsMarketo: string;
  pendingLeadsSF: string;
  pendingLeadsMailchimp :string ;
  pendingLeadsGetresponse:string;
  pendingLeadsActiveCampaign:string;
  pendingAweber:string;
  pendingHubspot : string;
  error: boolean = false;
  maperror: boolean = false;
  company: any;
  errorMessage: string;
  addMarketo: FormGroup;
  mailChimpForm : FormGroup;
  getResponseForm : FormGroup;
  dataSend: any;
  dataList: any= [];
  calcAllFileds: any= [];
  crmAllFileds: any= [];
  insertFields: any = [];
  mailChimpallList: any= [];
  isSyncEnableMarketo: boolean = false;
  isSyncEnableSF: boolean = false;
  isSyncEnableMailchimp :boolean = false;
  isSyncEnableGetresponse : boolean = false;
  isSyncEnableActiveCampaign: boolean = false;
  isSyncEnableAweber: boolean = false ;
  isSyncEnableHubspot: boolean = false ;
  marketoTestForm: FormGroup;
  integrations: any = new Integrations({});
  isTested: boolean = false;
  isMapped: boolean = false;
  isViewed: boolean = false;
  integrationType: string;
  isLoading: boolean = false;
  syncError: boolean = false;
  marketoMapError :any = [] ;
  calcType: string;
  isAppsumoUser: boolean = false;
  isFreeLancerUser:boolean = false;
  isEssentialUser:boolean = false;
  planText:string = "";
  aweberAccounts : any ;
  mailchimpKey :any ;
  getresponseKey :any ;
  hubspotKey :any;
  isTokenExpired: boolean = false;
  marketoError : string ;
  isAweberReconfigure : boolean= false;
  isLimited :Boolean = false;
  mapFields: any =  {
    salesforce : [] ,
    marketo : [],
    mailchimp :[],
    getresponse:[],
    activeCampaign:[],
    aweber :[],
    hubspot : []
  };
  maperrorMessage: string ;
  configuration: any = {
    salesforce: false,
    marketo: false,
    mailchimp: false,
    getresponse:false,
    activeCampaign: false,
    aweber : false,
    hubspot : false
  } ;

  loader: boolean = true;
  zapierRequest: any;

    constructor(
       private _integrationService: IntegrationService,
       private jsonBuilderHelper: JSONBuilder,
       private _script: Script,
       private fb: FormBuilder,
       private _cookieService: CookieService,
       private _subdomainService: SubDomainService,
       private _featureAuthService: FeatureAuthService ) {
        // this.company = localStorage.getItem('company');
        this.company = this._subdomainService.subDomain.company_id;
    }

    ngOnInit() {
      this.isSyncEnableMarketo = false;
      this.isSyncEnableSF = false ;
      this.loader = true;
      this.isViewed = false;
      this.getsyncLead();
      //this.getMailChimpList();
      //this.getCalTestData();
      this.marketoTestForm = this.fb.group({
        firstName : [''],
        email : ['', Validators.compose([Validators.required])],
        lastName : ['']
      });
      this.addMarketo = this.fb.group({
           client_id : ['', Validators.compose([Validators.required])],
           client_secret : ['', Validators.compose([Validators.required])],
           ideantity : ['', Validators.compose([Validators.required])]
      });

      this.mailChimpForm = this.fb.group({
           apiKey : [this.integrations.api_key, Validators.compose([Validators.required])]
       });

      this.getResponseForm = this.fb.group({
           apiKey : [this.integrations.api_key, Validators.compose([Validators.required])]
       });
      this._script.runScript('zapierIntegration')
        .then((data)=>{
            if(data[0].loaded)  this.loader = false;
        })
        .catch((error)=>{

        })
        this.getConfiguration();
        this.getAllConfiguration();
        this.getCalcIntegrations();
        jQuery(".email-tabs li").on("click", function () {
          jQuery(this).addClass('active').siblings(".email-tabs li").removeClass('active');
          if(jQuery(this).hasClass('zapier')){
            jQuery('.tab-zapier').removeClass('hide').siblings('.integration-tab').addClass('hide');
          }
          else{
            jQuery('.tab-zapier').addClass('hide').siblings('.integration-tab').removeClass('hide');
          }
        });
        let storage = JSON.parse(this._cookieService.readCookie('storage'));
       // console.log(storage.company.billing.chargebee_plan_id);
        if(storage.company.is_appsumo_created === true && storage.company.billing.chargebee_plan_id==='appsumo_d'){
          this.isAppsumoUser = true ;
          this.planText = 'Appsumo';
        }
        else if(storage.company.billing.chargebee_plan_id.split('_')[0] === 'freelancer'){
          this.isFreeLancerUser = true;
          this.planText = 'Freelancer';
        }
        else if(storage.company.billing.chargebee_plan_id.split('_')[0] === 'essentials'){
          this.isEssentialUser = true;
          this.planText = 'Freelancer';
        }
    }

    ngAfterViewInit() {
          jQuery('.label-floating').addClass('is-empty');
        // console.log('integration components invoked app id is ', this.jsonBuilderHelper.getJSONBuilt()._id);
    }

    getCalcIntegrations() {
      this._integrationService.getCalcIntegrations(this.jsonBuilderHelper.getJSONBuilt()._id)
          .subscribe((result) => {
            //console.log("AAAAALLLLLLL CALCINTEGRATIONS",result);
            this.salesforce = result.salesforce.active;
            this.marketo = result.marketo.active;
            this.activeCampaign = result.active_campaign.active;
            this.mailchimp = result.mailchimp.active;
            this.getresponse = result.getresponse.active;
            this.aweber  = result.aweber.active;
            this.hubspot = result.hubspot.active;
            this.mapFields.salesforce = result.salesforce.map_fields;
            this.mapFields.marketo = result.marketo.map_fields;
            this.mapFields.activeCampaign = result.active_campaign.map_fields;
            this.mapFields.mailchimp = result.mailchimp.map_fields;
            this.mapFields.getresponse = result.getresponse.map_fields;
            this.mapFields.hubspot = result.hubspot.map_fields;
            this.mailchimpComp.getMappedFields(result.mailchimp);
            this.activeCampaignComp.getMappedFields(result.active_campaign);
            this.getresponseComp.getMappedFields(result.getresponse);
            this.hubspotComp.getMappedFields(result.hubspot);
            this.awebercomp.getMappedFields(result.aweber);
            if(this.isAweberReconfigure){
               this.awebercomp.selectAccount(result.aweber);
            }
          }, (error) => {
            console.log(error);
          });
    }

    updateCalcIntegrations(type: String, checked) {
      if(this._featureAuthService.features.integrations[<string>type] || this._featureAuthService.features.integrations[<string>type+'_limited'] ){
        if (checked === true) {
          this.testPopup(<string>type);
        }else {
        this.updateIntegrationStaus(type, checked);
        }
      }
    }

    updateIntegrationStaus(type: String, checked) {
      if ( type === 'salesforce') {
        this.salesforce = checked;
      }else if (type === 'marketo') {
        this.marketo = checked;
      }else if(type==='mailchimp'){
        this.mailchimp = checked ;
      }else if(type==='getresponse'){
        this.getresponse = checked ;
      }
      else if(type === 'active_campaign'){
        this.activeCampaign = checked ;
      }
      else if(type === 'aweber'){
        this.aweber = checked ;
      }else if(type === 'hubspot'){
        this.hubspot = checked ;
      }
      let data = {
        salesforce: type === 'salesforce' ? checked : this.salesforce,
        marketo: type === 'marketo' ? checked : this.marketo,
        mailchimp : type ==='mailchimp' ? checked : this.mailchimp,
        getresponse : type ==='getresponse' ? checked : this.getresponse,
        activeCampaign: type === 'active_campaign' ? checked : this.activeCampaign,
        aweber: type === 'aweber' ? checked : this.aweber,
        hubspot :type==='hubspot' ? checked : this.hubspot
      };

      this._integrationService.updateCalcIntegrations(data, this.jsonBuilderHelper.getJSONBuilt()._id)
          .subscribe((result) => {
              this.salesforce = result.salesforce.active;
              this.marketo = result.marketo.active;
              this.mailchimp = result.mailchimp.active;
              this.getresponse = result.getresponse.active;
              this.activeCampaign = result.active_campaign.active;
              this.aweber = result.aweber.active;
              this.hubspot = result.hubspot.active;
              let toastText = '';
              if (type === 'marketo') {
                  if (this.marketo) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Marketo';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to Marketo';
                  }
              } if (type === 'salesforce') {
                  if (this.salesforce) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Salesforce';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to  Salesforce';
                  }
              }
              if (type === 'mailchimp') {
                  if (this.salesforce) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Mailchimp';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to  Mailchimp';
                  }
              }
              if (type === 'getresponse') {
                  if (this.getresponse) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to GetResponse';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to  GetResponse';
                  }
              }
              if (type === 'active_campaign') {
                  if (this.activeCampaign) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Active Campaign';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to Active Campaign';
                  }
              }
              if (type === 'aweber') {
                  if (this.aweber) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Aweber';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to Aweber';
                  }
              }
              if (type === 'hubspot') {
                  if (this.hubspot) {
                    toastText = 'Integration Activated !! The leads from this calculator will be sent to Hubspot';
                  }else {
                    toastText = 'Integration Deactivated !! The leads from this calculator will not be sent to Hubspot';
                  }
              }
              window.toastNotification(toastText);
          }, (error) => {
            console.log(error);
          });
    }

    getAllConfiguration() {
      this._integrationService.getAllConfiguration()
        .subscribe((result) => {
          this.configuration.salesforce = result.salesforce ? result.salesforce : false;
          this.configuration.marketo = result.marketo ? result.marketo : false;
          this.configuration.mailchimp = result.mailchimp ? result.mailchimp : false;
          this.configuration.getresponse = result.getresponse ? result.getresponse : false;
          this.configuration.activeCampaign = result.active_campaign ? result.active_campaign_config : false;
          this.configuration.aweber = result.aweber ? result.aweber : false;
          this.configuration.hubspot = result.hubspot ? result.hubspot : false;
        }, (error) => {
          console.log(error);
        });
    }

    getConfiguration() {
      this._integrationService.getConfiguration()
        .subscribe((result) => {
            if (result.marketo_config) {
              this.integrations.client_id = result.marketo_config.client_id;
              this.integrations.client_secret = result.marketo_config.client_secret;
              this.integrations.ideantity = result.marketo_config.instance_url;
            }
            if(result.mailchimp_config){
              this.mailchimpKey = result.mailchimp_config.api_key ;
              this.mailchimpComp.getconfig(this.mailchimpKey);
            }
            if(result.getresponse_config){
              this.getresponseKey = result.getresponse_config.api_key ;
              this.getresponseComp.getconfig(this.getresponseKey);
            }
            if(result.hubspot_config){
              this.hubspotKey = result.hubspot_config.api_key ;
              this.hubspotComp.getconfig(this.hubspotKey );
            }
            if(result.active_campaign_config){
              this.activeCampaignComp.integrations.api_key = result.active_campaign_config.api_key;
              this.activeCampaignComp.integrations.account = result.active_campaign_config.instance_url;
              this.activeCampaignComp.activeCampaignForm.value.apiKey = result.active_campaign_config.api_key;
              this.activeCampaignComp.activeCampaignForm.value.account = result.active_campaign_config.instance_url;
            }
        }, (error) => {
          console.log(error);
        });
    }

    addIsfocusedClientID() {
       jQuery('#clientID').addClass('is-focused');
    }
     removeIsfocusedClientID() {
      jQuery('#clientID').removeClass('is-focused');
     }
     addIsfocusedSecret() {
       jQuery('#clientSecret').addClass('is-focused');
     }

    removeIsfocusedSecret(){
      jQuery('#clientSecret').removeClass('is-focused');
    }
    addIsfocusedidenatity() {
       jQuery('#ideantity').addClass('is-focused');
    }

    removeIsfocusedideantity() {
      jQuery('#ideantity').removeClass('is-focused');
    }
    testPopup(type: string) {
          if(type ==='mailchimp'){
             jQuery('#mailchimp').modal('show');
             //this.mailchimpComp.getMailChimpList();
             this.mailchimpComp.testView();
          }else if(type ==='getresponse'){
            jQuery('#get_response').modal('show');
            //this.getresponseComp.isListSelected = false;
            this.getresponseComp.getGetResponseList();
          }else if(type ==='active_campaign'){
            this.activeCampaignComp.activeCampaignConnect = false;
            this.activeCampaignComp.activeCampaignLead = false;
            this.activeCampaignComp.activeCampaignList = true;
            jQuery('#active-campaign').modal('show');
            this.activeCampaignComp.getList('active_campaign');
          }else if(type ==='aweber'){
             jQuery('#add-aweber').modal('show');
             this.awebercomp.testView();
          }else if(type ==='hubspot'){
             jQuery('#hubspot').modal('show');
             this.hubspotComp.testView();
          }
          else{
            this.calcAllFileds = [];
            this.crmAllFileds = [];
            this.insertFields = [];
            this.error = false ;
            this.getFields(type);
            //this.getCalcMapFields(type);
            this.marketType = type;
            this.isTested = true;
            this.isMapped = false;
            jQuery('#marketo').modal('show');
          }
    }
    testSaveLead() {
      jQuery('#btn-map').html('PLEASE WAIT...').attr('disabled', true);
      let type = this.integrationType ;
      this.isTokenExpired = false;
      let data = {
        'map_fields' : this.insertFields
      }
      //console.log(data.map_fields.length,'$$mappppppppppppppppppppppppppppp');
      if(data.map_fields.length > 0 ){
       this._integrationService.testSaveLead(type, this.jsonBuilderHelper.getJSONBuilt()._id, data)
            .subscribe((response) => {
              if (response.status.success) {
                  if(response.status.item_skipped >= 1){
                    this.maperror = true;
                    this.isMapped = false;
                    this.isTested = true;
                    if(type==='marketo'){
                      this.marketoMapError  = response.status.response[0];
                    }else{
                      this.maperrorMessage = response.status;
                    }
                    jQuery('#btn-map').html('Test Connection').attr('disabled', false);
                  }else{
                    this.sendMapFields();
                    //jQuery('#success-marketo-old').modal('show');
                    this.getCalTestData(response.dataSent);
                    this.isViewed = true;
                    this.maperror = false;
                    this.isTested = false;
                    this.isMapped = true;
                    if(this.isLimited){
                      jQuery('#marketo').modal('hide');
                    }
                    this.updateIntegrationStaus(this.integrationType, true);
                    window.toastNotification('Connection Test Successful');
                  }
              }else {
                    this.maperror = true;
                    this.isMapped = false;
                    this.isTested = true;
                    let errorCode= response.status.code ;
                    jQuery('#btn-map').html('Test Connection').attr('disabled', false);
                    if(type ==='salesforce' && response.status.fields){
                      let inValidField = response.status.fields[0];
                      if(inValidField){
                        this.maperrorMessage = 'Invalid value for field ' + inValidField ;
                      }else{
                         this.maperrorMessage ='Error in one or more of your mappings. Please map fields again.';
                      }
                    }else if(type ==='marketo' &&  errorCode ==='609'){
                      this.maperrorMessage = 'Error in one of your primary key mapping. Please map fields again.' ;
                    } else{
                      this.maperrorMessage = response.status.message;
                    }
              }
            }, (error) => {
              this.maperror = true;
              this.maperrorMessage = error.error.err_message;
              this.getCalTestData(type);
              jQuery('#btn-map').html('Test Connection').attr('disabled', false);
            });
      }else{
          jQuery('#btn-map').html('Test Connection').attr('disabled', false);
          this.maperror = true;
          if(type==='marketo'){
             this.maperrorMessage = 'Minimum 1 field is required for Marketo';
          }else{
                this.maperrorMessage = 'Company & Last Name are required for Salesforce';
          }
      }
    }

    getsyncLead() {
        this.isSyncEnableMarketo = false;
        this.isSyncEnableSF = false;
        this.isSyncEnableMailchimp = false;
        this.isSyncEnableGetresponse = false;
        this.isSyncEnableActiveCampaign = false;
        this._integrationService.getCalcSyncLeads(this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
            //console.log("SYNC",result);
            this.pendingLeadsSF = result.salesforce + ' item';
            this.pendingLeadsMarketo = result.marketo + ' item';
            this.pendingLeadsMailchimp = result.mailchimp + ' item';
            this.pendingLeadsGetresponse = result.getresponse + ' item';
            this.pendingLeadsActiveCampaign = result.active_campaign + ' item';
            this.pendingAweber = result.aweber + ' item';
            this.pendingHubspot = result.hubspot + ' item';
            if (this.pendingLeadsMarketo >= '1') {
                this.isSyncEnableMarketo = true;
            }
            if (this.pendingLeadsSF >= '1') {
                this.isSyncEnableSF = true;
            }
            if (this.pendingLeadsMailchimp >= '1') {
                this.isSyncEnableMailchimp = true;
            }
            if (this.pendingLeadsGetresponse >= '1') {
                this.isSyncEnableGetresponse = true;
            }
            if (this.pendingLeadsActiveCampaign >= '1') {
                this.isSyncEnableActiveCampaign = true;
            }
            if (this.pendingAweber >= '1') {
                this.isSyncEnableAweber = true;
            }
            if(this.pendingHubspot >= '1'){
              this.isSyncEnableHubspot = true;
            }
            //console.log(this.isSyncEnableHubspot, 'iiiiiiiiiiiiiiiiiiiiiiiiii')
        }, (error) => {
          console.log(error);
        });
    }

   syncLead(type: string) {
     this.integrationType = type;
     this.synLoading(type);
     this._integrationService.syncCalcLeads(type, this.jsonBuilderHelper.getJSONBuilt()._id)
      .subscribe((result) => {
        if (result.success) {
          this.syncResult(type,'false');
          this.getsyncLead();
          if(type==='salesforce'){
            type = 'Salesforce';
          }else if(type==='marketo'){
            type='Marketo';
          }else if(type==='getresponse'){
            type='GetResponse';
          }else if(type==='active_campaign'){
            type='Active Campaign';
          }else if(type==='aweber'){
            type='Aweber';
          }else if(type==='hubspot'){
            type='HubSpot';
          }else if(type==='mailchimp'){
            type='MailChimp';
          }
          window.toastNotification('Leads will be synced to connected ' + type + ' for this calculator');
        }
        else {
            this.syncError = true;
            this.syncResult(type,'true');
            jQuery('#syncError-modal').modal('show');
        }
      }, (error) => {
          this.syncError = true;
          this.syncResult(type,'true');
          jQuery('#syncError-modal').modal('show');
          //window.toastNotification('Some error occured!! Please Reconfigure' + type);
      });
   }

   synLoading(type:string){
     switch(type){
          case "salesforce":   
              jQuery('.sync-leads-sf').html('Please Wait..');
              break;
          case "marketo":   
              jQuery('.sync-leads-marketo').html('Please Wait..');
              break;    
          case "mailchimp":   
              jQuery('.sync-leads-mailchimp').html('Please Wait..');
              break;
          case "getresponse":   
              jQuery('.sync-leads-getresponse').html('Please Wait..');
              break;
          case "active_campaign":   
              jQuery('.sync-leads-activeCampaign').html('Please Wait..');
              break;
          case "aweber":   
              jQuery('.sync-leads-aweber').html('Please Wait..');
              break;
          case "hubspot":   
              jQuery('.sync-leads-hubspot').html('Please Wait..');
              break;    
      }
   }
   syncResult(type:string, response :string){
     switch(response){
          case "false":   
            switch(type){
              case "salesforce":   
                  this.isSyncEnableSF = false ;
                  jQuery('.sync-leads-sf').html('Sync');
                  break;
              case "marketo":   
                  this.isSyncEnableMarketo = false ;
                  jQuery('.sync-leads-marketo').html('Sync');
                  break;    
              case "mailchimp":   
                  this.isSyncEnableMailchimp = false;
                  jQuery('.sync-leads-mailchimp').html('Sync');
                  break;
              case "getresponse":   
                  this.isSyncEnableGetresponse = false;
                  jQuery('.sync-leads-getresponse').html('Sync');
                  break;
              case "active_campaign":   
                  this.isSyncEnableActiveCampaign = false;
                  jQuery('.sync-leads-activeCampaign').html('Sync');
                  break;
              case "aweber":
                  this.isSyncEnableAweber = false ;
                  jQuery('.sync-leads-aweber').html('Sync');
                  break;
              case "hubspot":   
                  this.isSyncEnableHubspot = false ;
                  jQuery('.sync-leads-hubspot').html('Sync');
              break;   
            }
         case "true":   
            switch(type){
              case "salesforce":   
                  this.isSyncEnableSF = true ;
                 jQuery('.sync-leads-sf').html('Sync (' + this.pendingLeadsSF + ')');
                  break;
              case "marketo":   
                  this.isSyncEnableMarketo = true ;
                  jQuery('.sync-leads-marketo').html('Sync (' + this.pendingLeadsMarketo + ')');
                  break;    
              case "mailchimp":   
                  this.isSyncEnableMailchimp = true;
                  jQuery('.sync-leads-mailchimp').html('Sync (' + this.pendingLeadsMailchimp + ')');
                  break;
              case "getresponse":   
                  this.isSyncEnableGetresponse = true;
                  jQuery('.sync-leads-getresponse').html('Sync (' + this.pendingLeadsGetresponse + ')');
                  break;
              case "active_campaign":   
                  this.isSyncEnableActiveCampaign = true;
                  jQuery('.sync-leads-activeCampaign').html('Sync (' + this.pendingLeadsActiveCampaign + ')');
                  break;
              case "aweber":   
                  this.isSyncEnableAweber = true ;
                  jQuery('.sync-leads-aweber').html('Sync (' + this.pendingAweber + ')');
                  break;
              case "hubspot":   
                  this.isSyncEnableHubspot = true ;
                  jQuery('.sync-leads-hubspot').html('Sync (' + this.pendingHubspot + ')');
                  break;    
              }
          default: break; 
        }
     }

   getCalTestData(data: any) {
          this.dataSend = data;
          this.dataList = [];
          for (let key in data) {
            if (data.hasOwnProperty(key)) {
              let obj = {};
              obj['key'] = key;
              obj['value'] = data[key];
              this.dataList.push(obj);
            }
          }
  }
   
   connectIntegrations(type:string){
      if(!this._featureAuthService.features.integrations[type] && !this._featureAuthService.features.integrations[type + '_limited']){
        return;
      }
      this.integrationType = type;
      jQuery('#'+type).modal('show');
      if(type ==='mailchimp'){
        this.mailchimpComp.getconfig(this.mailchimpKey);
      }
      if(type ==='get_response'){
        this.getresponseComp.getconfig(this.getresponseKey);
      }if(type==='hubspot'){
        this.hubspotComp.getconfig(this.hubspotKey);
      }
      if(type==='marketo'){
        this.isTested = false ;
        this.isMapped = false;
      }
      
   }

   openAuthModal(type :string) {
     if(!this._featureAuthService.features.integrations[type] && !this._featureAuthService.features.integrations[type+ '_limited']){
       return;
     }
     jQuery('#mapping-int-toAccess').modal('show');
     this.integrationType = type;
   }
   
   connectoAuthIntegrations(type: string) {
        if(!this._featureAuthService.features.integrations[type] && !this._featureAuthService.features.integrations[type+ '_limited']){
            return;
        }
        jQuery('#mapping-int-toAccess').modal('hide');
        let data = {} ;
        this._integrationService.getLink(data, type)
        .subscribe((response) => {
             let companyId: string = <string>this._subdomainService.subDomain.company_id;
             this._cookieService.createCookie('comp', companyId , 3);
             let newWindow = window.open(response, 'GoogleWindow', 'width=600, height=500,scrollbars=yes');
             let self = this;
             let interval = setInterval(function(){
                 let cookie = self._cookieService.readCookie('comp');
                 if (cookie === 'success') {
                     newWindow.close();
                     self.getAllConfiguration();
                     if(type ==='aweber'){
                         self.isAweberReconfigure= true;
                         self.getCalcIntegrations();
                         //self.awebercomp.selectAccount(self.aweberConfiguration);
                     }else{
                        self.updateCalcIntegrations('salesforce', true);
                     }
                     window.toastNotification('You have successfully added '+ type +' for integration, Please test it by clicking on Test Connection.');
                     clearInterval(interval);
                 }else if (cookie === null) {
                       clearInterval(interval);
                       newWindow.close();
                 }
             }, 5000);
        }, (error) => {
            console.log(error);
        });
   }

   connectMarketo() {
     this.isTested = false;
       jQuery('#btnmMarketo').html('please Wait...').attr('disabled', true);
        let data = {
            client_id : this.addMarketo.value.client_id,
            client_secret : this.addMarketo.value.client_secret,
            restEndpoint : this.addMarketo.value.ideantity,
            company : this.company
        };
       this._integrationService.authorization(data, 'marketo')
        .subscribe((response) => {
              if (response.errors) {
                this.errorMessage = 'Incorrect Endpoints. Please re-enter correct endpoint for Marketo.';
                this.error = true;
                jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
             }else if (response.error) {
               if (response.error === 'unauthorized')  {
                   this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Id';
               }else if (response.error === 'invalid_client') {
                    this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Secret';
               }else {
                    this.errorMessage = response.error_description;
               }
               this.error = true;
               jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
             }else {
                this.error = false;
                jQuery('#btnmMarketo').html('Reconfigure Marketo').attr('disabled', false);
                this.configuration.marketo = true;
                this.isTested = true;
                this.getFields('marketo');
                //this.getCalcMapFields('marketo');
                //jQuery('#add-marketo').modal('hide');
                window.toastNotification('You have successfully added Marketo for integration, Please test it by clicking on Test Connection.');
            }
        }, (error) => {
          if (error.error.code === 'ENOTFOUND' || error.error.code === 'E_UNEXPECTED') {
                 this.errorMessage = 'Authentication error - Please re-enter the end points';
            }else {
                this.errorMessage = error.error.message;
            }
          this.error = true;
          jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
        });
   }

    getFields(type:string){
    this.integrationType = type;  
    let isIntegrationLimited : Boolean = this._featureAuthService.features.integrations[type+ '_limited'];
    let isIntegrationFull: Boolean = this._featureAuthService.features.integrations[type];
     if(isIntegrationLimited && !isIntegrationFull){
        this.isLimited = true;
       this.getCalcBasicMapFields(type);
     }else{
       this.getCalcMapFields(type);
     }
     //console.log(this.isLimited,'islimieeeeeeeeeeeeeeeeeeeeeee',isIntegrationLimited,'aaaaaaaaa',isIntegrationFull);
   } 

   getCalcMapFields(type: string) {
     let calc_type = this.jsonBuilderHelper.getJSONBuilt().templateType;
     if (calc_type === 'Numerical') {
       this.calcType = 'Calculator';
     }else {
        this.calcType = 'Quiz';
     }
     this.isLoading = true;
     this.maperror = false ;
     this.integrationType = type;
        this._integrationService.getCalcMapFields(type, this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          let calcfiedls = result.calc;
          let crmfields = result.crm;
          this.calcAllFileds = [];
          this.crmAllFileds = [];
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              let obj = {};
              obj['calc_name'] = calcfiedls[key].name;
              obj['calc_key'] = calcfiedls[key].api_key;
              obj['calc_value'] = 'Sample : ' + calcfiedls[key].value;
              this.calcAllFileds.push(obj);
            }
          }
          for (let key in crmfields) {
            if (crmfields.hasOwnProperty(key) && crmfields[key].api_key !== 'Name') {
              let obj = {};
              obj['crm_name'] = crmfields[key].name;
              obj['crm_key'] = crmfields[key].api_key;
              let selectedMapFields = this.mapFields[type];
              let mpFiledLength = selectedMapFields.length;
              for (let i = 0 ; i < mpFiledLength; i++ ) {
                if (crmfields[key].api_key === selectedMapFields[i].crm) {
                  //console.log(crmfields[key].api_key,'mapeedededsjdhgsjbhfsjdgfjsdgfs',selectedMapFields[i].calc);
                  obj['calc_key'] =  selectedMapFields[i].calc;
                  let insertObj = {};
                  insertObj['calc'] = selectedMapFields[i].calc;
                  insertObj['crm'] = crmfields[key].api_key;
                  this.insertFields.push(insertObj);
                  break;
                }else {
                  obj['calc_key'] =  '';
                }
              }
              this.crmAllFileds.push(obj);
            }
          }
        }, (error) => {
          this.maperror = true;
          this.isTokenExpired = true ;
          this.isLoading = false;
          if (error.error.err_message === 'invalid_grant: expired access/refresh token') {
              this.maperrorMessage = 'Configuration error. Seems like your access token has expired. Please';
          }else if (error.error.err_message === 'The REST API is not enabled for this Organization.') {
              this.maperrorMessage = 'REST API not enabled for your Salesforce account. You require Enterprise edition of Salesforce for Integration. Please';
          }else {
               this.maperrorMessage =  error.error.err_message;
          }
          //this.maperrorMessage = error.error.err_message + '. Please ';
        });
    }

    insertMapData(calcKey: string, event: any ) {
      let obj = {};
      obj['calc'] = calcKey;
      obj['crm'] = event.target.value;
      let fieldslength = this.insertFields.length;
      if (fieldslength > 0) {
        let flagPush = true;
        for (let i = 0; i < fieldslength; i++) {
          if (calcKey === this.insertFields[i].calc) {
               this.insertFields[i]['crm'] = event.target.value;
               flagPush = false;
               //console.log('sdfsdfsfsfd',this.insertFields[i]);
               if(this.insertFields[i]['crm'] == '0') {
                 //console.log('hello');
                 this.insertFields.splice(i,1);
                 return;
               }
               break;
          }
        }
        if (flagPush) {
          this.insertFields.push(obj);
        }
      }else {
          this.insertFields.push(obj);
      }
    }

    sendMapFields() {
      jQuery('#btn-map').html('PLEASE WAIT').attr('disabled', false);
      let type = this.integrationType;
      let data = {
            'map_fields' : this.insertFields
      }
      if(this.isLimited){
        this._integrationService.sendMapFieldsBasic(type, this.jsonBuilderHelper.getJSONBuilt()._id,data)
        .subscribe((result) => {
          //console.log(result,'resssssssssssssssssssssssssssssssssssss');
        }, (error) => {
            jQuery('#btn-map').html('Test Connection').attr('disabled', false);
        });
      }else{
        this._integrationService.sendMapFields(type, this.jsonBuilderHelper.getJSONBuilt()._id, this.insertFields)
        .subscribe((result) => {
            this.getCalcIntegrations();
        }, (error) => {
            this.isTested = true;
            this.isMapped = false;
            jQuery('#btn-map').html('Test Connection').attr('disabled', false);
        });
      }
    }
    close() {
         jQuery('#mapping-int-toAccess').modal('hide');
         jQuery('#marketo').modal('hide');
         this.crmAllFileds = [];
         this.calcAllFileds = [];
         this.insertFields = [];
    }
    closeMapping() {
      this.crmAllFileds = [];
      this.calcAllFileds = [];
      this.insertFields = [];
      jQuery('#marketo').modal('hide');
    }
    closeReviwData() {
      this.crmAllFileds = [];
      this.calcAllFileds = [];
      this.insertFields = [];
      jQuery('#marketo').modal('hide');
    }


    viewMapData(type: string) {
        this.isMapped = true;
        this.isViewed = true;
    }

    openPopup(type,action){
      let featureType = type.split('-').join('_');
     if(!this._featureAuthService.features.integrations[featureType] && !this._featureAuthService.features.integrations[featureType+ '_limited']){
       return;
     }
      switch (action) {
        case "configure":
          this.activeCampaignComp.activeCampaignList = false;
          this.activeCampaignComp.activeCampaignLead = false;
          this.activeCampaignComp.activeCampaignConnect = true;
          break;
        default:
          break;
      }
      jQuery('#'+type).modal('show');
      this.integrationType = featureType;
   }

    getCalcBasicMapFields(type) {
        this.isLoading = true;
        this._integrationService.getCalcBasicFields(type, this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          this.calcAllFileds = [];
          this.insertFields =[];
          let calcfiedls = result;
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              for(let i = 0; i <calcfiedls[key].length; i++) {
                let obj = {};
                let insertObj = {};
                obj['calc_name'] = calcfiedls[key][i].name;
                obj['crm_key'] = calcfiedls[key][i].api_key;
                obj['calc_value'] = calcfiedls[key][i].value;

                insertObj['calc'] = calcfiedls[key][i].name;
                insertObj['crm'] = calcfiedls[key][i].api_key;
                this.calcAllFileds.push(obj);
                this.insertFields.push(insertObj);
              }
            }
          }
        },(error) => {
            //console.log(error,'errorrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrr');
        });
    }

   checkFeatureAccess(type : string){
    let self = this;
     let available: Boolean = this._featureAuthService.features.integrations[type];
     if(!available){
        jQuery('div:not(#premiumModal)').modal('hide');
        //console.log(type,'>>>>>>>>>>>>>>>>>>>>>>>>>');
         self._featureAuthService.setSelectedFeature('integrations',type);
         jQuery('.integrations').addClass('activegreen limited-label');
         jQuery('#premiumModal').modal('show');
         jQuery('.modal-backdrop').insertAfter('#premiumModal');
         jQuery('#analyticsRef').attr('active',false);
     }
   }

   returnFeatureAccess(type : string){
     let isIntegrartionLimited : boolean = this._featureAuthService.features.integrations[type + '_limited'];
     let isIntegrationFull :boolean = this._featureAuthService.features.integrations[type];
     if(isIntegrartionLimited || isIntegrationFull ) {
       return true;
     }else if(!isIntegrationFull && !isIntegrartionLimited){
       return false;
     }
     //return this._featureAuthService.features.integrations[type];
   }

    childListener(event){
      // console.log("dfasdfasv",event);
     switch (event.action) {
       case "connect":
         switch(event.type){
            case "mailchimp":  this.configuration.mailchimp = true;break;
            case "active_campaign": this.configuration.activeCampaign = event.data; break;
            case "getresponse": this.configuration.getresponse = event.flag; break;
            case "hubspot": this.configuration.hubspot = true;break;
            default: break;
         }
         break;
       case  "activate":
         switch(event.type){
            case "mailchimp":  this.mailchimp = true;break;
            case "hubspot":  this.hubspot = true;break;
            case "active_campaign": this.activeCampaign = true; break;
            case "getresponse": this.getresponse = event.flag; break;
            case "aweber": this.aweber = event.flag; break;
            default: break;
         }
         break;
       default:
         // code...
         break;
     }
   }
   premiumPopup(feature:string, sub:string){
      this._featureAuthService.setSelectedFeature(feature,sub);
      jQuery('.'+feature).addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
}




