import {Component, AfterViewInit, OnInit, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {JSONBuilder} from '../../../../../services/JSONBuilder.service';
import { Integrations } from './../../../../../../../shared/models/integrations';
import {IntegrationService} from './../../../../../../../shared/services/integration.service';
import { CookieService, SubDomainService, FeatureAuthService} from './../../../../../../../shared/services/index';
import {ConfigIntegrationsComponent } from './../integrations.component';
declare var jQuery: any;
declare var window: any;

@Component({
  selector :'og-hubspot-component',
  templateUrl: './hubspot.component.html',
  styleUrls: ['./../../../assets/css/component_config.style.css', './../../../../../../../../assets/css/sahil-hover.css']
})

export class HubspotComponent implements OnInit   {

    isHubspotError: boolean = false;
    hubspotForm : FormGroup ;
    hubspot:boolean = false;
    hubspotErrorMsg:string;
    isLoading: boolean = false;
    isHubspotListExist:boolean = false;
    calcAllFileds: any= [];
    crmAllFileds: any = [];
    insertFields :any =[];
    maperror:boolean = false;
    errorMessage : string ='';
    maperrorMessage:string ;
    hubspotKey:any ;
    isHubspotConnected : boolean = false ;
    selectedList :any ;
    integrations: any = new Integrations({});
    mapFields: any = [];
    isMapped : boolean = false;
    isTested : Boolean = false;
    dataList : any  = [];
    dataSend : any;
    isLimited: Boolean = false;
    @Output() notify = new EventEmitter();
    company :any;
    constructor(
        private  _integrationService : IntegrationService,
        private _subdomainService: SubDomainService,
        private jsonBuilderHelper: JSONBuilder,
        private fb: FormBuilder,
        private _featureAuthService : FeatureAuthService  ) {
        this.company = this._subdomainService.subDomain.company_id;
        this.hubspotForm = this.fb.group({
           apiKey : [this.integrations.api_key, Validators.compose([Validators.required])]
       });
    }

    ngOnInit() {
      
    }

    connectHubspot() {
     jQuery('#btnHubspot').html('Please Wait...');
     let data = {
            api_key : this.hubspotForm.value.apiKey,
            company : this.company
        };
        this._integrationService.authorization(data, 'hubspot')
        .subscribe((response) => {
             if (response.status === 'error') {
              this.isHubspotError = true;
              this.isHubspotConnected = false;
              jQuery('#btnHubspot').html('Connect');
              this.hubspotErrorMsg = 'Invalid Credentials - Please re-enter your Hubspot Api key';
            }else{
              this.isHubspotError = false;
              this.isHubspotConnected = true;
              jQuery('#hubspot').modal('hide');
              jQuery('#btnHubspot').html('Connect');
              window.toastNotification('You have successfully added Hubspot for integration.');
            }
            this.notify.emit({
              flag:this.isHubspotConnected,
              action:'connect',
              type:'hubspot'
            });
        }, (error) => {
            this.isHubspotError = true;
            jQuery('#btnHubspot').html('Connect');
            if(error.error.code ==='ENOTFOUND'){
                this.hubspotErrorMsg = 'Invalid Credentials - Please re-enter your Hubspot Api key';
            }
        });
   }

    addIsfocusedKey() {
      jQuery('#hubspot_apiKey').addClass('is-focused');
      this.isHubspotError = false;
      this.hubspotErrorMsg ='';
    }
    removeIsfocusedKey() {
      jQuery('#hubspot_apiKey').removeClass('is-focused');
    }

    // testView(){
    //     this.isHubspotConnected = true;
    //     this.isMapped = false;
    //     this.getFields();
    //     //this.getCalcMapFields();
    // }
    testView(){
        this.maperror = false ;
        this.isHubspotConnected = true;
        this.isMapped = false;
        this.isTested = true;
        this.getFields();
        //jQuery('#hubspot').modal('show');
    }

    testSaveLead() {
        this.isHubspotError = false;
        jQuery('.btn-map-hubspot').html('PLEASE WAIT...').attr('disabled', true);
        let data = {
            'map_fields' : this.insertFields,
        }
        this._integrationService.testSaveLead('hubspot', this.jsonBuilderHelper.getJSONBuilt()._id,data)
                .subscribe((response) => {
                if (response.status.success) {
                    this.sendMapFields();
                    this.maperror = false;
                    this.isTested = true;
                    this.isMapped = true;
                    this.isHubspotConnected = false;
                    this.getCalTestData(response.dataSent);
                    if(this.isLimited){
                      jQuery('#hubspot').modal('hide');
                    }
                    window.toastNotification('Connection Test Successful');
                }else {
                        jQuery('.btn-map-hubspot').html('Test Connection').attr('disabled', false);
                        this.maperror = true;
                        this.maperrorMessage = response.status.message;
                }
                }, (error) => {
                        jQuery('.btn-map-hubspot').html('Test Connection').attr('disabled', false);
                        this.maperror = true ;
                        this.errorMessage = 'Invalid Credentials - Please re-enter your Hubspot Api key';
                });
        
    }

    getCalcMapFields() {
        this.isLoading = true;
        this._integrationService.getCalcMapFields('hubspot', this.jsonBuilderHelper.getJSONBuilt()._id)
         .subscribe((result) => {
          this.isLoading = false;
          let calcfiedls = result.calc;
          let crmfields = result.crm;
          this.insertFields = [];
          this.calcAllFileds = [];
          this.crmAllFileds = [];
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              let obj = {};
              obj['calc_name'] = calcfiedls[key].name;
              obj['calc_key'] = calcfiedls[key].api_key;
              obj['calc_value'] = 'Sample : ' + calcfiedls[key].value;
              this.calcAllFileds.push(obj);
            }
          }
          for (let key in crmfields) {
              let obj = {};
              obj['crm_name'] = crmfields[key].api_key;
              obj['crm_key'] = crmfields[key].api_key;
              let selectedMapFields = this.mapFields;
              let mpFiledLength = selectedMapFields.length;
              for (let i = 0 ; i < mpFiledLength; i++ ) {
                if (crmfields[key].api_key === selectedMapFields[i].crm) {
                  obj['calc_key'] =  selectedMapFields[i].calc;
                  let insertObj = {};
                  insertObj['calc'] = selectedMapFields[i].calc;
                  insertObj['crm'] = crmfields[key].api_key;
                  this.insertFields.push(insertObj);
                  break;
                }else {
                  obj['calc_key'] =  '';
                }
              }
              this.crmAllFileds.push(obj);
            }
        },(error) => {
            //console.log(error,'erro')
        });
    }

    insertMapData(calcKey: string, event: any ) {
      let obj = {};
      obj['calc'] = calcKey;
      obj['crm'] = event.target.value;
      let fieldslength = this.insertFields.length;
      if (fieldslength > 0) {
        let flagPush = true;
        for (let i = 0; i < fieldslength; i++) {
          if (calcKey === this.insertFields[i].calc) {
               this.insertFields[i]['crm'] = event.target.value;
               flagPush = false;
               if(this.insertFields[i]['crm'] == '0') {
                 //console.log(this.insertFields,'mapppinggggggg');
                 this.insertFields.splice(i,1);
                 return;
               }
               break;
          }
        }
        if (flagPush) {
          this.insertFields.push(obj);
        }
      }else {
          this.insertFields.push(obj);
      }
    }

    sendMapFields() {
      jQuery('.btn-map-hubspot').html('PLEASE WAIT').attr('disabled', false);
      let data = {
            'map_fields' : this.insertFields
        }
        if(this.isLimited){
            this._integrationService.sendMapFieldsBasic('hubspot', this.jsonBuilderHelper.getJSONBuilt()._id,data)
            .subscribe((result) => {
              this.notify.emit({
                    flag:true,
                    action:'activate',
                    type:'hubspot'
              });
            }, (error) => {
                jQuery('.btn-map-hubspot').html('Test Connection').attr('disabled', false);
            });
       }else{
            this._integrationService.sendMapFields('hubspot', this.jsonBuilderHelper.getJSONBuilt()._id,this.insertFields)
            .subscribe((result) => {
              this.notify.emit({
                    flag:true,
                    action:'activate',
                    type:'hubspot'
              });
            }, (error) => {
                jQuery('.btn-map-hubspot').html('Test Connection').attr('disabled', false);
            });
       }
    }

    getconfig(api_key:any) {
      //this.hubspotKey = api_key ;
      this.integrations.api_key = api_key;
      this.isHubspotConnected = false;
      this.isMapped= false;
      this.isTested = false;
      //console.log(api_key,'iiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiiii');
    }

    getMappedFields(fields :any){
      this.mapFields = fields.map_fields;
    }

    getFields(){
    let isIntegrationLimited : Boolean = this._featureAuthService.features.integrations['hubspot_limited'];
    let isIntegrationFull: Boolean = this._featureAuthService.features.integrations['hubspot'];
    if(isIntegrationLimited && !isIntegrationFull){
       this.isLimited = true;
       this.getCalcBasicMapFields();
     }else{
       this.getCalcMapFields();
     }
   } 

     getCalTestData(data: any) {
        this.dataSend = data;
        this.dataList = [];
        for (let key in data) {
            if (data.hasOwnProperty(key)) {
                let obj = {};
                obj['key'] = key;
                obj['value'] = data[key];
                this.dataList.push(obj);
            }
        }
    }

    getCalcBasicMapFields() {
        this.isLoading = true;
        this._integrationService.getCalcBasicFields('hubspot', this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          this.calcAllFileds = [];
          this.insertFields =[];
          let calcfiedls = result;
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              for(let i = 0; i <calcfiedls[key].length; i++) {
                let obj = {};
                let insertObj = {};
                obj['calc_name'] = calcfiedls[key][i].name;
                obj['crm_key'] = calcfiedls[key][i].api_key;
                obj['calc_value'] = calcfiedls[key][i].value;

                insertObj['calc'] = calcfiedls[key][i].name;
                insertObj['crm'] = calcfiedls[key][i].api_key;
                this.calcAllFileds.push(obj);
                this.insertFields.push(insertObj);
              }
            }
          }
        },(error) => {
            //console.log(error,'erro');
        });
    }

}




