import { RequestOptions } from '@angular/http';
import { CurrentPlan } from './../../../../../../shared/models/currentPlan';
import { Datatable } from './../../../../../../shared/interfaces/datatable.interface';
import { Component, Input, ViewEncapsulation, AfterViewInit, OnDestroy, OnInit, Optional } from '@angular/core';
import { JSONBuilder } from '../../../../services/JSONBuilder.service';
import { FormulaService } from '../../../../services/formula.service';
import { ConditionalResultService } from "../../../../services/conditionalResult.service";

declare var jQuery: any;
declare var bootbox: any;
declare var ga: any;
declare var _kmq: any;
@Component({
  selector: 'conditional-result-editor',
  templateUrl: './html/conditional_result_editor.component.html',
  styleUrls: ['./css/ion.rangeSlider.css', './css/ion.rangeSlider.skinHTML5.css',],
  encapsulation: ViewEncapsulation.None
})

export class ConditionalResultComponent implements AfterViewInit, OnDestroy, OnInit {
  @Input() control: any;
  @Input() optionIndex: any;
  @Input() controlIndex:number;
  slider: any;
  option: any;
  sliderJson: any;

  constructor(private jsonBuilderHelper: JSONBuilder, private formulaService: FormulaService, private conditionalResultService: ConditionalResultService) { }

  ngOnInit(): void {
    this.option = this.control.options[this.optionIndex];
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.initSlider();
      this.initWyswyg();
      this.initCondDefaultWysiwig();
    }, 10);
    /*For tabbing*/
    jQuery('.type-details-head').on('click', function () {
      jQuery(this).parent().toggleClass('tab-collapse');
    });
    /* For tabbing end */
  }

  onNavigateURLChange($event: any) {
    let url = $event.target.value;
    if (!/^(f|ht)tps?:\/\//i.test(url)) {
      url = "http://" + url;
    }
    this.option.description= url;
  }

  ngOnDestroy() {
    if (this.control) {
      jQuery('.wysiwyg' + this.control._id + this.optionIndex).froalaEditor('destroy');
      jQuery('.wysiwyg-sub' + this.control._id + this.optionIndex).froalaEditor('destroy');
      jQuery('.default-cond-desc' + this.control._id).froalaEditor('destroy');
      jQuery('.default-cond-title' + this.control._id).froalaEditor('destroy');
    }
  }

  initCondDefaultWysiwig() {
    let self = this;

    var options: any = {};
    for (var variable in this.formulaService.allValidVariables())
      options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

    jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
    jQuery.FroalaEditor.RegisterCommand('questions', {
      title: 'Questions list',
      type: 'dropdown',
      focus: true,
      undo: true,
      icon: 'questions',
      refreshAfterCallback: true,
      options: options,
      callback: function (cmd: any, val: any) {
        this.html.insert(val);
      }
    });


    jQuery('.default-cond-desc' + this.jsonBuilderHelper.getSelectedControl()._id).froalaEditor({
      heightMax: 250, toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'insertLink', 'questions'],
    });

    jQuery('.default-cond-title' + this.jsonBuilderHelper.getSelectedControl()._id).froalaEditor({
      heightMax: 250, toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'insertLink', 'questions'],
    });

    jQuery('.default-cond-desc' + this.jsonBuilderHelper.getSelectedControl()._id).on('froalaEditor.contentChanged', function (e: any, editor: any) {
      self.jsonBuilderHelper.getSelectedControl().config.attr.style = e.currentTarget.value;
    });

    jQuery('.default-cond-title' + this.jsonBuilderHelper.getSelectedControl()._id).on('froalaEditor.contentChanged', function (e: any, editor: any) {
      self.jsonBuilderHelper.getSelectedControl().config.attr.class = e.currentTarget.value;
    });
  }

  initSlider() {
    var that = this;
    if ((parseFloat(this.control.props.maxVal) - parseFloat(this.control.props.minVal)) <= 5) {
      var range = Number(parseFloat(((parseFloat(this.control.props.maxVal) - parseFloat(this.control.props.minVal)) / 5).toFixed(2)));
    } else {
      var range = Number(Math.round(parseFloat(((parseFloat(this.control.props.maxVal) - parseFloat(this.control.props.minVal)) / 5).toFixed(2))));
    }

    this.sliderJson = {
      min: Number(this.control.props.minVal).toFixed(2),
      max: Number(this.control.props.maxVal).toFixed(2),
      step: range,
      grid: true,
      from: Number(this.option.attr.class).toFixed(2),
      to: Number(this.option.attr.style).toFixed(2),
      prettify_enabled: true,
      type: 'double',
      //from_fixed: true,
      prettify_separator: ',',
      onFinish: function (data: any) {
        that.option.attr.class = data.from;
        that.option.attr.style = data.to;
        that.sliderCorrections();
      },
      onUpdate: function (data: any) { }
    };


    jQuery('#' + 'slider' + this.control._id + this.optionIndex).ionRangeSlider(this.sliderJson);
    this.conditionalResultService.updateToFixed(this.control);
  }

  sliderCorrections() {
    let that = this;
    if (that.optionIndex == 0) {
      if (that.option.attr.style > that.control.options[that.optionIndex + 1].attr.style)
        that.option.attr.style = that.control.options[that.optionIndex + 1].attr.style;

      that.control.options[that.optionIndex + 1].attr.class = that.option.attr.style;
      that.sliderUpdater(Number(that.optionIndex + 1));
    } else if (that.optionIndex == that.control.options.length - 1) {
      if (that.option.attr.class < that.control.options[that.optionIndex - 1].attr.class)
        that.option.attr.class = that.control.options[that.optionIndex - 1].attr.class;

      that.control.options[that.optionIndex - 1].attr.style = that.option.attr.class;
      that.sliderUpdater(Number(that.optionIndex - 1));
    } else {
      if (that.option.attr.style > that.control.options[that.optionIndex + 1].attr.style)
        that.option.attr.style = that.control.options[that.optionIndex + 1].attr.style;
      if (that.option.attr.class < that.control.options[that.optionIndex - 1].attr.class)
        that.option.attr.class = that.control.options[that.optionIndex - 1].attr.class;

      that.control.options[that.optionIndex - 1].attr.style = that.option.attr.class;
      that.control.options[that.optionIndex + 1].attr.class = that.option.attr.style;
      that.sliderUpdater(Number(that.optionIndex + 1));
      that.sliderUpdater(Number(that.optionIndex - 1));
    }
    that.sliderUpdater(Number(that.optionIndex));
  }

  sliderUpdater(option: number) {
    let slider = jQuery('#' + 'slider' + this.control._id + option).data("ionRangeSlider");
    if (slider) {
      slider.update({
        from: Number(this.control.options[option].attr.class).toFixed(2),
        to: Number(this.control.options[option].attr.style).toFixed(2)
      });
    }
  }

  confirmValues(fromVal: number, toVal: number) {
    let self: any = this;
    bootbox.confirm({
      size: 'small',
      message: `
                    <div class="bootbox-body-left">
                        <div class="mat-icon">
                           <i class="material-icons">error</i>
                        </div>
                    </div>
                    <div class="bootbox-body-right">
                      <p>The conditions are overlapping.</p> <p> Are you ok with this?</p>
                    </div>
                `,
      // There are mistakes in ' + errorResults + '
      buttons: {
        'cancel': {
          label: 'Cancel', className: 'btn btn-cancel btn-cancel-hover'
        }, 'confirm': {
          label: 'Continue', className: 'btn btn-ok btn-hover'
        }
      },
      callback: function (result: any) {
        if (result === true) {
          self.option.attr.class = fromVal;
          self.option.attr.style = toVal;
        } else {
          let slider = jQuery('#' + 'slider' + self.control._id + self.optionIndex).data("ionRangeSlider");
          if (slider) {
            slider.update({
              to: self.option.attr.style,
              from: self.option.attr.class,
            });
          }
        }
      }
    });
  }

  updateSlider() {
    // Checks for lower and upper vals
    if (this.option.attr.class <= this.control.props.minVal) this.option.attr.class = this.control.props.minVal;
    if (this.option.attr.style >= this.control.props.maxVal) this.option.attr.style = this.control.props.maxVal;
    if (this.option.attr.class > this.option.attr.style) this.option.attr.class = this.option.attr.style;

    this.sliderCorrections();
  }
  initWyswyg() {
    let self = this;

    var options: any = {};
    for (var variable in this.formulaService.allValidVariables())
      options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

    jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
    jQuery.FroalaEditor.RegisterCommand('questions', {
      title: 'Questions list',
      type: 'dropdown',
      focus: true,
      undo: true,
      icon: 'questions',
      refreshAfterCallback: true,
      options: options,
      callback: function (cmd: any, val: any) {
        this.html.insert(val);
      }
    });
    jQuery('.wysiwyg' + this.control._id + this.optionIndex + ',.wysiwyg-sub' + this.control._id + this.optionIndex).froalaEditor({
      heightMax: 250, toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'insertLink', 'questions'],
    });

    jQuery('.wysiwyg' + this.control._id + this.optionIndex).on('froalaEditor.contentChanged', (e: any, editor: any) => {
      self.option.label = e.currentTarget.value;
    });
    jQuery('.wysiwyg-sub' + this.control._id + this.optionIndex).on('froalaEditor.contentChanged', (e: any, editor: any) => {
      self.option.icon = e.currentTarget.value;
    });

    jQuery('.wysiwyg' + this.control._id + this.optionIndex).on('froalaEditor.focus', function (e: any, editor: any) {
      self.option.defualtselected = !self.option.defualtselected;
    });

     jQuery('.wysiwyg-sub' + this.control._id + this.optionIndex).on('froalaEditor.focus', function (e: any, editor: any) {
      self.option.defualtselected = !self.option.defualtselected;
    });

  }
}



