    ConfigIntegrationsComponent.prototype.getCalcMapFields = function (type) {
        var _this = this;
        var calc_type = this.jsonBuilderHelper.getJSONBuilt().templateType;
        if (calc_type === 'Numerical') {
            this.calcType = 'Calculator';
        }
        else {
            this.calcType = 'Quiz';
        }
        this.isLoading = true;
        this.maperror = false;
        this.integrationType = type;
        this._integrationService.getCalcMapFields(type, this.jsonBuilderHelper.getJSONBuilt()._id)
            .subscribe(function (result) {
            _this.isLoading = false;
            var calcfiedls = result.calc;
            var crmfields = result.crm;
            _this.calcAllFileds = [];
            _this.crmAllFileds = [];
            for (var key in calcfiedls) {
                if (calcfiedls.hasOwnProperty(key)) {
                    var obj = {};
                    obj['calc_name'] = calcfiedls[key].name;
                    obj['calc_key'] = calcfiedls[key].api_key;
                    obj['calc_value'] = 'Sample : ' + calcfiedls[key].value;
                    _this.calcAllFileds.push(obj);
                }
            }
            for (var key in crmfields) {
                if (crmfields.hasOwnProperty(key) && crmfields[key].api_key !== 'Name') {
                    var obj = {};
                    obj['crm_name'] = crmfields[key].name;
                    obj['crm_key'] = crmfields[key].api_key;
                    var selectedMapFields = _this.mapFields[type];
                    var mpFiledLength = selectedMapFields.length;
                    for (var i = 0; i < mpFiledLength; i++) {
                        if (crmfields[key].api_key === selectedMapFields[i].crm) {
                            //console.log(crmfields[key].api_key,'mapeedededsjdhgsjbhfsjdgfjsdgfs',selectedMapFields[i].calc);
                            obj['calc_key'] = selectedMapFields[i].calc;
                            var insertObj = {};
                            insertObj['calc'] = selectedMapFields[i].calc;
                            insertObj['crm'] = crmfields[key].api_key;
                            _this.insertFields.push(insertObj);
                            break;
                        }
                        else {
                            obj['calc_key'] = '';
                        }
                    }
                    _this.crmAllFileds.push(obj);
                }
            }
        }, function (error) {
            _this.maperror = true;
            _this.isTokenExpired = true;
            _this.isLoading = false;
            if (error.error.err_message === 'invalid_grant: expired access/refresh token') {
                _this.maperrorMessage = 'Configuration error. Seems like your access token has expired. Please';
            }
            else if (error.error.err_message === 'The REST API is not enabled for this Organization.') {
                _this.maperrorMessage = 'REST API not enabled for your Salesforce account. You require Enterprise edition of Salesforce for Integration. Please';
            }
            else {
                _this.maperrorMessage = error.error.err_message;
            }
            //this.maperrorMessage = error.error.err_message + '. Please ';
        });
    };
    ConfigIntegrationsComponent.prototype.insertMapData = function (calcKey, event) {
        var obj = {};
        obj['calc'] = calcKey;
        obj['crm'] = event.target.value;
        var fieldslength = this.insertFields.length;
        if (fieldslength > 0) {
            var flagPush = true;
            for (var i = 0; i < fieldslength; i++) {
                if (calcKey === this.insertFields[i].calc) {
                    this.insertFields[i]['crm'] = event.target.value;
                    flagPush = false;
                    //console.log('sdfsdfsfsfd',this.insertFields[i]);
                    if (this.insertFields[i]['crm'] == '0') {
                        //console.log('hello');
                        this.insertFields.splice(i, 1);
                        return;
                    }
                    break;
                }
            }
            if (flagPush) {
                this.insertFields.push(obj);
            }
        }
        else {
            this.insertFields.push(obj);
        }
    };
