module.exports = "<div id=\"get_response\" class=\"configure-modal modal fade\" tabindex=\"-1\" role=\"dialog\">\r\n\t<div class=\"modal-dialog modal-lg\">\r\n\t\t<!-- Modal content-->\r\n\t\t<div class=\"modal-content modal-custom\">\r\n            <div class=\"col-md-12 col-xs-12 col-sm-12 np\">\r\n                <div class=\"col-md-5 np img-style\">\r\n                    <img class=\"\" src=\"assets/images/marketo-bg-full.png\">\r\n                    <img class=\"int-img\" src=\"assets/images/int-getResponse.png\">\r\n                </div>\r\n                <div class=\"col-md-7 np modal-background\">\r\n                    <div id=\"getResponse-firtStage\" *ngIf =\"!isGetresponseConnected\">\r\n                        <div class=\"modal-header\">\r\n                            <button type=\"button\" class=\"close btn-close\" data-dismiss=\"modal\" aria-label=\"Close\">\r\n                                <i class=\"material-icons modal-close\">close</i>\r\n                            </button>\r\n                            <h5 class=\"modal-title\">Send leads from Outgrow to Getresponse</h5>\r\n                            <p class=\"subheading\">\r\n                                Automate your Email Marketing with Outgrow Getresponse Integration\r\n                            </p>\r\n                            <span class=\"sec-subheading\">GetResponse API Key\r\n                                <small class=\"text-red\">(required)</small>\r\n                                <p class=\"subheading\">\r\n                                    Once logged in to GetResponse, click on the Account Name at the top of the left hand side bar and select Integrations. Click on API & OAuth in right sidebar. From here, select and copy the API Key you want to use.\r\n                                </p>\r\n                            </span>\r\n                        </div>\r\n                        <form class=\"form-wrapper\" id=\"add-getresponseKey\" [formGroup]=\"getResponseForm\">\r\n                            <div class=\"modal-body\">\r\n                                <div class=\"sahil-material\">\r\n                                    <div *ngIf=\"isGetResponseError\" class=\"alert alert-danger custom-danger\">\r\n                                        <p>\r\n                                            <span class=\"mat-icon\">\r\n                                                <i class=\"material-icons\">report_problem</i>\r\n                                            </span>\r\n                                        {{getResponseErrorMsg}}\r\n                                        </p>\r\n                                    </div>\r\n\r\n                                    <div class=\"form-group label-floating\" id=\"getResponse_apiKey\" [class.is-empty] = \"integrations.api_key=='' || integrations.api_key==null \">\r\n                                        <label class=\"control-label\" for=\"getResponse_apiKey\">Enter your Getresponse API Key</label>\r\n                                        <input class=\"form-control\"  type=\"text\" name=\"getResponseKey\" formControlName=\"apiKey\" [(ngModel)]=\"integrations.api_key\" (focus)=\"addIsfocusedKey()\" (blur)=\"removeIsfocusedKey()\">\r\n                                    </div>\r\n                                    <div *ngIf=\"getResponseForm.controls.apiKey.touched && !getResponseForm.controls.apiKey.valid\" class=\"alert alert-danger\">\r\n                                        <p *ngIf=\"getResponseForm.controls.apiKey.errors.required\">\r\n                                            <span class=\"mat-icon\">\r\n                                                <i class=\"material-icons\">report_problem</i>\r\n                                            </span>\r\n                                            Api key is required.\r\n                                        </p>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                            <div class=\"col-md-12 col-sm-12 col-xs-12 np text-center\">\r\n                                <button type=\"submit\" id=\"btnGetResponse\" [disabled]=\"!getResponseForm.valid\"  class=\"btn btn-red-outline btn-add-user btn-hover\" (click)=\"connectGetResponse()\" >Connect</button>\r\n                            </div>\r\n                        </form>\r\n                    </div>\r\n                    <div id=\"getResponse-secondStage\" *ngIf=\"isGetresponseConnected && !isListSelected\">\r\n                        <div class=\"modal-header\">\r\n                            <button type=\"button\" class=\"close btn-close\" data-dismiss=\"modal\" aria-label=\"Close\">\r\n                                <i class=\"material-icons modal-close\">close</i>\r\n                            </button>\r\n                            <h5 class=\"modal-title\">Send leads from Outgrow to Getresponse</h5>\r\n                            <p class=\"subheading\">\r\n                                Automate your Email Marketing with Outgrow Getresponse Integration\r\n                            </p>\r\n                            <span class=\"sec-subheading\">Getresponse API Key\r\n                                <small class=\"text-red\">(required)</small>\r\n                                <p class=\"subheading\">\r\n                                    Once logged in to GetResponse, click on the Account Name at the top of the left hand side bar and select Integrations. Click on API & OAuth in right sidebar. From here, select and copy the API Key you want to use.\r\n                                </p>\r\n                            </span>\r\n                        </div>\r\n                            <div class=\"modal-body\">\r\n                                <div class=\"sahil-material\">\r\n                                    <div *ngIf=\"isGetResponseError\" class=\"alert alert-danger custom-danger\">\r\n                                        <p>\r\n                                            <span class=\"mat-icon\">\r\n                                                <i class=\"material-icons\">report_problem</i>\r\n                                            </span>\r\n                                        {{getResponseErrorMsg}}\r\n                                        </p>\r\n                                    </div>\r\n                                    <div class=\"col-md-12 col-sm-12 col-xs-12 text-center\" *ngIf=\"isLoading\">\r\n\t\t\t\t\t\t\t\t\t    <i class=\"material-icons loader-plan\">autorenew</i>\r\n\t\t\t\t\t\t\t        </div>\r\n                                    <div class=\"form-group label-floating getResponseKey\" *ngIf=\"!isLoading\">\r\n                                        <label class=\"label-text\">Your Getresponse API Key</label>\r\n                                        <input class=\"form-control\"  type=\"text\" name=\"apiKey\" [value]=\"getresponseKey\" disabled>\r\n                                    </div>\r\n                                    <div *ngIf=\"getResponseForm.controls.apiKey.touched && !getResponseForm.controls.apiKey.valid\" class=\"alert alert-danger\">\r\n                                        <p *ngIf=\"getResponseForm.controls.apiKey.errors.required\">\r\n                                            <span class=\"mat-icon\">\r\n                                                <i class=\"material-icons\">report_problem</i>\r\n                                            </span>\r\n                                        Api key is required.\r\n                                        </p>\r\n                                    </div>\r\n                                    <div class=\"select-form-group\" *ngIf=\"isGetResponseListExist\">\r\n                                        <label class=\"label-text\">Select Get Response Campaign</label>\r\n                                        <select class=\"form-control\" (change)=\"getListID($event.target.value)\">\r\n                                            <option value=\"0\"> Select Campaign </option>\r\n                                            <option *ngFor=\"let lists of getResponseallList\"  [selected] = \"lists.list_id == selectedList\"  [value]=\"lists.list_id\">{{lists.list_name}}</option>\r\n                                        </select>\r\n                                        <span class=\"select-caret\"></span>\r\n                                    </div>\r\n                                    <div class=\"row-data\" *ngIf=\"!isGetResponseListExist && !isLoading\">\r\n\t\t\t\t\t\t\t\t        <label class=\"control-label label-head heading\">No GetResponse List found</label>\r\n\t\t\t\t\t\t\t        </div>\r\n                                </div>\r\n                            </div>\r\n                            <div class=\"col-md-12 col-sm-12 col-xs-12 np text-center\">\r\n                                <button type=\"submit\" id=\"btnGetResponseList\"  class=\"btn btn-red-outline btn-add-user btn-hover\" (click)=\"viewGetResponse()\" >Configure</button>\r\n                            </div>\r\n    \t            </div>\r\n                    <div id=\"view-getResponse\" *ngIf=\"isListSelected\">\r\n                      <div class=\"modal-header\">\r\n                        <button type=\"button\" class=\"close btn-close\" data-dismiss=\"modal\" aria-label=\"Close\">\r\n                          <i class=\"material-icons modal-close\">close</i>\r\n                        </button>\r\n                        <h5 class=\"modal-title\">TEST CONNECTION</h5>\r\n                        <p class=\"subheading subtitle\">Selected <b> {{campaignName}} </b> campaign and following mapping will be used for this integration.</p>\r\n                      </div>\r\n                      <div class=\"modal-body\">\r\n                        <div *ngIf=\"error\" class=\"total-alert alert alert-danger custom-danger map-int-alert\">\r\n                            <p><span class=\"mat-icon\"><i class=\"material-icons\">report_problem</i></span>\r\n                              {{maperrorMessage}}\r\n                            </p>\r\n                        </div>\r\n                                      <div class=\"col-md-12 col-sm-12 col-xs-12 text-center\" *ngIf=\"isLoading\">\r\n                            <i class=\"material-icons loader-plan\">autorenew</i>\r\n                        </div>\r\n                        <div class=\"row-data\" *ngIf=\"!isLoading\">\r\n                                          <div class=\"col-md-12 col-xs-12 col-sm-12 np scrollbar\">\r\n                                              <div class=\"row-data\">\r\n                                                  <label class=\"control-label label-head heading\">Field Name</label>\r\n                                                  <label class=\"label-head heading\">Entry</label>\r\n                                              </div>\r\n                                              <div *ngFor=\"let calc of calcAllFields\" class=\"row-data\">\r\n                                                  <label class=\"control-label label-head\">{{calc.calc_name}}</label>\r\n                                                  <label class=\"\">{{calc.calc_value}} </label>\r\n                                              </div>\r\n                                          </div>\r\n                                          <div class=\"modal-footer col-md-12 text-center\">\r\n                                              <a id=\"btn-getresponse-test\" (click)=\"testSaveLead()\" class=\"btn btn-basic configure-btn\" href=\"javascript:void(0);\" type=\"button\">\r\n                                                  TEST CONNECTION\r\n                                              </a>\r\n                                              <span>A test record will be pushed to Getresponse to test connection with these mapping.</span>\r\n                              </div>\r\n                        </div>\r\n                        <div class=\"col-md-12 col-xs-12 col-sm-12 np scrollbar hide\">\r\n                          <div class=\"row-data\">\r\n                            <label class=\"control-label label-head heading\">Field Name</label>\r\n                            <label class=\"label-head heading\">Entry</label>\r\n                          </div>\r\n                          <div class=\"row-data\">\r\n                            <label class=\"control-label label-head\">Email Id</label>\r\n                            <label class=\"\">Email_Id </label>\r\n                          </div>\r\n                                          <div class=\"row-data\">\r\n                            <label class=\"control-label label-head\">First Name</label>\r\n                            <label class=\"\">FirstName </label>\r\n                          </div>\r\n                                          <div class=\"row-data\">\r\n                            <label class=\"control-label label-head\">Last Name</label>\r\n                            <label class=\"\">LastName </label>\r\n                          </div>\r\n                        </div>\r\n                      </div>\r\n                      <div class=\"modal-footer col-md-12 col-sm-12 col-xs-12 text-center hide\">\r\n                        <a id=\"btn-marketo-test\" (click)=\"close()\" class=\"btn btn-basic configure-btn\" href=\"javascript:void(0);\" type=\"button\">\r\n                          TEST CONNECTION\r\n                        </a>\r\n                                      <span>A test record will be pushed to Marketo to test connection with these mapping.</span>\r\n                      </div>\r\n\t\t\t\t\t          </div>\r\n                </div>\r\n            </div>\r\n\t\t</div>\r\n\t</div>\r\n</div>\r\n"

