module.exports = "<!--<div class=\"type-details hide\">\r\n    <span class=\"form-label no-margin\" *ngIf=\"jsonBuilderHelper.getSelectedControl().type ==='radio_button'\">Allow User to Multi Select</span>\r\n    <span class=\"form-label no-margin\" *ngIf=\"jsonBuilderHelper.getSelectedControl().type ==='checkbox'\">Allow User to Single Select</span>\r\n    <div class=\"switch\">\r\n        <label>\r\n            <input type=\"checkbox\" name=\"multi-checkbox\" class=\"show-check\"\r\n                [checked]=\"jsonBuilderHelper.getSelectedControl().type ==='radio_button'\"\r\n                (change)=\"onChangeControl()\" data-size=\"mini\" data-on-color=\"blue\">\r\n            <span class=\"lever\"></span>\r\n        </label>\r\n    </div>\r\n</div>-->\r\n<!-- Common Component -->\r\n<div class=\"type-details\">\r\n    <common-editor [control]=\"control\"></common-editor>\r\n</div>\r\n<div class=\"type-details\">\r\n    <span class=\"form-label\" [class.no-margin]=\"control.config.showHelp === false\">Help Text:</span>\r\n    <div class=\"switch\">\r\n        <label>\r\n            <input type=\"checkbox\" name=\"properties-checkbox\" class=\"show-check\" [checked]=\"control.config.showHelp\" (change)=\"onChangeDescription(control);callGA('HELPTEXT',control)\" data-size=\"mini\" data-on-color=\"blue\">\r\n            <span class=\"lever\"></span>\r\n        </label>\r\n    </div>\r\n    <div class=\"col-xs-12 no-padding text-group div-check\" *ngIf=\"control.config.showHelp\">\r\n        <textarea class=\"form-text question-textarea\" [(ngModel)]=\"control.props.helpText\"></textarea>\r\n    </div>\r\n</div>\r\n<!-- <div class=\"type-details horz-group\">\r\n    <span class=\"form-label\">Icons:</span>\r\n    \r\n</div>\r\n -->\r\n<div class=\"type-details\">\r\n    <span class=\"form-label\">Options:</span>\r\n    <div class=\"help-tip editor-helptip\" *ngIf=\"control.optionImageVisible && jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'\">\r\n        <i class=\"material-icons\">info_outline</i>\r\n        <div class=\"help-checktip\">Recommended Dimensions are 240px by 240px.</div>\r\n    </div>\r\n    <label class=\"rd-icon ic rd-option\"  *ngIf=\"jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'\">\r\n        <div class=\"help-text\">Image</div>\r\n        <input type=\"radio\" name=\"bh\" id=\"icon6\" value=\"\" (click)=\"optionImageToggle()\" [checked]=\"control.optionImageVisible\">\r\n        <label for=\"icon6\"><i class=\"material-icons\">photo_camera</i> </label>\r\n    </label>\r\n    <label class=\"rd-icon ic rd-option\">\r\n        <div class=\"help-text\">Icon</div>\r\n        <input type=\"radio\" name=\"bh\" id=\"icon4\" value=\"\" (click)=\"ShowIcon();callGA('TOGGLEICONON')\" [checked] = \"control.isIconPresent\">\r\n        <label for=\"icon4\"><i class=\"material-icons\">photo_filter</i> </label>\r\n    </label>\r\n    <label class=\"rd-icon ic rd-option\">\r\n\r\n        <div class=\"help-text\">Text</div>\r\n        <input type=\"radio\" name=\"bh\" id=\"icon5\" value=\"\" (click)=\"None();callGA('TOGGLEICONOFF')\" [checked] = \"(control.isIconPresent || control.optionImageVisible)?false:true\">\r\n\r\n        <label for=\"icon5\"><i class=\"material-icons\">menu</i> </label>\r\n    </label>\r\n        \r\n    <div class=\"switch hide\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'\">\r\n        <label>\r\n            <input type=\"checkbox\" name=\"properties-checkbox\" class=\"show-check\" [checked]=\"control.optionImageVisible\" (change)=\"optionImageToggle()\" data-size=\"mini\" data-on-color=\"blue\">\r\n            <span class=\"lever-image plural\"><i class=\"material-icons\">photo_camera</i></span>\r\n        </label>\r\n    </div>\r\n    <div class=\"col-xs-12 no-padding text-group\" *ngFor=\"let option of control.options ; let i = index\">\r\n        <span class=\"no-padding option-label\">\r\n            <div class=\"help-tip\">\r\n                <span class=\"default-label\" *ngIf=\"option.defualtselected\">D</span>\r\n                <div class=\"help-checktip\">Default Option</div>\r\n            </div>\r\n        <span class=\"\" *ngIf=\"!option.defualtselected\">{{i+1}}</span>\r\n        </span>\r\n        <!--icons-->\r\n        <div class=\"option-textbox\">\r\n            <div class=\"btn-group option-icons optionicon{{i}}\" *ngIf=\"showIcon\">\r\n                <button type=\"button\" class=\"btn btn-default dropdown-toggle\"\r\n                        (click) = \"OpenPreviousIcon(i)\"\r\n                         aria-haspopup=\"true\" aria-expanded=\"false\">\r\n                    <span class=\"selected-icon\">\r\n                        <i class=\"material-icons\" *ngIf=\"option.icon\">{{option.icon}}</i>\r\n                        <i class=\"material-icons\" *ngIf=\"!option.icon\">landscape</i>\r\n                    </span>\r\n                    <div class=\"choose iconopen{{i}}\">\r\n                        <a href=\"javascript:void(0);\" class=\"default choose-icon\" (click) = \"OpenChangeIcon(i)\">\r\n                            <span class=\"arrow-down\">\r\n                                <i class=\"material-icons\">keyboard_arrow_down</i>\r\n                            </span>\r\n                        </a>\r\n                    </div>\r\n                </button>\r\n                <div class=\"material-icon-dropdown\">\r\n                    <a href=\"javascript:void(0);\" class=\"close\" (click) = \"CloseChangeIcon(i)\"><i class=\"material-icons\" >close</i></a>\r\n                    <div class=\"selected-icon\">\r\n                        <span *ngIf=\"!option.previousIcons.length\" class=\"no-icon\">No Previous Icon</span>\r\n                        <label class=\"rd-icon ic\" *ngFor=\"let icon of option.previousIcons; let s of index\">\r\n                            <input type=\"radio\" name=\"p{{i+1}}\" id=\"iconp{{s}}+{{i+1}}\" value=\"{{icon}}\" (click)=\"changeIcon(option,$event)\">\r\n                            <label attr.for=\"iconp{{s}}+{{i+1}}\"><i class=\"material-icons\">{{icon}}</i> </label>\r\n                        </label>\r\n                    </div>\r\n                    <div class=\"icon-op\">\r\n                    <label class=\"rd-icon ic\" *ngFor=\"let icon of iconArray; let s of index\">\r\n                        <input type=\"radio\" name=\"{{i+1}}\" id=\"icon{{s}}+{{i+1}}\" value=\"{{icon}}\" (click)=\"changeIcon(option,$event)\">\r\n                        <label attr.for=\"icon{{s}}+{{i+1}}\"><i class=\"material-icons\">{{icon}}</i> </label>\r\n                    </label></div>\r\n                </div>\r\n                <!-- <ul class=\"dropdown-menu\" id=\"icon-list{{i}}\">\r\n                    <li>\r\n                        <label class=\"rd-icon ic\" *ngFor=\"let icon of option.previousIcons; let s of index\">\r\n                            <input type=\"radio\" name=\"p{{i+1}}\" id=\"iconp{{s}}+{{i+1}}\" value=\"{{icon}}\" (click)=\"changeIcon(option,$event)\">\r\n                            <label attr.for=\"iconp{{s}}+{{i+1}}\"><i class=\"material-icons\">{{icon}}</i> </label>\r\n                        </label>\r\n                        <span *ngIf=\"!option.previousIcons.length\">No Previous Icon</span>\r\n                    </li>\r\n                </ul> -->\r\n            </div>\r\n            <div class=\"option-textbox-sub\" [class.paddr0]=\"showIcon && !control.optionImageVisible\">\r\n                <input type=\"text\" class=\"form-text\" placeholder=\"Label\" [(ngModel)]=\"option.label\">\r\n            </div>            \r\n        </div>     \r\n        <div class=\"input-group img-option\" *ngIf=\"control.optionImageVisible && jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'\">\r\n            <input type=\"text\" class=\"file-input\" value=\"{{(option.imageName != '')?option.imageName:option.imageURL}}\" placeholder=\"No file\" readonly>\r\n            <span class=\"btn btn-basic2\" data-fp-apikey=\"ANQWcFDQRUiGfBqjfgINQz\" (click)=\"uploadImage(option)\">\r\n            UPLOAD <input type=\"\" style=\"display: none;\">\r\n            </span>\r\n        </div>       \r\n        <div class=\"col-md-11 col-xs-11 no-padding\">\r\n            <label class=\"check-value\" [class.help-tip-absent]=\"i!=0\">VALUE\r\n                <div class=\"help-tip\" *ngIf=\"i==0 && jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'\">\r\n                    <i class=\"material-icons\">info_outline</i>\r\n                    <div class=\"help-checktip\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'\">\r\n                        Each option has a numeric 'value' that describes its impact to the end result. In a calculator calculating the total cost of a fruit basket, the 'value' will be the cost of a fruit item.\r\n                    </div>\r\n                </div>\r\n            </label>\r\n            <input type=\"inputType\" class=\"form-text value-text\" placeholder=\"Value\" [(ngModel)]=\"option.value\" (keydown)=\"onKeyDown(i, $event)\">\r\n        </div>\r\n        <!-- More button -->\r\n        <div class=\"delete-option\" style=\"margin-top: 10px;\">\r\n            <a href=\"javascript:void();\" (click)=\"delete_Option_From_Items(control.options,i);callGA('DELETEOPTION')\">\r\n                <i class=\"material-icons\">clear</i>\r\n            </a>\r\n            <div class=\"help-text\">Delete</div>\r\n        </div>\r\n    </div>\r\n    <div class=\"col-xs-12 no-padding text-group\">\r\n        <span class=\"no-padding form-label\"></span>\r\n        <div class=\"col-md-10 col-xs-10 no-padding\">\r\n            <a href=\"javascript:void(0);\" (click)=\"add_Option_In_Dropdown();callGA('ADDOPTION')\" class=\"default\">Add Option</a>\r\n        </div>\r\n    </div>\r\n</div>\r\n<div class=\"type-details\">\r\n    <label class=\"check\">\r\n        <input type=\"checkbox\" value=\"Value1\" [disabled]=\"isQuestionInResults\" name=\"checkbox1\" id=\"check1\" [checked]=\"control.config.validations.required.status\" (change)=\"onChange(control);callGA('MANDATE',control)\">\r\n        <label for=\"check1\"> </label>\r\n        Mark as Mandatory\r\n    </label>\r\n</div>\r\n"

