" name=\"checkbox1\" id=\"check1\" [checked]=\"control.config.validations.required.status\" (change)=\"onChange(control);callGA('MANDATE',control)\">\r\n    <label for=\"check1\"> </label>\r\n    Mark as Mandatory\r\n  </label>\r\n</div>\r\n"

