module.exports = "<div class=\"type-details\">\r\n    <div><span class=\"form-label lead-label no-margin\">Lead Generation: </span>\r\n        <div class=\"switch\" [class.alwaysOff]=\"!isLeadGenAvailable\">\r\n            <label>\r\n            <input type=\"checkbox\" name=\"multi-checkbox\"\r\n            class=\"show-check\"\r\n            [checked] = \"leadSection?.visible\"\r\n            (change) = \"changeVisibility();callGA('SHOWLEADFORMTOGGLE')\"\r\n            data-size=\"mini\" data-on-color=\"blue\">\r\n            <span class=\"lever\"></span>\r\n            </label> \r\n        </div>\r\n        <div class=\"help-tip editor-helptip\">\r\n            <i class=\"material-icons\">info_outline</i>\r\n            <div class=\"help-checktip\">Toggle here to collect user information (name, email etc) along with the {{jsonBuilderHandler.getSelectedPage().type }}.</div>\r\n        </div>\r\n    </div>\r\n    <!-- <span class=\"editor-helptext\">Toggle here to collect user information (name, email etc) on the <i> {{jsonBuilderHandler.getSelectedPage().type }} </i>.</span> -->\r\n    <span *ngIf=\"jsonBuilderHandler.getOtherVisibleLeadForm() &&\r\n     (!leadSection?.visible || jsonBuilderHandler.getOtherVisibleLeadForm() !== 'Questionnaire')\" style=\"width: 100%;float: left;margin-top: 10px;\" class=\"editor-helptext no-margin\"> \r\n     Please note that you are currently collecting leads on the <i> {{ jsonBuilderHandler.getOtherVisibleLeadForm() }} Page</i>. \r\n     If you wish to collect leads here instead, Toggle here to override.\r\n    </span>\r\n</div>\r\n <!--realtime result-->\r\n<!--<div class=\"type-details\" *ngIf=\"jsonBuilderHandler.getJSONBuilt().template == 'one-page-card'\">\r\n    <span class=\"form-label no-margin\">Show Real Time Results: </span>\r\n    <div class=\"help-tip editor-helptip\">\r\n            <i class=\"material-icons\">info_outline</i>\r\n            <div class=\"help-checktip\">Toggle here to enable your users to see the results in real time as they answer the questions.</div>\r\n        </div>\r\n    <div class=\"switch\" [class.alwaysOff]=\"!isRealTimeResult\">\r\n        <label>\r\n            <input type=\"checkbox\" (change)=\"onRealTimeChange();callGA('REALTIMECHANGE')\" name=\"multi-checkbox\"\r\n            class=\"show-check\"\r\n            [checked]=\"jsonBuilderHandler.getJSONBuilt().realTime\"\r\n            data-size=\"mini\" data-on-color=\"blue\">\r\n            <span class=\"lever\"></span>\r\n        </label> \r\n    </div>\r\n</div>-->\r\n<!--end real time result-->"

