module.exports = "<div class=\"col-xs-12 no-padding\">\r\n  <div class=\"type-details\">\r\n    <div *ngIf=\"editorControl.header.order\">\r\n      <span class=\"form-label\">Heading:</span>\r\n      <textarea class=\"wysiwyg\" [(ngModel)]=\"editorControl.header.props.title\" [value]=\"editorControl.header.props.title\" ></textarea>\r\n    </div>\r\n\r\n  <div class=\"type-details-sub\">\r\n    <div *ngIf=\"editorControl.sub_header.order\">\r\n      <span class=\"form-label\" [class.no-margin]=\"!editorControl.sub_header.visible\">Sub Heading:</span>\r\n      <div class=\"switch\">\r\n        <label>\r\n          <input type=\"checkbox\" class=\"\" (change)=\"toggleSubHeader();callGA('TOGGLESUB')\" [checked]=\"editorControl.sub_header.visible\">\r\n          <span class=\"lever\"></span>\r\n        </label>\r\n      </div>\r\n      <div [class.hide]=\"!editorControl.sub_header.visible\">\r\n        <textarea class=\"wysiwyg-sub\" [(ngModel)]=\"editorControl.sub_header.props.title\" [value]=\"editorControl.sub_header.props.title\" ></textarea>\r\n      </div>\r\n    </div>\r\n\r\n\r\n\r\n  <div class=\"type-details-sub\" >\r\n    <div>\r\n      <span class=\"form-label\">Button Text:</span>\r\n      <input\r\n        type=\"text\"\r\n        class=\"detail-text\"\r\n        *ngIf=\"editorControl.leadform.visible\"\r\n        [(ngModel)]=\"editorControl.leadform.props.title\"\r\n      />\r\n      <input\r\n        type=\"text\"\r\n        class=\"detail-text\"\r\n        *ngIf=\"editorControl.click_button.visible\"\r\n        [(ngModel)]=\"editorControl.click_button.props.title\"\r\n      />\r\n    </div>\r\n\r\n  </div>\r\n    <div class=\"type-details-sub\">\r\n    <div *ngIf=\"editorControl.logo.order\"  >\r\n      <span class=\"form-label\" [class.no-margin]=\"editorControl.logo.visible === false\">Logo:</span>\r\n      <div class=\"help-tip editor-helptip\">\r\n        <i class=\"material-icons\">info_outline</i>\r\n        <div class=\"help-checktip\">Recommended Dimensions are 180px by 50px.</div>\r\n      </div>\r\n      <div class=\"switch\">\r\n        <label>\r\n          <input type=\"checkbox\" class=\"show-check\" (change)=\"toggleLogo($event);callGA('TOGGLELOGO')\" [checked]=\"editorControl.logo.visible\">\r\n          <span class=\"lever\"></span>\r\n        </label>\r\n      </div>\r\n      <div class=\"div-check\" *ngIf=\"editorControl.logo.visible\">\r\n        <div class=\"col-xs-12 no-padding\">\r\n          <div class=\"input-group\">\r\n            <input\r\n              type=\"text\"\r\n              class=\"file-input\"\r\n              placeholder=\"No file\"\r\n              value=\"{{(editorControl.logo.props.unit != '')?editorControl.logo.props.unit:editorControl.logo.props.title}}\"\r\n              readonly\r\n            >\r\n            <label class=\"input-group-btn\">\r\n              <span class=\"btn btn-basic2\" data-fp-apikey=\"ANQWcFDQRUiGfBqjfgINQz\" (click)=\"upload('logo');callGA('UPLOADLOGO')\">\r\n                  UPLOAD<input type=\"\"  style=\"display: none;\">\r\n              </span>\r\n            </label>\r\n          </div>\r\n        </div>\r\n        <div class=\"col-md-12 col-xs-12 no-padding full-check-value top-space10\">\r\n          <label class=\"check-value\">REDIRECT URL</label>\r\n          <input\r\n          type=\"text\"\r\n          class=\"form-text value-text\"\r\n          placeholder=\"http://\"\r\n          (change)=\"onNavigateURLChange($event)\"\r\n          [(ngModel)]=\"editorControl.logo.config.direction\"\r\n          />\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n  </div>\r\n\r\n</div>\r\n<div class= \"type-details powered\">\r\n  <div class=\"col-xs-12 no-padding setting-detail-box powered\">\r\n          <span class=\"label-welcome\">Hide Welcome Screen</span>\r\n          <div class=\"help-tip editor-helptip\">\r\n            <i class=\"material-icons\">info_outline</i>\r\n            <div class=\"help-checktip\">You may hide the welcome screen in case you are using your own welcome screen or if you wish to take the user directly to question 1.</div>\r\n          </div>\r\n          <div class=\"switch\">\r\n            <label>\r\n              <input type=\"checkbox\" (change)=\"changeVisivility();callGA('HIDEWELCOMETOGGLE')\" [checked]=\"!page.visible\"/>\r\n              <span class=\"lever\"></span>\r\n            </label>\r\n          </div>\r\n    </div>\r\n</div>\r\n"

