module.exports = "<span class=\"section-help\">You can group your questions in sections to make it simple for the users to follow.</span>\r\n<div class=\"type-details\">\r\n  <div>\r\n    <span class=\"form-label\">Section Title:</span>\r\n    <textarea class=\"wysiwyg question-textarea\" [(ngModel)]=\"section.title\"></textarea>\r\n  </div>\r\n</div>\r\n<div class=\"type-details\">\r\n  <span class=\"form-label\" [class.no-margin]=\"section.showDesc === false\">Help Text:</span>\r\n  <div class=\"switch\">\r\n    <label>\r\n      <input type=\"checkbox\" name=\"properties-checkbox\"\r\n             class=\"show-check\"\r\n             [checked]=\"section.showDesc\"\r\n             (change)=\"onChangeDescription(section);callGA('HELPTEXT',section)\"\r\n      >\r\n      <span class=\"lever\"></span>\r\n    </label>\r\n  </div>\r\n  <div class=\"col-xs-12 no-padding text-group div-check\" *ngIf=\"section.showDesc\">\r\n    <textarea class=\"form-text question-textarea\" [(ngModel)]=\"section.description\"></textarea>\r\n  </div>\r\n</div>\r\n<div class=\"type-details\" *ngIf=\"_JSONBuilder.getJSONBuilt().template == 'one-page-slider' || _JSONBuilder.getJSONBuilt().template == 'inline-temp'\">\r\n  <div>\r\n    <span class=\"form-label\">Button Text:</span>\r\n    <div class=\"help-tip editor-helptip\">\r\n      <i class=\"material-icons\">info_outline</i>\r\n      <div class=\"help-checktip\">This will come after the last question in the section.</div>\r\n    </div>\r\n    <input type=\"text\" class=\"form-text\" [(ngModel)]=\"section.buttonTitle\" />\r\n  </div>\r\n</div>\r\n"

