module.exports = "<!-- Common Component -->\r\n<div class=\"type-details\">\r\n  <common-editor [control]=\"control\">\r\n  </common-editor>\r\n</div>\r\n<!-- Common Component-->\r\n<div class=\"type-details\">\r\n  <span class=\"form-label\" [class.no-margin]=\"control.config.showHelp === false\">Help Text:</span>\r\n  <div class=\"switch\">\r\n    <label>\r\n      <input type=\"checkbox\" name=\"properties-checkbox\"\r\n             class=\"show-check\"\r\n             [checked]=\"control.config.showHelp\"\r\n             (change)=\"onChangeDescription(control);callGA('HELPTEXT',control)\"\r\n             data-size=\"mini\" data-on-color=\"blue\">\r\n      <span class=\"lever\"></span>\r\n    </label>\r\n  </div>\r\n  <!--//on/off-->\r\n  <div class=\"col-xs-12 no-padding div-check\" *ngIf=\"control.config.showHelp\">\r\n    <textarea class=\"form-text question-textarea\" [(ngModel)]=\"control.props.helpText\"></textarea>\r\n  </div>\r\n\r\n</div>\r\n\r\n<div class=\"type-details options-form\">\r\n  <span class=\"form-label\">Options:</span>\r\n  <div class=\"col-xs-12 no-padding text-group\" *ngFor=\"let option of control.options ; let i = index\">\r\n\t\t<span class=\"no-padding option-label\">\r\n\t\t\t\t\t<div class=\"help-tip\">\r\n            <span class=\"default-label\" *ngIf=\"option.defualtselected\">D</span>\r\n            <div class=\"help-checktip\">Default Option</div>\r\n        </div>\r\n\t\t<span class=\"\" *ngIf=\"!option.defualtselected\">{{i+1}}</span>\r\n\t\t</span>\r\n    <div class=\"option-textbox\">\r\n      <div class=\"option-textbox-sub\">\r\n        <input type=\"text\" class=\"form-text\" placeholder=\"Label\" [(ngModel)]=\"option.label\" (keyup)=\"onOptionLabelChange(option)\">\r\n      </div>      \r\n    </div>\r\n    <div class=\"col-md-11 col-xs-11 no-padding\">\r\n      <label class=\"check-value\" [class.help-tip-absent]=\"i!=0\">VALUE\r\n        <div class=\"help-tip\" *ngIf=\"i==0 && jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'\">\r\n          <i class=\"material-icons\">info_outline</i>\r\n          <div class=\"help-checktip\">\r\n            Each option has a numeric 'value' that describes its impact to the end result. In a calculator calculating the total cost of a fruit basket, the 'value' will be the cost of a fruit item.\r\n          </div>\r\n        </div>\r\n      </label>\r\n      <input type=\"inputType\" class=\"form-text value-text\" placeholder=\"Value\" [value]=\"option.value\" (keydown)=\"onKeyDown(i, $event)\" (keyup)=\"onOptionValChange(option,$event)\">\r\n    </div>\r\n    <!-- More button -->\r\n    <div class=\"delete-option\" style=\"margin-top: 10px;\">\r\n        <a href=\"javascript:void();\" *ngIf=\"control.options.length !== 1\" (click)=\"delete_Option_From_Items(control.options,i);callGA('DELETE')\">\r\n            <i class=\"material-icons\">clear</i>\r\n        </a>\r\n        <div class=\"help-text\">Delete</div>\r\n    </div>\r\n  </div>\r\n  <div class=\"col-xs-12 no-padding text-group\">\r\n    <span class=\"no-padding form-label\"></span>\r\n    <div class=\"col-md-10 col-xs-10 no-padding\">\r\n      <a href=\"javascript:void(0);\" (click)=\"add_Option_In_Dropdown();callGA('ADDOPTION')\" class=\"default\">Add Option</a>\r\n    </div>\r\n  </div>\r\n</div>\r\n\r\n<div class=\"type-details hide\">\r\n  <label class=\"check\">\r\n    <input type=\"checkbox\" value=\"Value1 \" name=\"checkbox1\" id=\"check1\"\r\n           [disabled]=\"isQuestionInResults\"\r\n           [checked]=\"control.config.validations.required.status\"\r\n           (change)=\"onChange(control);callGA('MANDATE',control)\"\r\n    >\r\n    <label for=\"check1\"> </label> Mark as Mandatory\r\n  </label>\r\n</div>\r\n"

