module.exports = "<!-- Start: Modal Formula -->\r\n<div id=\"formula-modal-new\" class=\"modal fade\" tabindex=\"-1\" role=\"dialog\">\r\n  <div class=\"modal-dialog modal-xlg\">\r\n    <!-- Modal content-->\r\n    <div class=\"modal-content modal-bg\">\r\n      <div class=\"modal-body\">\r\n        <button type=\"button\" class=\"close btn-close\" data-dismiss=\"modal\" aria-label=\"Close\">\r\n          <i class=\"material-icons\">clear</i></button>\r\n        <div class=\"col-md-12 no-padding\" id=\"dragzone\">\r\n          <div class=\"col-xs-12 col-sm-3 formula-right\">\r\n            <div class=\"col-xs-12 no-padding\">\r\n              <h3 class=\"formula-right-heading\">Select Questions/Results to add them to your formula</h3>\r\n            </div>\r\n            <div class=\"col-xs-12 formula-question-outr\">\r\n              <div class=\"formula-questions scrollbar\" id=\"drop\">\r\n                <a  class=\"formula-item\"\r\n                    *ngFor=\"let control of jsonBuilderHelper.getTemplateQuestionareWithEmittedLeadFormQuestion(); let i=index;\"\r\n                    [class.hide]=\"control.type=='leadform_question'||control.type=='text-area'||\r\n                    (control.type=='textfield' && (control.config.type=='text' || control.config.type=='email'))\"\r\n                    (click) = \"addQuestion('Q'+(i+1))\"\r\n                    draggable=\"true\"\r\n                    id=\"{{'Q_'+(i+1)}}\"\r\n                >\r\n                  Q. {{i+1}}\r\n                   <span *ngIf = \"jsonBuilderHelper.getJSONBuilt().template === 'inline-temp'\">{{control.props.title+'___'+control.props.followUpText | removeTags}}</span>\r\n                  <span *ngIf = \"jsonBuilderHelper.getJSONBuilt().template !== 'inline-temp'\">{{ control.props.title | removeTags}}</span>\r\n                  <div class=\"asterik-popover\"  *ngIf=\"control.type=='slider' && control.props.unit=='%' && control.props.postfix\">\r\n                    <i class=\"material-icons\">info_outline</i>\r\n                    <div class=\"asterik-popover-inner\">It looks like the answer to this question is a percentage. Make sure to divide this variable by 100%, so as to correctly reflect the number as a percentage in the formula.</div>\r\n                  </div>\r\n                </a>\r\n                <a  class=\"formula-item\"\r\n                    *ngFor=\"let formula of jsonBuilderHelper.getJSONBuilt().formula; let i=index;\"\r\n                    [class.hide]=\"!formula.result || i == formulaIndex || replaceRs(formula.result).indexOf('R'+(formulaIndex+1))>=0\"\r\n                    (click) = \"addQuestion('R'+(i+1))\"\r\n                    draggable=\"true\"\r\n                    id=\"{{'R_'+(i+1)}}\"\r\n                >\r\n                  Result {{i+1}}\r\n                </a>\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <div class=\"col-xs-12 col-sm-6 formula-left\">\r\n            <div class=\"col-xs-12 no-padding text-center\">\r\n              <h3 class=\"formula-left-heading\">Formula Builder -</h3>\r\n              <label class=\"formula-left-subheading\">&nbsp;Result 1 </label>\r\n              <i class=\"material-icons formula-heading-editing hide\">create</i>\r\n            </div>\r\n            <div class=\"formula-output\" tabindex=\"1\" id=\"formula\" [ngStyle]=\"{'margin-top': '30px', borderColor:(error?'#fb545b':'#dae2e6')}\">\r\n              <textarea id=\"formula-area\"\r\n                        (keyup)=\"checkValidity()\"\r\n                        [ngStyle]=\"{'width': '100%', border: 'none', resize: 'none', letterSpacing:'2px'}\"\r\n                        rows=\"7\" >\r\n              </textarea>\r\n            </div>\r\n            <div class=\"formula-helptext-outr\" *ngIf=\"error && error!='You can only use basic operators.To use others upgrade your account.'\">\r\n              <i class=\"material-icons\">info_outline</i>\r\n              <label class=\"formula-helptext\" [ngStyle]=\"{'color': '#fb545b'}\">{{error}}</label>\r\n           </div>\r\n\r\n            <div  class=\"formula-helptext-outr\" [class.bounceIn-error]=\"flashError\"\r\n                  *ngIf=\"featureAuthService.features.formula_operators.simple_operators\" >\r\n              <i class=\"material-icons lock-icon\">lock_outline</i>\r\n              <label  class=\"formula-helptext\" [ngStyle]=\"{'color': '#fb545b'}\">You can use +,-,x and / operators. Please upgrade to unlock others.</label>\r\n              <span class=\"btn btn-basic\" *ngIf=\"featureAuthService.features.formula_operators.simple_operators\" (click)=\"showPremiumFormulaPopup()\">upgrade</span><br *ngIf=\"error\"/>\r\n            </div>\r\n\r\n            <label class=\"formula-helptext\">You can use \"+\", \"-\", \"*\", and \"/\" operators by typing them on your keyboard. You can also use over 100 other mathematical functions like log, if-then etc.\r\n              <label>\r\n                <a href=\"http://support.outgrow.co/v1.0/docs/formula\" target=\"_blank\" [ngStyle]=\"{color:'#fb545b'}\">Click here</a> to learn more.\r\n              </label>\r\n            </label>\r\n            <br/>\r\n            <!--<label class=\"formula-helptext\">\r\n              <a href=\"http://mathjs.org/docs/reference/functions.html\" target=\"_blank\" [ngStyle]=\"{color:'#fb545b'}\">Click here</a> to learn more.\r\n            </label>-->\r\n            <div class=\"col-xs-12 no-padding formula-resulttable\">\r\n              <div class=\"col-xs-12 no-padding\">\r\n                <label>Sample Result\r\n                  <div class=\"help-tip formula-helptip\">\r\n                      <i class=\"material-icons\">info_outline</i>\r\n                      <div class=\"help-checktip\"> Based on a random selection of user responses</div>\r\n                    </div>\r\n                </label>\r\n                <a href=\"javascript:void(0);\" (click)=\"refreshClick()\"> Refresh Combination</a>\r\n              </div>\r\n              <div class=\"formula-tabledata col-xs-12 scrollbar\">\r\n                <div class=\"for-horzscroll\">\r\n                  <table>\r\n                    <tbody>\r\n                    <tr id=\"random-ques-nums\"></tr>\r\n                    <tr id=\"random-ques-titles\"></tr>\r\n                    </tbody>\r\n                  </table>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-xs-12 formula-tabledata-result\">\r\n                <span>Result:</span>\r\n                <span>{{finalRandomValue}}</span>\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <div class=\"col-xs-12 col-sm-3 formula-right-new\">\r\n            <div class=\"col-xs-12 no-padding\">\r\n              <h3 class=\"formula-right-heading text-center\">Formula Settings</h3>\r\n            </div>\r\n            <div class=\"col-xs-12 no-padding formula-new-units\">\r\n              <div class=\"formula-footer-unit\">\r\n                <label>Unit of Variable:</label>\r\n                <div class=\"formula-footer-range-slider\">\r\n                  <div class=\"switch\">\r\n                    <label>\r\n                      <input  type=\"checkbox\"\r\n                             (change)=\r\n                               \"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.prefix=!jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.prefix;\r\n                                jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.postfix=!jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.postfix;\r\n\r\n                                updateFormulaUIComponents()\"\r\n                             [checked]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.prefix\">\r\n                      <span class=\"lever\"></span>\r\n                    </label>\r\n\r\n                  </div>\r\n                </div>\r\n                <div class=\"formula-footer-rangetext hide\" *ngIf=\"!jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.postfix\">\r\n                  <h3>No. unit of variable is set</h3>\r\n                </div>\r\n                <div class=\"col-xs-12 no-padding\">\r\n                  <div class=\"formula-footer-unitinput\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.prefix\">\r\n                    <span>Prefix</span>\r\n                    <input type=\"text\" [(ngModel)]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.preValue\" (change)=\"updateFormulaUIComponents()\"\r\n                           placeholder=\"$\" />\r\n                  </div>\r\n                  <div class=\"formula-footer-unitinput\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.postfix\">\r\n                    <span>Suffix</span>\r\n                    <input type=\"text\" [(ngModel)]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].units.postValue\" (change)=\"updateFormulaUIComponents()\"\r\n                           placeholder=\"%\" />\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"formula-footer-range\">\r\n                <div class=\"col-xs-12 no-padding\">\r\n                  <label>Show Range:\r\n                    <div class=\"range-tip\">\r\n                      <i class=\"material-icons\">help</i>\r\n                      <div class=\"range-checktip\">If you want to show your result as a range rather than a fixed number, you can select upper and lower limits as percentages.</div>\r\n                    </div>\r\n                  </label>\r\n                  <div class=\"formula-footer-range-slider\">\r\n                    <div class=\"switch\">\r\n                      <label>\r\n                        <input type=\"checkbox\"\r\n                               (change)=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.status=!jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.status;\r\n                                jsonBuilderHelper.getSelectedControl().config.showHelp = false;\r\n                               updateFormulaUIComponents()\"\r\n                               [checked]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.status\">\r\n                        <span class=\"lever\"></span>\r\n                      </label>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n                <div class=\"formula-footer-rangetext hide\" *ngIf=\"!jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.status\">\r\n                  <h3>Showing Value in Fixed Range</h3>\r\n                </div>\r\n                <div class=\"formula-footer-rangeinput\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.status\">\r\n                  <div class=\"col-xs-12 show-range-new\">\r\n                    <label>Lower Bound\r\n                      <div class=\"range-tip\">\r\n                        <i class=\"material-icons\">help</i>\r\n                        <div class=\"range-checktip\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.lower.type == 'Percentage'\">\r\n                          The Lower Bound will be the result calculated from the formula above minus this percentage.\r\n                        </div>\r\n                        <div class=\"range-checktip\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.lower.type == 'Number'\">\r\n                          The  Lower Bound will be the result calculated from the formula above minus this absolute value.\r\n                        </div>\r\n                      </div>\r\n                    </label>\r\n                    <input  type=\"number\" id=\"valueLabel1\" (keyup)=\"updateFormulaUIComponents()\" [(ngModel)]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.lower.value\" placeholder=\"Value\" class=\"rangeinput-main\" />\r\n                    <span>%</span>\r\n                  </div>\r\n                  <div class=\"col-xs-12 show-range-new\">\r\n                    <label>Upper Bound\r\n                      <div class=\"range-tip\">\r\n                        <i class=\"material-icons\">help</i>\r\n                        <div class=\"range-checktip\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.lower.type == 'Percentage'\">\r\n                          The upper bound will be the result calculated from the formula above plus this percentage.\r\n                        </div>\r\n                        <div class=\"range-checktip\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.lower.type == 'Number'\">\r\n                          The upper bound will be the result calculated from the formula above plus this absolute value.\r\n                        </div>\r\n                      </div>\r\n                    </label>\r\n                    <input  type=\"number\" id=\"valueLabel2\" (keyup)=\"updateFormulaUIComponents()\" [(ngModel)]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].range.higher.value\" placeholder=\"Value\" class=\"rangeinput-main\" />\r\n                    <span>%</span>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"formula-footer-decimal\">\r\n                <label>Decimals:</label>\r\n                <div class=\"formula-footer-decimalinput\">\r\n                  <select (change)=\"onChangeDecimalPlaces($event.target.value)\">\r\n                    <option [selected]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].decimal==0\" value=\"0\">No Decimal</option>\r\n                    <option [selected]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].decimal==1\" value=\"1\">.0</option>\r\n                    <option [selected]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].decimal==2\" value=\"2\">.00</option>\r\n                    <option [selected]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].decimal==3\" value=\"3\">.000</option>\r\n                    <option [selected]=\"jsonBuilderHelper.getJSONBuilt().formula[formulaIndex].decimal==4\" value=\"4\">.0000</option>\r\n                  </select>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      <div class=\"modal-footer\">\r\n        <div class=\"formula-footer-left\">\r\n          <label>Expected Result Between: </label>\r\n          <ul id=\"final-result-range\">\r\n            <li>0</li>\r\n          </ul>\r\n        </div>\r\n        <div class=\"col-xs-3 no-padding formula-footer-right\">\r\n          <a [class.hide]=\"error\" class=\"formula-item formula-final\" (click)=\"getFormula($event)\" data-dismiss=\"modal\" data-formula=\"\">Apply Formula</a>\r\n          <a *ngIf=\"error\" class=\"disable-formula\">Apply Formula</a>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</div>\r\n<!-- End: Modal Formula -->\r\n"

