import { Component,OnInit } from '@angular/core';
import { CookieService, FeatureAuthService} from './../../shared/services/index';
import {MarketingService} from '../../shared/services/marketing.service';
import {Script} from '../../shared/services/script.service';

declare var window: any;

@Component({
  selector: 'og-builder',
  template: `
    <router-outlet></router-outlet>
  `,
})

export class BuilderParentComponent implements OnInit{
  constructor(
    private _cookieService : CookieService,
    private _featureAuthService : FeatureAuthService,
    private _script:Script,
    private _marketingService:MarketingService
  ) {
      this._script.load('marketing')
        .then((data) => {
          //console.log('Scripts Loaded', data);
          if(data.length && data[0].status == 'Loaded')
            _marketingService.initMarketingStuff();
        })
        .catch((error) => {
          //any error
        });
  }
  ngOnInit(){
  }
}



