import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {JSONBuilder} from '../../../../../services/JSONBuilder.service';
import { Integrations } from './../../../../../../../shared/models/integrations';
import {IntegrationService} from './../../../../../../../shared/services/integration.service';
import {SubDomainService} from './../../../../../../../shared/services/index';
//import {ConfigIntegrationsComponent} from './../integrations.component';
declare var jQuery: any;
declare var window: any;

@Component({
  selector :'og-activecampaign-component',
  templateUrl: './activeCampaign.component.html',
  styleUrls: ['./../../../assets/css/component_config.style.css', './../../../../../../../../assets/css/sahil-hover.css']
})

export class ActiveCampaignComponent implements OnInit {
    activeCampaignForm : FormGroup ;
    isDisabled: boolean = true;
    company :any;
    isLoading : boolean = false;
    integrations: any = new Integrations({});
    isActiveCampaignError:boolean = false;
    activeCampaignErrorMsg:string;
    lists:any;
    selectedList:string = '';
    testLeads: any;
    isListExist:boolean = false;
    activeCampaignConnect:boolean = true;
    activeCampaignList:boolean = false;
    activeCampaignLead:boolean = false;
    selectedListName:string = '';
    @Output() notify = new EventEmitter();
    constructor(
        //private _parent:ConfigIntegrationsComponent,
        private  _integrationService : IntegrationService,
        private jsonBuilderHelper: JSONBuilder,
        private _subdomainService: SubDomainService,
        private fb: FormBuilder,  ) {
        this.company = this._subdomainService.subDomain.company_id;
    }

    ngOnInit() {

        this.activeCampaignForm = this.fb.group({
           apiKey : [this.integrations.api_key, Validators.compose([Validators.required])],
           account : [this.integrations.account, Validators.compose([Validators.required])],
        });
    }

    connectActiveCampaign() {
       this.isActiveCampaignError = false;
       jQuery('#btnmActiveCampaign').html('please Wait...').attr('disabled', true);
        let data = {
           key:this.activeCampaignForm.value.apiKey,
           account: this.activeCampaignForm.value.account
        };
       this._integrationService.authorization(data, 'active_campaign')
       .subscribe((response)=>{
         if(response.success) {
           this.testLeads = null;
           this.notify.emit({
             flag:true,
             action:'connect',
             type:'active_campaign',
             data:response
           });
           this.getList('active_campaign');
         }
         else {
          this.activeCampaignErrorMsg = 'Please Enter Your Correct API Key or Account Name ';
          this.isActiveCampaignError = true;
          jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
         }
       },
       (error)=>{
          this.activeCampaignErrorMsg = 'Please Enter Your Correct API Key or Account Name ';
          this.isActiveCampaignError = true;
          jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
       })   
    }

    getList(type){
      this.isActiveCampaignError = false;
      this.isListExist = false;
          this.isLoading = true;
          this._integrationService.getList(type)
          .subscribe((data:any)=>{
                this.lists = Object.keys(data).map(function (key) { return data[key]; });
                if(this.selectedList !== '') this.listSelected(this.selectedList);
                this.isLoading = false;
                this.getTestLeads();
          },
          (error)=>{
             this.isActiveCampaignError = true;
             this.activeCampaignErrorMsg = 'We ran some problem with the getting list from Active Campaign.Please Re-configure';
          })
    }

    getTestLeads(){
      if(!this.testLeads){
        this._integrationService.getCalcBasicFields('active_campaign',this.jsonBuilderHelper.getJSONBuilt()._id)
          .subscribe((data:any)=>{
            this.isLoading = false;
            this.testLeads = data.calc;
            jQuery('#btnmActiveCampaign').html('Connect').attr('disabled', false);
            this.activeCampaignConnect = false;
            this.activeCampaignList = true;
            if(!jQuery('#active-campaign').hasClass('in'))    jQuery('#active-campaign').modal('show');
            if(this.lists.length > 0){ 
              this.isListExist = true;
              this.isDisabled = this.selectedList != '' ? false : true;
            }
        },
        (error)=>{
             this.isActiveCampaignError = true;
             this.activeCampaignErrorMsg = 'We ran some problem with the Test Field for Active Campaign.Please Re-configure';
          })
      }
      else {
        this.activeCampaignConnect = false;
        this.activeCampaignList = true;
        if(!jQuery('#active-campaign').hasClass('in'))    jQuery('#active-campaign').modal('show');
        if(this.lists.length > 0){ 
              this.isListExist = true;
              this.isDisabled = this.selectedList != '' ? false : true;
        }
      }
    }

    configure(){
      jQuery('#activeCampaignLead').html('please Wait...').attr('disabled', true);
      this.activeCampaignList = false;
      this.activeCampaignLead = true;
    }

    sendTestLeads(){
        this.isActiveCampaignError = false;
        jQuery('#btn-activeCampaign-test').html('please Wait...').attr('disabled', true);
        let postData = {};
        this.testLeads.map(function(lead, index){
            postData[lead.api_key] = lead.value;
        });
        postData["p["+this.selectedList+"]"] = this.selectedList;
        this._integrationService.sendTestLeads('active_campaign', postData, this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((data:any)=>{
            this.saveMapFields();
        },
        (error)=>{
          this.isActiveCampaignError = true;
          this.activeCampaignErrorMsg = 'We get some problem with the sending leads to Active Campaign.Please Re-configure';
          jQuery('#btn-activeCampaign-test').html('TEST CONNECTION').attr('disabled', false);
        })
    }

    saveMapFields(){
        let mapFields = [];
        this.isActiveCampaignError = false;
        this.testLeads.map(function(val, index){
                mapFields.push({
                    crm: val.api_key,
                    calc: val.name
                });
            })
          let data = {
            'map_fields' : mapFields,
            'list_id'    : this.selectedList
        }    
        this._integrationService.sendMapFieldsBasic('active_campaign', this.jsonBuilderHelper.getJSONBuilt()._id, data)
            .subscribe((response)=>{
                this.notify.emit({
                 flag:true,
                 action:'activate',
                 type:'active_campaign',
                });
                jQuery('#active-campaign').modal('hide');
                this.activeCampaignConnect = true;
                jQuery('#btn-activeCampaign-test').html('TEST CONNECTION').attr('disabled', false);
        },
        (error)=>{
            this.isActiveCampaignError = true;
            this.activeCampaignErrorMsg = 'Error';
            jQuery('#btn-activeCampaign-test').html('TEST CONNECTION').attr('disabled', false);
        })
    }

    requestList(value){
        this.selectedList = value;
        this.listSelected(value);
        //console.log(this.selectedListName);
        if(value == 0)    this.isDisabled = true;
        else this.isDisabled = false
    }

    addIsfocusedKey(id) {
       jQuery('#'+id).addClass('is-focused');
       this.isActiveCampaignError = false;
       this.activeCampaignErrorMsg ='';
    }

    removeIsfocusedKey(id) {
      jQuery('#'+id).removeClass('is-focused');
    }
    getMappedFields(fields :any){
      this.selectedList = fields.list_id;
    }

    listSelected(listId){
      this.lists.forEach((list, index)=>{
        if(list.id == listId)
          this.selectedListName = list.name;
      })
    }
   /* modalClose(modalId){
       jQuery('#'+modalId).data('modal',null);
    }*/

    /*endModal(){
        jQuery('#active-campaign').modal('hide');
        jQuery('#active-campaign').data('modal',null);
        jQuery('#active-campaign-connect').data('modal',null);
        jQuery('#active-campaign-list').data('modal',null);
        jQuery('#active-campaign-connect').removeClass('hide');
        jQuery('#active-campaign-list').addClass('hide');
         jQuery('#view-activeCampaign').addClass('hide');
    }*/
}




