import { Component, OnInit, AfterViewInit, Input, ChangeDetectorRef } from '@angular/core';
import { SubDomainService } from '../../../shared/services/index';
import { environment } from '../../../../environments/environment';
import { BuilderService } from '../../+builder/services/builder.service';
import { Title } from '@angular/platform-browser';

declare var jQuery: any;
declare var window: any;
declare var document: any;

@Component({
  selector: 'og-preview',
  template: `
    <div class="responsive-menu" (mouseenter)="onMouseEnter()">
        <span class="title">Resize Template: </span>
        <div class="icon-block">
            <a href="javascript:void(0);" (click)="switchView('desktop')" [class.active-view] = "className==='desktop'">
                <i class="material-icons">desktop_mac</i> <span>|</span></a>
            <a href="javascript:void(0);" (click)="switchView('tablet')"  [class.active-view] = "className==='tablet'">
                <i class="material-icons">tablet_mac</i><span>|</span> </a>
            <a href="javascript:void(0);" (click)="switchView('mobile')"  [class.active-view] = "className==='mobile'">
                <i class="material-icons">smartphone</i></a>
        </div>
    </div>
    <div id="main-profile">
        <iframe
        (mouseenter)="onMouseEnter()" 
        (mouseleave)="onMouseLeave()"
        id="mobile-iframe"
        [ngClass]="{
            desktop: className==='desktop', 
            tablet: className==='tablet',
            mobile: className==='mobile'
        }" 
        [src]="src | safeUrl" align="middle">
        </iframe>
    </div>`,
  styles: [`
    @font-face {
        font-family: montserratregular;
        font-style: normal;
        font-weight: 400;
        src: url(../../../assets/fonts/montserrat-regular-webfont.eot) format("embedded-opentype"), url(../../../assets/fonts/montserrat-regular-webfont.woff2) format("woff2"), url(../../../assets/fonts/montserrat-regular-webfont.woff) format("woff"), url(../../../assets/fonts/montserrat-regular-webfont.ttf) format("truetype")
    }
    .responsive-menu {
        background: rgba(0, 0, 0, 0.45);
        color: #fff;
        width: 320px;
        text-align: center;
        padding: 6px 6px;
        position: absolute;
        z-index: 999;
        margin: 0 auto;
        margin-left: auto;
        margin-right: auto;
        left: -3px;
        right: 0;
        top: 10px;
        font-size: 13px;
    }

    .responsive-menu a {
        color: #fff;
        padding: 0 5px;
            float: left;
    }

    .responsive-menu a span {
        margin-top: -20px;
        margin-left: 13px;
        float: right;
        margin-top: 3px;
        color: rgba(255, 255, 255, 0.31);
    }

    iframe {
        width: 100%;
        height: 100%;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        top: 0px;
        border: none;
    }
    .active-view i{
        color: #fb545b;
    }
    .responsive-menu span.title {
        margin-right: 26px;
        float: left;
        margin-top: 7px;
        margin-left: 26px;
    }

    .responsive-menu div {
        float: left;
        margin-top: 4px;
    }

    .desktop {
        width: 100%;
        height: 100%;
    }

    .mobile {
        width: 375px;
        height: 570px;
        margin: 1% auto;
        height:570px;
        border: 1px solid #dcdddf;
    }

    .tablet {
        width: 775px;
        height: 100%;
        margin: 0 auto;
        border: 1px solid #dcdddf;
    }
        @font-face {
        font-family: 'Material Icons';
        font-style: normal;
        font-weight: 400;
        src: url(MaterialIcons-Regular.eot);
        src: local('Material Icons'), local('materialIcons-Regular'), url(../assets/fonts/materialIcons-Regular.woff2) format('woff2'), url(../assets/fonts/materialIcons-Regular.woff) format('woff'), url(../assets/fonts/materialIcons-Regular.ttf) format('truetype')
      }
        .material-icons {
        font-family: 'Material Icons';
        font-weight: 400;
        font-style: normal;
        display: inline-block;
        line-height: 1;
        text-transform: none;
        letter-spacing: normal;
        word-wrap: normal;
        white-space: nowrap;
        direction: ltr;
        -webkit-font-smoothing: antialiased;
        text-rendering: optimizeLegibility;
        -moz-osx-font-smoothing: grayscale;
        font-feature-settings: 'liga'
      }
      .responsive-menu i{ font-size:24px;}
`]
})

export class PreviewComponent implements OnInit, AfterViewInit {
  @Input() json: any;
  src: string;
  className: string = 'desktop';
  constructor(private subDomainService: SubDomainService,
    private cdr: ChangeDetectorRef,
    private titleService: Title,
    private _builderService: BuilderService) { }

  ngOnInit() {
    let calc = localStorage.getItem('calc');
    let project =localStorage.getItem('project');
    if (calc && project != 'New') {
      this._builderService.getProject({ _id: calc, company: this.subDomainService.subDomain.sub_domain })
        .subscribe(
        (response: any) => {
          if (!jQuery.isEmptyObject(response)) {
            let responseJson  = JSON.stringify(response);
            jQuery('#favicon').attr('href', JSON.parse(responseJson).favicon);
            this.titleService.setTitle(JSON.parse(responseJson).title.replace(/(<([^>]+)>)/ig, ''));
            localStorage.setItem('template', responseJson);
            this.src = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/preview/previewFrame';
          }
        },
        (error: any) => { }
        );
    } else {
      let template = localStorage.getItem('template');
      if (template || this.json) {
        let app = this.json || JSON.parse(template);
        if (app.parentApp)
          localStorage.setItem('calc', app.parentApp);
        else
          localStorage.setItem('calc', app._id);

        this.src = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/preview/previewFrame';

        jQuery('meta[name=description]').attr('content', app.description);
        document.title = app.title;
        jQuery('#favicon').attr('href', app.favicon);
      }
    }

    window.Intercom('update', { hide_default_launcher: true });
  }

  ngAfterViewInit() {
    //code
    this.viewOnWindowWidthBasis();
    jQuery(window).resize(() => {
      this.viewOnWindowWidthBasis();
    });
  }

  viewOnWindowWidthBasis(): void {
    let width = jQuery(window).width();
    if (width > 775)
      this.switchView('desktop');
    else if (width <= 775 && width > 375)
      this.switchView('tablet');
    else
      this.switchView('mobile');
    this.cdr.detectChanges();
  }

  onMouseEnter() {
    if (this.className === 'mobile')
      jQuery('.responsive-menu').fadeIn(600);
  }

  onMouseLeave() {
    if (this.className === 'mobile')
      jQuery('.responsive-menu').fadeOut(600);
  }

  switchView(className: string) {
    this.className = className;
    let width = 600;
    let height = 400;
    window.resizeBy(width, height);
    window.moveTo(((screen.width - width) / 2), ((screen.height - height) / 2));
  }
}



