import { Component, ViewEncapsulation, Input, AfterViewInit, OnChanges, OnDestroy } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
import { FormulaService } from '../../../services/formula.service';
declare var jQuery: any;
declare var filepicker: any;
declare var observe: any;
@Component({
  selector: 'common-editor',
  template: `   
            <div class="ques-textarea-parent">
                <span *ngIf="jsonBuilderHelper.getJSONBuilt().template!='inline-temp'" class="form-label">QUESTION TITLE:</span> 
                <span *ngIf="jsonBuilderHelper.getJSONBuilt().template=='inline-temp'" class="form-label">QUESTION <span class="small-sub-text">(Text before answer)</span>:</span> 
                <div class="help-tip editor-helptip" *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card' && control.imageVisible">
                    <i class="material-icons">info_outline</i>
                    <div class="help-checktip">Recommended Dimensions are 800px by 300px.</div>
                </div>
                <div *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'">
                  <label class="rd-icon ic rd-option">
                      <div class="help-text">Image</div>
                      <input type="radio" name="ques" id="icon26" value="" (click)="showImage()" [checked]="control.imageVisible">
                      <label for="icon26"><i class="material-icons">photo_camera</i> </label>
                  </label>
                  <label class="rd-icon ic rd-option">
                      <div class="help-text">Text</div>
                      <input type="radio" name="ques" id="icon25" value="" (click)="hideImage()" [checked]="!control.imageVisible" >
                      <label for="icon25"><i class="material-icons">menu</i> </label>
                  </label>                  
                </div>
                <!--<textarea class="form-text ques-title ques-title2" [(ngModel)]="control.props.title" (keyup)="textAreaAdjust($event)" (keypress)="textEnd($event)" row="1"></textarea>-->
                <textarea class="wysiwyg{{control._id}}" [(ngModel)]="control.props.title" [value]="control.props.title" ></textarea>
                <div class="type-details-sub" *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'inline-temp'">
                  <span class="form-label">QUESTION <span class="small-sub-text">(Text after answer)</span>:</span> 
                  <textarea class="wysiwyg-after{{control._id}}" [(ngModel)]="control.props.followUpText" [value]="control.props.followUpText" ></textarea>
                  <!--<textarea class="form-text ques-title ques-title1" [(ngModel)]="control.props.followUpText" (keyup)="textAreaAdjust1($event)" (keypress)="textEnd($event)" row="1"></textarea>-->
                </div>
                <div *ngIf="jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'">
                <div class="div-check" *ngIf="control.imageVisible">
                  <div class="top-space10 col-md-12 no-padding">
                    <div class="input-group">
                      <input
                        type="text"
                        class="file-input"
                        value="{{(control.imageName != '')?control.imageName:control.imageURL}}"
                        placeholder="No file"
                        readonly
                      >
                      <label class="input-group-btn">
                        <span class="btn btn-basic2" data-fp-apikey="ANQWcFDQRUiGfBqjfgINQz" (click)="uploadImage(control);">
                        UPLOAD<input type=""  style="display: none;">
                        </span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
                
            </div>        
    `,
  encapsulation: ViewEncapsulation.None,
})

export class CommonEditor implements AfterViewInit, OnChanges, OnDestroy {
  @Input() control: any;
  filePickerKey: any = 'A3ygIw4hISSCdApqW4SAwz';

  ngAfterViewInit() {
    setTimeout(function () { jQuery('.ques-title').css('height', jQuery('.ques-title').prop('scrollHeight')); }, 1);
    this.initWyswyg();

  }
  showImage(){
    this.control.imageVisible = true;
  }
  hideImage(){
    this.control.imageVisible = false;
  }
  constructor(private jsonBuilderHelper: JSONBuilder,
    private formulaService: FormulaService) {
    this.control = jsonBuilderHelper.getSelectedControl();
  }
  textAreaAdjust(event: any) {
    jQuery('.ques-title2').css('height', jQuery('.ques-title2').prop('scrollHeight'));
  }
  textAreaAdjust1(event: any) {
    jQuery('.ques-title1').css('height', jQuery('.ques-title1').prop('scrollHeight'));
  }
  textEnd(event: any) {
    if (event.keyCode === 10 || event.keyCode === 13)
      event.preventDefault();
  }

  ngOnChanges() {
    this.initWyswyg();
  }

  uploadImage(control: any) {
    filepicker.setKey(this.filePickerKey);
    filepicker.pick(
      {
        mimetypes: ['image/*'],
        imageQuality: 50
      },
      (InkBlob: any) => {
        control.imageURL = InkBlob.url;
        control.imageName = InkBlob.filename;
        jQuery('#filepicker_dialog_container').find('a').click();
      },
      (FPError: any) => {
        console.log(FPError.toString());
      }
    );
  }

  ngOnDestroy() {
    jQuery('.wysiwyg' + this.control._id).froalaEditor('destroy');
    jQuery('.wysiwyg-after' + this.control._id).froalaEditor('destroy');
  }

  initWyswyg() {
    let self = this;
    if (jQuery('.wysiwyg' + this.control._id))
      jQuery('.wysiwyg' + this.control._id).froalaEditor('destroy');
    if (jQuery('.wysiwyg-after' + this.control._id))
      jQuery('.wysiwyg-after' + this.control._id).froalaEditor('destroy');
    setTimeout(() => {
      var options: any = {};
      for (var variable in this.formulaService.allValidVariables())
        options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

      jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
      jQuery.FroalaEditor.RegisterCommand('questions', {
        title: 'Questions list',
        type: 'dropdown',
        focus: true,
        undo: true,
        icon: 'questions',
        refreshAfterCallback: true,
        options: options,
        callback: function (cmd: any, val: any) {
          this.html.insert(val);
        }
      });

      jQuery('.wysiwyg' + this.control._id).froalaEditor({
        heightMax: 250,
        toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'questions'],
      });

      jQuery('.wysiwyg-after' + this.control._id).froalaEditor({
        heightMax: 250,
        toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'questions'],
      });

      jQuery('.wysiwyg-after' + this.control._id).froalaEditor('html.set', this.control.props.followUpText);
      jQuery('.wysiwyg-after' + this.control._id).on('froalaEditor.contentChanged', (e: any, editor: any) => {
        self.control.props.followUpText = e.currentTarget.value;
      });
      jQuery('.wysiwyg' + this.control._id).froalaEditor('html.set', this.control.props.title);
      jQuery('.wysiwyg' + this.control._id).on('froalaEditor.contentChanged', (e: any, editor: any) => {
        self.control.props.title = e.currentTarget.value;
      });
    }, 10);
  }
}




