import {Component, AfterViewInit, OnInit, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {JSONBuilder} from '../../../../../services/JSONBuilder.service';
import { Integrations } from './../../../../../../../shared/models/integrations';
import {IntegrationService} from './../../../../../../../shared/services/integration.service';
import { CookieService, SubDomainService, FeatureAuthService} from './../../../../../../../shared/services/index';
import {ConfigIntegrationsComponent } from './../integrations.component';
declare var jQuery: any;
declare var window: any;

@Component({
  selector :'og-aweber-component',
  templateUrl: './aweber.component.html',
  styleUrls: ['./../../../assets/css/component_config.style.css', './../../../../../../../../assets/css/sahil-hover.css']
})

export class AweberComponent implements OnInit   {
    aweberAccountList : any =[];
    aweberLists : any =[];
    crmAllFileds: any= [];
    error: boolean = false;
    aweberForm : FormGroup ;
    mailchimp:boolean = false;
    mailChimpErrorMsg:string;
    isLoading: boolean = false;
    isListExist:boolean = false;
    isAccountExist : boolean = false;
    calcAllFileds: any= [];
    insertFields :any =[];
    maperror:boolean = false;
    maperrorMessage:string ;
    listId : any ;
    accountId : any ;
    isListSelected :boolean = false;
    mailchimpKey:any ;
    isMailchimpConnected : boolean = false ;
    listName: string;
    selectedList :any ;
    selectedAccount :any;
    calcType: string ;
    integrationType :string ;
    isLimited : Boolean = false;
    isViewed :boolean = false;
    isMapped : boolean = false;
    dataList: any= [];
    dataSend: any;
    isExist :boolean = true;
    
    integrations: any = new Integrations({});
    mapFields: any =  {
    salesforce : [] ,
    marketo : [],
    mailchimp :[],
    getresponse:[],
    activeCampaign:[],
    aweber :[]
  };
    @Output() notify = new EventEmitter();
    company :any;
    constructor(
        private  _integrationService : IntegrationService,
        private _subdomainService: SubDomainService,
        private jsonBuilderHelper: JSONBuilder,
        private fb: FormBuilder,
        private _featureAuthService : FeatureAuthService  ) {
        this.company = this._subdomainService.subDomain.company_id;
        this.aweberForm = this.fb.group({
           tagName : this.integrations.tags
       });
    }

    ngOnInit() {
      this.isListSelected = false;
    }

   addIsfocusedKey() {
      jQuery('#mailChimp_apiKey').addClass('is-focused');
      this.error = false;
      this.mailChimpErrorMsg ='';
    }
    removeIsfocusedKey() {
      jQuery('#mailChimp_apiKey').removeClass('is-focused');
    }

    getAweberAccounts(){
     this.isLoading = true ;
     if(this.selectedAccount==='' ||this.selectedAccount==='0'){
      this.isListExist = false;
     }else{
       this.isListExist = true;
     }
     this.isAccountExist = false;
     this._integrationService.getAccount('aweber')
        .subscribe((response :any) => {
          if(this.isExist){
            this.getAweberList(this.selectedAccount);
          }
          if(response.total_size!==0){
            this.aweberAccountList = [];
            this.aweberLists = [];
            this.isAccountExist = true ;
            this.isLoading = false;
            let aweberList = response.entries;
            for (let key in aweberList) {
              if (aweberList.hasOwnProperty(key)) {
                let obj = {};
                obj['account_id'] = aweberList[key].id;
                this.aweberAccountList.push(obj);
              }
            }
          }else{
            this.isAccountExist = false;
          }
        }, (error) => {
           //console.log('error',error);
        });
   }

    testSaveLead() {
      if(this.listId){
        let tags =[];
        tags = this.integrations.tags ?this.integrations.tags : this.aweberForm.value.tagName.split(',');
        this.error = false;
        let data = {
            'map_fields' : this.insertFields,
            'list_id'    : this.listId,
            'account'    : this.accountId,
            'tags'       : tags 
        }
        jQuery('.btn-map').html('PLEASE WAIT...').attr('disabled', true);
        this._integrationService.testSaveLead('aweber', this.jsonBuilderHelper.getJSONBuilt()._id, data)
                .subscribe((response) => {
                if (response.status.success) {
                    this.sendMapFields();
                    this.getCalTestData(response.dataSent);
                    this.isViewed = true;
                    this.maperror = false;
                    this.isMapped = true ;
                    window.toastNotification('Connection Test Successful');
                    if(this.isLimited){
                      jQuery('#add-aweber').modal('hide');
                    }
                    this.isListSelected = false;
                    this.isMailchimpConnected = true;
                    //this.updateIntegrationStaus('mailchimp', true);
                }else {
                        jQuery('.btn-map').html('Test Connection').attr('disabled', false);
                }
                }, (error) => {
                jQuery('.btn-map').html('Test').attr('disabled', false);
                });
      }else{
          this.maperror = true;
          this.maperrorMessage = 'Please reconfigure & select the list where you want to save leads';
      }
    }

    getCalcBasicMapFields() {
        this.isLoading = true;
        this._integrationService.getCalcBasicFields('aweber', this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          this.calcAllFileds = [];
          this.insertFields =[];
          let calcfiedls = result;
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              for(let i = 0; i <calcfiedls[key].length; i++) {
                let obj = {};
                let insertObj = {};
                obj['calc_name'] = calcfiedls[key][i].name;
                obj['crm_key'] = calcfiedls[key][i].api_key;
                obj['calc_value'] = calcfiedls[key][i].value;

                insertObj['calc'] = calcfiedls[key][i].name;
                insertObj['crm'] = calcfiedls[key][i].api_key;
                this.calcAllFileds.push(obj);
                this.insertFields.push(insertObj);
              }
            }
          }
        },(error) => {
            //console.log(error,'errorrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrrr');
        });
    }

   getFields(){
    let isIntegrationLimited : Boolean = this._featureAuthService.features.integrations['aweber_limited'];
    let isIntegrationFull: Boolean = this._featureAuthService.features.integrations['aweber'];
     if(isIntegrationLimited && !isIntegrationFull){
        this.isLimited = true;
       this.getCalcBasicMapFields();
     }else{
       this.getCalcMapFields();
     }
   } 
   
   getCalcMapFields() {
     let calc_type = this.jsonBuilderHelper.getJSONBuilt().templateType;
     if (calc_type === 'Numerical') {
       this.calcType = 'Calculator';
     }else {
        this.calcType = 'Quiz';
     }
     this.isLoading = true;
     this.maperror = false ;
     this.maperrorMessage ='';
     if(this.accountId!='' || this.listId!==''){
        this._integrationService.getCalcMapFields('aweber', this.jsonBuilderHelper.getJSONBuilt()._id)
        .subscribe((result) => {
          this.isLoading = false;
          let calcfiedls = result.calc;
          let crmfields = result.crm;
          this.calcAllFileds = [];
          this.crmAllFileds = [];
          for (let key in calcfiedls) {
            if (calcfiedls.hasOwnProperty(key)) {
              let obj = {};
              obj['calc_name'] = calcfiedls[key].name;
              obj['calc_key'] = calcfiedls[key].api_key;
              obj['calc_value'] = 'Sample : ' + calcfiedls[key].value;
              this.calcAllFileds.push(obj);
            }
          }
          for (let key in crmfields) {
              let obj = {};
              obj['crm_name'] = crmfields[key].api_key;
              obj['crm_key'] = crmfields[key].api_key;
              let selectedMapFields = this.mapFields['aweber'];
              let mpFiledLength = selectedMapFields.length;
              for (let i = 0 ; i < mpFiledLength; i++ ) {
                if (crmfields[key].api_key === selectedMapFields[i].crm) {
                  obj['calc_key'] =  selectedMapFields[i].calc;
                  let insertObj = {};
                  insertObj['calc'] = selectedMapFields[i].calc;
                  insertObj['crm'] = crmfields[key].api_key;
                  this.insertFields.push(insertObj);
                  break;
                }else {
                  obj['calc_key'] =  '';
                }
              }
              this.crmAllFileds.push(obj);
            }
        }, (error) => {
          this.maperror = true;
          this.isLoading = false;
          this.maperrorMessage =  error.error.err_message;
        });
     }else{
        this.isLoading= false;
        this.maperror = true;
        this.maperrorMessage = 'You have not selected the accounts & list yet. Please reconfigure.';
     }
    }

     insertMapData(calcKey: string, event: any ) {
      let obj = {};
      obj['calc'] = calcKey;
      obj['crm'] = event.target.value;
      let fieldslength = this.insertFields.length;
      if (fieldslength > 0) {
        let flagPush = true;
        for (let i = 0; i < fieldslength; i++) {
          if (calcKey === this.insertFields[i].calc) {
               this.insertFields[i]['crm'] = event.target.value;
               flagPush = false;
               //console.log('sdfsdfsfsfd',this.insertFields[i]);
               if(this.insertFields[i]['crm'] == '0') {
                 //console.log('hello');
                 this.insertFields.splice(i,1);
                 return;
               }
               break;
          }
        }
        if (flagPush) {
          this.insertFields.push(obj);
        }
      }else {
          this.insertFields.push(obj);
      }
    }

  getCalTestData(data: any) {
          this.dataSend = data;
          this.dataList = [];
          for (let key in data) {
            if (data.hasOwnProperty(key)) {
              let obj = {};
              obj['key'] = key;
              obj['value'] = data[key];
              this.dataList.push(obj);
            }
          }
        //console.log(this.dataList,'dataaaaaaaaaaaaaaaaa');  
  }

    sendMapFields() {
      jQuery('.btn-map').html('PLEASE WAIT').attr('disabled', false);
      //tags = this.aweberForm.value.tagName ? this.aweberForm.value.tagName.split(',') : this.integrations.tag;
       let data = {
            'map_fields' : this.insertFields
            //'list_id'    : this.listId,
            //'account' : this.accountId,
            //'tags'       : tags
      }
      if(this.isLimited){
        this._integrationService.sendMapFieldsBasic('aweber', this.jsonBuilderHelper.getJSONBuilt()._id,data)
        .subscribe((result) => {
          this.notify.emit({
                 flag:true,
                 action:'activate',
                 type:'aweber'
          });
        }, (error) => {
            jQuery('.btn-map').html('Test Connection').attr('disabled', false);
        });
      }else{
        this._integrationService.sendMapFields('aweber', this.jsonBuilderHelper.getJSONBuilt()._id,this.insertFields)
        .subscribe((result) => {
          this.notify.emit({
                 flag:true,
                 action:'activate',
                 type:'aweber'
          });
        }, (error) => {
            jQuery('.btn-map').html('Test Connection').attr('disabled', false);
        });
      }
    }

  getAweberList(account_id){
    this.isLoading = true;
     if(account_id==='0'){
       this.error = true;
       this.mailChimpErrorMsg = 'Please select the account where you want to save leads';
     }else{
       let data = {
       "id" : account_id
     } 
     this.error = false;
     this.accountId = account_id;
     this.listId = this.selectedList ;
     this._integrationService.getList('aweber',data)
        .subscribe((response:any ) => {
            if(!this.isExist){
              jQuery('#accountId').attr('disabled', true);
            }
            this.isListExist = true;
            this.aweberLists = [];
            this.isLoading = false;
            let aweberLists = response.entries;
            for (let key in aweberLists) {
              if (aweberLists.hasOwnProperty(key)) {
                let obj = {};
                obj['list_name'] = aweberLists[key].name;
                obj['list_id'] = aweberLists[key].id;
                this.aweberLists.push(obj);
              }
            }
        }, (error) => {
           //console.log('error',error);
        });
     } 
   }

    getListID(list_id) {
       this.listName = jQuery('#listName :selected').text();
       if(this.listId!='' || !this.listId) {
            this.listId = '';
            this.listId = list_id;
            this.insertFields.splice(3,1);
        }else{
          this.listId  = list_id;
        }
     }
     

     viewTestData() {
       this.maperror = false;
       if(!this.listId || this.listId === '0'){
          this.error = true;
          this.mailChimpErrorMsg = 'Please select the list where you want to save leads';
        }else{
          this.error = false;
           jQuery('#btnAweberList').html('Please wait...');
           this.isListSelected = true;
           let tags = [];
           tags =  this.integrations.tags ? this.integrations.tags : this.aweberForm.value.tagName.split(',');
           let data = {
             'account_id' : this.accountId,
             'list_id'    : this.listId,
             'list_name'  : this.listName,
             'tags'       : tags
           }
           this._integrationService.saveList('aweber',this.jsonBuilderHelper.getJSONBuilt()._id,data)
            .subscribe((response:any ) => {
              this.getFields();
              //this.getCalcMapFields();    
            }
            , (error) => {
              console.log('error',error);
            });
       }
     }

    getconfig(api_key:any) {
      this.mailchimpKey = api_key ;
      this.integrations.api_key = this.mailchimpKey;

    }
    getMappedFields(fields :any){
      this.selectedAccount = fields.account_id;
      this.selectedList    = fields.list_id;
      this.listId = this.selectedList;
      this.listName = fields.list_nmae;
      this.integrations.tags = fields.tags;
      this.mapFields['aweber'] = fields.map_fields;
    }

    selectAccount(data:any){
      jQuery('#add-aweber').modal('show');
      this.selectedAccount = data.account_id;
      this.selectedList= data.list_id;
        if(this.selectedAccount!=='' && this.selectedList!==''){
          this.isAccountExist = true;
          this.getAweberAccounts();
          //this.getAweberList(this.selectedAccount);
          this.isListExist = true;
          this.isListSelected= false;
          this.isMapped= false;
          this.isViewed = false;
          this.isExist = true;
          this.isLoading = true;
        }else{
          this.isListSelected = false;
          this.isExist = false;
          this.getAweberAccounts();
        }
    }
    testView(){
        this.maperror = false ;
        this.listId = this.selectedList;
        this.accountId = this.selectedAccount;
        this.getFields();
        this.isListSelected = true;
        this.isMapped= false;
        this.isViewed = false;
        jQuery('#add-aweber').modal('show');
    }

    close(){
      jQuery('#add-aweber').modal('hide');
      this.crmAllFileds = [];
      this.calcAllFileds = [];
      this.insertFields = [];
    }


}




