import { Component, AfterViewInit, ViewEncapsulation, DoCheck, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SubDomainService } from '../../../../../../shared/services/index';
import { JSONBuilder } from '../../../../services/JSONBuilder.service';
import { SafeUrl } from '../../../../../templates/pipes/safeUrl.pipe';
import { environment } from '../../../../../../../environments/environment';
import {FeatureAuthService} from "../../../../../../shared/services/feature-access.service";
declare var jQuery: any;

@Component({
  selector: 'config-embedded-code',
  templateUrl: './assets/html/embedded_code.template.html',
  encapsulation: ViewEncapsulation.None
})

export class ConfigEmbeddedCodeComponent implements OnInit {
  hideSmallCode: boolean = true;
  hideFullCode: boolean = true;
  embedVariable: boolean = true;
  smallPageCode: any = '';
  fullPageCode: any = '';
  iloaderJS: any = '';
  src: any;
  constructor(private jsonBuilderHelper: JSONBuilder,
              private subDomainService: SubDomainService,
              private _featureAuthService: FeatureAuthService
  ) { }

  ngOnInit() {
    // this._featureAuthService.features.embedding.active;
    var embedVariable = true;
    if (this.jsonBuilderHelper.getJSONBuilt().template == 'sound-cloud')
      this.iloaderJS = '//' + environment.APP_EXTENSION + '/js/iloader.js';
    else
      this.iloaderJS = '//' + environment.APP_EXTENSION + '/js/loader/iloader.js';
    if (environment.PROTOCOL === 'https://')
      this.src = 'https://' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;
    else
      this.src = '//' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;


    this.smallPageCode = `<div><div id='og-iframe-container' data-url='` + this.src + `' data-width='100%'></div><script src='` + this.iloaderJS + `' async></script></div>`;
    this.fullPageCode = `<div><div id='og-iframe-container' data-url='` + this.src + `' data-width='100%'></div><script src='` + this.iloaderJS + `' async></script></div>`;
  }
  embedToggle() {
    this.hideFullCode = !this.hideFullCode;
    this.hideSmallCode = true;
  }
  embedToggle2() {
    if(this._featureAuthService.features.embedding.active) {
      this.hideSmallCode = !this.hideSmallCode;
      this.hideFullCode = true;
    } else{
      this._featureAuthService.setSelectedFeature('embedding');
      jQuery('.embedding').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }
}



