import { Component, AfterViewInit, OnInit, ViewEncapsulation, OnChanges } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
import { FormulaService } from '../../../services/formula.service';
import { EditorLeadForm } from '../../editors/leadform/editor_leadform.component';
import { AddSection } from './component/addsection.component';
import { BuilderService } from '../../../services/builder.service';
import { JSONItemTracker } from '../../../services/JSONUpdateItemTracker.service';
import { Item, Section, Page } from '../../../models/model';
import { FeatureAuthService } from '../../../../../shared/services/feature-access.service';
import { RecommendationService } from '../../../../templates/services/recommendation.service';
import { EditorWysiwyg } from './../../editors/wysiwyg/editor_wysiwyg.component';
import { environment } from "../../../../../../environments/environment";
import { ShareOutcomeService } from './../../../services/shareOutcome.service';
import { ConditionalResultService } from '../../../services/conditionalResult.service';
import {MembershipService} from '../../../../../shared/services/membership.service';

declare var jQuery: any;
declare var math: any;
declare var filepicker: any;
declare var window: any;
declare var ga: any;
declare var _kmq: any;
declare var FB: any;
declare var bootbox: any;

@Component({
  selector: 'editor_result_page',
  templateUrl: './assets/html/editor_resultPage.html',
  encapsulation: ViewEncapsulation.None
})

export class EditorResultPage implements OnInit, AfterViewInit, OnChanges {
  filePickerKey: any = 'A3ygIw4hISSCdApqW4SAwz';
  page: Page;
  formulaResult: any = {};
  control: any;
  validUrl: boolean = true;
  sectionOrder: any[] = [];
  resultSection: Section;
  leadformSection: Section;
  editorHtml: string;
  QuestionnaireJson: any;
  navigate_url: any;
  editorControl: any = {
    result_header: {},
    section: {},
    leadform: {},
    click_button: {},
    share_links: {},
    cta_shares: {},
    cta_likes: {},
    result_redo: {},
    backImage: {},
    result_disclaimer: {},
    footer_links: {},
    result_summary: {}
  };
  private isCtaAccessible: Boolean = false;
  private isRealTimeResult: Boolean = false;
  private isDisclaimers: Boolean = false;
  private shareLinks: any;

  constructor(private jsonBuilderHandler: JSONBuilder,
              private _builderService: BuilderService,
              private _ItemTrackService: JSONItemTracker,
              private formulaService: FormulaService,
              private _featureAuthService: FeatureAuthService,
              private recommendationService: RecommendationService,
              private _outcomeService: ShareOutcomeService,
              private conditionalResultService: ConditionalResultService,
              private membershipService:MembershipService
  ) {
    this.page = jsonBuilderHandler.getSelectedPage();
    for (let section in this.page.sections) {
      for (let item in this.page.sections[section].items) {
        // check for result outputs
        if (this.page.sections[section].title === 'Result') {
          this.resultSection = this.page.sections[section];
          this.jsonBuilderHandler.setSelectedControl(this.resultSection.items[0]);
        }
        if (this.page.sections[section].type === 'LeadForm') this.leadformSection = this.page.sections[section];
        for (let prop in this.editorControl) {
          if (prop === this.page.sections[section].items[item].type) this.editorControl[prop] = this.page.sections[section].items[item];
        }
      }
    }
    if (jsonBuilderHandler.getJSONBuilt().templateType == 'Recommendation') this.shareLinks = this._outcomeService.getSharelinks();
    //console.log('this.shareLinks = this._outcomeService.getSharelinks();', this.jsonBuilderHandler.getJSONBuilt().formula);
  }

  isEmpty(obj: any) {
    if (obj == null || obj.length === 0 || typeof obj !== "object") return true;

    if (obj.length > 0) return false;

    for (var key in obj)
      if (key != '_id' && obj[key] != "") return false;

    return true;
  }

  updateCondInitialVisibility() {
    let resultPage = this.jsonBuilderHandler.getJSONBuilt().pages.filter((page: any) => page.type == 'Result');
    if (resultPage) {
      let resultSection = resultPage[0].sections.filter((section: any) => section.type == 'Result');
      if (resultSection) resultSection[0].items.map((item: any) => item.isIconPresent = true);
    }
  }

  ngOnInit() {
    this.updateCondInitialVisibility();
    this._ItemTrackService.resetUnsavedData();
    this._ItemTrackService.setUnSavedPage(this.page);
    this.isRealTimeResult = this._featureAuthService.features.real_time_results.active;
    this.isCtaAccessible = this._featureAuthService.features.cta.active;
    this.isDisclaimers = this._featureAuthService.features.disclaimers.active;
    if(this.jsonBuilderHandler.getJSONBuilt().templateType=='Numerical' && !this.isEmpty(this.editorControl.cta_shares) && !this.isEmpty(this.editorControl.cta_likes))
     this.editorControl.cta_shares.options[0].title=this.editorControl.cta_likes.options[0].title=this.editorControl.click_button.props.unit;
    this.membershipService.getplanSubscription().subscribe(
      (response: any) => {
      if(response.currentplan.subscription.plan_id=='appsumo_d'){
        this.editorControl.leadform.props.title='Submit';
        this.jsonBuilderHandler.getJSONBuilt().navigate_Url='';
      }},
      (error :any ) =>  {}
    );
  }

  ngOnChanges() {
    //
  }

  togglePrivacy() {
    this.editorControl.footer_links.visible = !this.editorControl.footer_links.visible;
  }

  toggleWysiwig(control: any) {
    control.visible = !control.visible;
  }

  ngAfterViewInit() {
    let self = this;
    let editor: any;
    let editor1: any;
    editor = jQuery('.wysiwyg-result').froalaEditor({
      heightMax: 250, toolbarButtons: ['bold', '|', 'italic', '|', 'underline',],
    });
    editor1 = jQuery('.wysiwyg-desc').froalaEditor({
      heightMax: 250, toolbarButtons: ['bold', '|', 'italic', '|', 'underline',],
    });

    jQuery('.wysiwyg-result').on('froalaEditor.contentChanged', function (e: any, editor: any) {
      self.editorHtml = e.currentTarget.value;
      self.editorControl.result_header.props.title = e.currentTarget.value;
    });

    jQuery('.wysiwyg-desc').on('froalaEditor.contentChanged', function (e: any, editor1: any) {
      self._outcomeService.getSelectedFormula().html = e.currentTarget.value;
    });

    this.initWysiwyg();
    this.handleNameEdit();
    jQuery('.type-details-head').on('click', function () {
      jQuery(this).parent().toggleClass('tab-collapse');
    });

    jQuery(document).on('click', '.result-area ', function () {
      jQuery('.result-area-tab').removeClass('hide');
      jQuery('.details-area-tab').addClass('hide');
      // jQuery('.result-area').css('background-color', '#f6f8f9');
      // jQuery('.details-area').css('background-color', '#ffffff');
      jQuery('.result-area').css('color', '#23a0d6');
      jQuery('.details-area').css('color', '#8e989f')
    })

    jQuery(document).on('click', '.details-area', function () {
      jQuery('.result-area-tab').addClass('hide');
      jQuery('.details-area-tab').removeClass('hide');
      // jQuery('.result-area').css('background-color', '#ffffff')
      // jQuery('.details-area').css('background-color', '#f6f8f9')
      jQuery('.result-area').css('color', '#8e989f')
      jQuery('.details-area').css('color', '#23a0d6')
    })

    jQuery(document).on('click', '.flip-container', function () {
      jQuery(this).addClass('hover').siblings('.flip-container').removeClass("hover");
    });
  }

  conditionalChanged(i: any, control: any) {
    if (this._featureAuthService.features.conditional_messaging.active) {
      this.jsonBuilderHandler.getJSONBuilt().formula[i].range.status = false;
      control.config.showHelp = !control.config.showHelp;
      this.jsonBuilderHandler.conditional.conditionalVar = false;
      control.optionImageVisible = false;
    } else {
      this._featureAuthService.setSelectedFeature('conditional_messaging');
      jQuery('.conditional_messaging').addClass('activegreen limited-label');
      this.showPremiumPopup();
    }
  }

  conditionalToggleClick(i: any) {
    if (this.jsonBuilderHandler.getJSONBuilt().formula[i].result.toString().trim() == '') {

      bootbox.dialog({
        // closeButton: false,
        message: `
                    <div class="bootbox-body-left">
                        <div class="mat-icon">
                           <i class="material-icons">error</i>
                        </div>
                    </div>
                    <div class="bootbox-body-right">
                       <p>To use conditional messaging, please set up your formula first.</p>
                    </div>
                `,
        buttons: {
          success: {
            label: "OK",
            className: "btn btn-ok btn-hover",
            callback: function () { }
          }
        }
      });
    }
  }
  initWysiwyg() {
    let self = this;
    setTimeout(() => {
      var options: any = {};
      for (var variable in this.formulaService.allValidVariables())
        options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

      jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
      jQuery.FroalaEditor.RegisterCommand('questions', {
        title: 'Questions list',
        type: 'dropdown',
        focus: true,
        undo: true,
        icon: 'questions',
        refreshAfterCallback: true,
        options: options,
        callback: function (cmd: any, val: any) {
          this.html.insert(val);
        }
      });

      jQuery('.wysiwyg-share-desc').froalaEditor('destroy');
      jQuery('.wysiwyg-share-title').froalaEditor('destroy');

      jQuery('.wysiwyg-share-title').froalaEditor({
        heightMax: 250, toolbarButtons: ['questions'],
      });

      jQuery('.wysiwyg-share-desc').froalaEditor({
        heightMax: 250, toolbarButtons: ['questions'],
      });

      // jQuery('.wysiwyg-share-title').on('froalaEditor.focus', function (e: any, editor: any) {
      //   jQuery('.wysiwyg-share-title').froalaEditor('html.set', '<p>' + e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '') + '</p>');
      // });
      //
      // jQuery('.wysiwyg-share-desc').on('froalaEditor.focus', function (e: any, editor: any) {
      //   jQuery('.wysiwyg-share-desc').froalaEditor('html.set', '<p>' + e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '') + '</p>');
      // });

      if (this.jsonBuilderHandler.getJSONBuilt().templateType == 'Numerical') {
        /* TitleForShare In Numerical Start*/
        let defaultTitle = self.editorControl.cta_shares.options[0].label;
        if (!this._featureAuthService.features.custom_branding.share_text && !(new RegExp("\\| via @outgrowco\\b")).test(defaultTitle)) {
          defaultTitle += ' | via @outgrowco';
          self.editorControl.cta_shares.options[0].label = defaultTitle;
          self.editorControl.cta_shares.options[1].label = defaultTitle;
          self.editorControl.cta_shares.options[2].label = defaultTitle;
          jQuery('.wysiwyg-share-title').froalaEditor('html.set', `<p>${defaultTitle}</p>`);
        }

        jQuery('.wysiwyg-share-title').on('froalaEditor.contentChanged', function (e: any, editor: any) {
          let decoded = jQuery('<textarea/>').html(e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '')).text();
          let {text, gText} = self.getShareTitle(decoded, defaultTitle, false);
          defaultTitle = gText ? gText : defaultTitle;
          self.editorControl.cta_shares.options[0].label = text;
          self.editorControl.cta_shares.options[1].label = text;
          self.editorControl.cta_shares.options[2].label = text;
        });
        /* TitleForShare In Numerical End*/
        /* Description In Numerical Start*/
        // let defaultDescription = self.editorControl.cta_shares.options[0].icon;
        // if (!this._featureAuthService.features.custom_branding.share_text && !(new RegExp("@outgrowco\\b")).test(defaultDescription)) {
        //   defaultDescription += ' @outgrowco';
        //   self.editorControl.cta_shares.options[0].icon = defaultDescription;
        //   self.editorControl.cta_shares.options[1].icon = defaultDescription;
        //   self.editorControl.cta_shares.options[2].icon = defaultDescription;
        //   jQuery('.wysiwyg-share-desc').froalaEditor('html.set', `<p>${defaultDescription}</p>`);
        // }

        jQuery('.wysiwyg-share-desc').on('froalaEditor.contentChanged', function (e: any, editor: any) {
          let decoded = jQuery('<textarea/>').html(e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '')).text();
          // let {text, gText} = self.getShareTitle(decoded, defaultDescription, true);
          // defaultDescription = gText ? gText : defaultDescription;
          let text=decoded;
          self.editorControl.cta_shares.options[0].icon = text;
          self.editorControl.cta_shares.options[1].icon = text;
          self.editorControl.cta_shares.options[2].icon = text;
        });
        /* Description In Numerical End*/
      } else {
        /* TitleForShare In Recom Start*/
        let defaultTitle = self.shareLinks.share.facebook.title;
        if (!this._featureAuthService.features.custom_branding.share_text && !(new RegExp("\\| via @outgrowco\\b")).test(defaultTitle)) {
          defaultTitle += ' | via @outgrowco';
          self.shareLinks.share.twitter.title = defaultTitle;
          self.shareLinks.share.facebook.title = defaultTitle;
          self.shareLinks.share.linkedin.title = defaultTitle;
          jQuery('.wysiwyg-share-title').froalaEditor('html.set', `<p>${defaultTitle}</p>`);
        }

        jQuery('.wysiwyg-share-title').on('froalaEditor.contentChanged', function (e: any, editor: any) {
          let decoded = jQuery('<textarea/>').html(e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '')).text();
          let {text, gText} = self.getShareTitle(decoded, defaultTitle, false);
          defaultTitle = gText ? gText : defaultTitle;
          self.shareLinks.share.twitter.title = text;
          self.shareLinks.share.facebook.title = text;
          self.shareLinks.share.linkedin.title = text;
          self.updateAllOutcomeCta();
        });
        /* TitleForShare In Recom End*/
        /* DescriptionForShare In Recom Start*/
        // let defaultDescription = self.shareLinks.share.twitter.description;
        // if (!this._featureAuthService.features.custom_branding.share_text && !(new RegExp("@outgrowco\\b")).test(defaultDescription)) {
        //   defaultDescription += ' @outgrowco';
        //   self.shareLinks.share.twitter.description = defaultDescription;
        //   self.shareLinks.share.facebook.description = defaultDescription;
        //   self.shareLinks.share.linkedin.description = defaultDescription;
        //   jQuery('.wysiwyg-share-desc').froalaEditor('html.set', `<p>${defaultDescription}</p>`);
        // }

        jQuery('.wysiwyg-share-desc').on('froalaEditor.contentChanged', function (e: any, editor: any) {
          let decoded = jQuery('<textarea/>').html(e.currentTarget.value.replace(/<(?:.|\n)*?>/gm, '')).text();
          // let {text, gText} = self.getShareTitle(decoded, defaultDescription, true);
          // defaultDescription = gText ? gText : defaultDescription;
          let text=decoded;
          self.shareLinks.share.twitter.description = text;
          self.shareLinks.share.facebook.description = text;
          self.shareLinks.share.linkedin.description = text;
          self.updateAllOutcomeCta();
        });
        /* DescriptionForShare In Recom End*/
      }
    }, 100);
  }

  getShareTitle(decoded: any, oldText: any, isDescription: any) {
    let text: any;
    let gText: any;
    if (!this._featureAuthService.features.custom_branding.share_text) {
      if ((new RegExp("\\| via @outgrowco\\b")).test(decoded)) {
        text = decoded;
        gText = decoded;
      } else {
        this._featureAuthService.setSelectedFeature("custom_branding","share_text");
        this.showPremiumPopup();
        text = oldText;
        if (isDescription)
          jQuery('.wysiwyg-share-desc').froalaEditor('html.set', `<p>${oldText}</p>`);
        else
          jQuery('.wysiwyg-share-title').froalaEditor('html.set', `<p>${oldText}</p>`);
      }
    } else
      text = decoded;
    return { text, gText };
  }

  handleNameEdit() {
    jQuery(document).on("click", ".edit_name_link", function () {
      if ((jQuery(this).parent().parent().parent().find('.edit-name').hasClass('hide'))) {
        jQuery(this).parent().parent().parent().find('.result_text').addClass('hide');
        jQuery(this).parent().parent().parent().find('.edit-name').removeClass('hide');
        jQuery(this).parent().parent().parent().find('.edit-name').focus();
      }
    });

    jQuery(document).on("focusout", ".edit-name", function () {
      jQuery(this).parent().parent().find('.index-span').removeClass('hide');
      jQuery(this).parent().parent().find('.result_text').removeClass('hide');
      jQuery(this).parent().parent().find('.edit-name').addClass('hide');
    });

    jQuery(document).on("focus", ".edit-name", function () {
      jQuery(this).parent().parent().find('.index-span').addClass('hide');
    });

    jQuery(document).on("keyup", ".edit-name", function (e: any) {
      jQuery(this).parent().parent().find('.index-span').addClass('hide');
      jQuery(this).parent().find('.result_text').addClass('hide');
      if (e.which == 13) jQuery(this).focusout();
    });
  }

  accordianOpen(val: any, control: any) {
    this.jsonBuilderHandler.setSelectedControl(control);
    //if(jQuery("a[href$='"+val+"']").parents('.panel').children('.panel-collapse').hasClass('in')) {
    event.stopPropagation();
    event.preventDefault();
    //}
  }

  validateUrl() {
    var urlregex = /(http(s)?:\\)?([\w-]+\.)+[\w-]+[.com|.in|.org]+(\[\?%&=]*)?/;
    if (urlregex.test(this.jsonBuilderHandler.getJSONBuilt().navigate_Url)) {
      this.validUrl = true;
    } else {
      this.validUrl = false;
    }
  }

  toggleDisclaimer() {
    this.isDisclaimers = this._featureAuthService.features.disclaimers.active;
    if (this.isDisclaimers) {
      this.editorControl.result_disclaimer.visible = !this.editorControl.result_disclaimer.visible;
    } else {
      if (!this.editorControl.result_disclaimer.visible) {
        this.editorControl.result_disclaimer.visible = true;
      } else {
        this._featureAuthService.setSelectedFeature('disclaimers');
        jQuery('.disclaimers').addClass('activegreen limited-label');
        this.showPremiumPopup();
      }
    }
  }

  disclaimerClick(){
    if(!this.isDisclaimers){
      this._featureAuthService.setSelectedFeature('disclaimers');
      jQuery('.disclaimers').addClass('activegreen limited-label');
      this.showPremiumPopup();
    }
  }

  isSocialChecked(socialMedia: any) {
    for (let option in this.editorControl.share_links.options) {
      if (this.editorControl.share_links.options[option].type == socialMedia) {
        return this.editorControl.share_links.options[option].selected;
      }
    }
    return true;
  }

  toggleSocialIcon(socialMedia: any) {
    var flag = false;
    for (let option in this.editorControl.share_links.options) {
      if (this.editorControl.share_links.options[option].type == socialMedia) {
        this.editorControl.share_links.options[option].selected = !this.editorControl.share_links.options[option].selected;
        flag = true;
      }
    }
    if (flag == false) {
      let option = (new Item).getOption();
      option.type = socialMedia;
      option.selected = false;
      this.editorControl.share_links.options.push(option);
    }
  }

  scrollToTop() {
    if (jQuery('.settings-header').length) {
      var position = jQuery('.settings-header').position().top;
      jQuery('.no-scroll').animate({ scrollTop: position }, 1000);
    }
  }

  addNewSection(control: any) {
    setTimeout(function () {
      control.delLoading = false;
      control.addLoading = false;
    }, 500);

    this.addItemResult();
  }

  duplicateResult(control: any, i: any) {
    setTimeout(function () {
      control.delLoading = false;
      control.addLoading = false;
    }, 500);
    if (this.jsonBuilderHandler.getJSONBuilt().formula.length <= this.resultSection.items.length) this.jsonBuilderHandler.duplicateFormula(this.jsonBuilderHandler.getJSONBuilt().formula[i]);
    this.jsonBuilderHandler.animInit();
    let ItemResult: any = this.jsonBuilderHandler.duplicateResultItem(this.resultSection, control);

    // save result output item in resultPage
    this._builderService.addItem(this.resultSection._id, ItemResult.item, this.resultSection.items.length - 1).subscribe((response: any) => {
      this.resultSection.items[ItemResult.index] = new Item().deserialize(response);
      this.jsonBuilderHandler.debounce(this.jsonBuilderHandler.animLoad(), 1800);
      this.jsonBuilderHandler.setSelectedControl(this.resultSection.items[this.resultSection.items.length - 1]);
      this.updateCondInitialVisibility();
    }, (error: any) => { });
  }

  deleteResult(formulaIndex: any, resultControl: any) {
    //let formulaIndex = Number(resultControl.formulaIndex);
    if (this.resultSection.items.length > 1) {
      this._builderService.deleteItem(resultControl._id, this.resultSection._id)
        .subscribe((response: any) => {
          if (response.title != 'Not Deleted') {
            this.jsonBuilderHandler.deleteResultSection(this.resultSection, formulaIndex);
            this.jsonBuilderHandler.getJSONBuilt().formula.splice(formulaIndex, 1);
            window.toastNotification('Result Deleted Successfully');
            formulaIndex = (formulaIndex) ? formulaIndex - 1 : 0;
            this.jsonBuilderHandler.setSelectedControl(this.resultSection.items[formulaIndex]);
            if (this.resultSection.items.length <= 3) jQuery('.result-comm').slimScroll({ destroy: true });
          }
          resultControl.delLoading = false;
        }, (error: any) => {
          console.log(error);
        });
    }
  }

  addItemResult() {
    if (this.jsonBuilderHandler.getJSONBuilt().formula.length <= this.resultSection.items.length) this.jsonBuilderHandler.addFormula();
    this.jsonBuilderHandler.animInit();
    let ItemResult: any = this.jsonBuilderHandler.addResultSection(this.resultSection);
    // /* save result output item in resultPage */
    this._builderService.addItem(this.resultSection._id, ItemResult.item, this.resultSection.items.length - 1).subscribe((response: any) => {
      this.resultSection.items[ItemResult.index] = new Item().deserialize(response);
      this.jsonBuilderHandler.debounce(this.jsonBuilderHandler.animLoad(), 1800);
      this.jsonBuilderHandler.setSelectedControl(this.resultSection.items[this.resultSection.items.length - 1]);
    }, (error: any) => {
      //
    });
  }

  toggleSocialLink() {
    this.editorControl.share_links.visible = !this.editorControl.share_links.visible;
  }

  toggleRedo() {
    this.editorControl.result_redo.visible = !this.editorControl.result_redo.visible;
  }

  toggleCta(type: string) {
    this.isCtaAccessible = this._featureAuthService.features.cta.active;
    if (this.isCtaAccessible && this.jsonBuilderHandler.getJSONBuilt().templateType == 'Numerical' || type == 'cta_button') {
      if (type == 'cta_button' && !this._featureAuthService.features.cta.redirect_url) {
        this._featureAuthService.setSelectedFeature('cta','redirect_url');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }
      else if (type == 'cta_shares' && !this._featureAuthService.features.cta.shares) {
        this._featureAuthService.setSelectedFeature('cta','shares');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }
      else if (type == 'cta_likes' && !this._featureAuthService.features.cta.like_follow) {
        this._featureAuthService.setSelectedFeature('cta','like_follow');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }

      this.editorControl.click_button.visible = false;
      this.editorControl.cta_shares.visible = false;
      this.editorControl.cta_likes.visible = false;
      if (type == 'cta_button')
        this.editorControl.click_button.visible = true;
      else if (type == 'cta_shares')
        this.editorControl.cta_shares.visible = true;
      else if (type == 'cta_likes')
        this.editorControl.cta_likes.visible = true;
    } else {
      this._featureAuthService.setSelectedFeature('cta');
      jQuery('.cta').addClass('activegreen limited-label');
      this.showPremiumPopup();
      this.editorControl.cta_shares.visible = false;
      this.editorControl.cta_likes.visible = false;
      this.editorControl.click_button.visible = true;
    }
  }

  showPremiumPopup() {
    jQuery('#premiumModal').modal('show');
    jQuery('.modal-backdrop').insertAfter('#premiumModal');
  }

  optionToggle(type: any, option: any) {
    if (type == "cta_shares") {
      // this.editorControl.cta_shares.options[0].selected = false;
      // this.editorControl.cta_shares.options[1].selected = false;
      // this.editorControl.cta_shares.options[2].selected = false;
      if (option == "facebook") {
        this.editorControl.cta_shares.options[0].selected = !this.editorControl.cta_shares.options[0].selected;
      } else if (option == "twitter") {
        this.editorControl.cta_shares.options[1].selected = !this.editorControl.cta_shares.options[1].selected;
      } else {
        this.editorControl.cta_shares.options[2].selected = !this.editorControl.cta_shares.options[2].selected;
      }
    } else if (type == "cta_likes") {
      // this.editorControl.cta_likes.options[0].selected = false;
      // this.editorControl.cta_likes.options[1].selected = false;
      if (option == "facebook") {
        this.editorControl.cta_likes.options[0].selected = !this.editorControl.cta_likes.options[0].selected;
      } else {
        this.editorControl.cta_likes.options[1].selected = !this.editorControl.cta_likes.options[1].selected;
      }
    }
  }

  toggleSummary() {
    this.editorControl.result_summary.visible = !this.editorControl.result_summary.visible;
    if (this.editorControl.result_summary.visible) {
      this.resultSection.fullWidth = false;
      this.leadformSection.fullWidth = false;
    } else {
      this.resultSection.fullWidth = true;
      this.leadformSection.fullWidth = true;
    }
  }

  toggleFormula(index: any) {
    this.jsonBuilderHandler.updateTemplateQuestionare();
    jQuery('.formula-final').data('formula', index);
  }

  unitsChanged(index: number, value: any) {
    this.jsonBuilderHandler.getJSONBuilt().formula[index].units.postfix = (value == 'Suffix') ? true : false;
  }

  onRealTimeChange() {
    this.isRealTimeResult = true;
    if (this.isRealTimeResult) {
      this.jsonBuilderHandler.getJSONBuilt().realTime = !this.jsonBuilderHandler.getJSONBuilt().realTime;
    } else {
      this._featureAuthService.setSelectedFeature('real_time_results');
      jQuery('.real_time_results').addClass('activegreen limited-label');

      this.showPremiumPopup();
      this.jsonBuilderHandler.getJSONBuilt().realTime = false;
    }
  }

  saveAppsetting() {
    this._builderService.saveAppSetting(this.jsonBuilderHandler.getJSONBuilt())
      .subscribe((response: any) => {
        // this.jsonBuilderHandler.getJSONBuilt().url = response.url;
      }, (error: any) => {
        console.log(error);
      });
  }

  ctaFeatureCheck() {
    if (!this._featureAuthService.features.custom_branding.edit_cta_text) {
      this._featureAuthService.setSelectedFeature('custom_branding','edit_cta_text');
      jQuery('.cta').addClass('activegreen limited-label');
      this.showPremiumPopup();
    }
  }
  ctaCheck() {
    this.isCtaAccessible = this._featureAuthService.features.cta.active;
    if (this.isCtaAccessible === false) {
      this._featureAuthService.setSelectedFeature('cta');
      jQuery('.cta').addClass('activegreen limited-label');

      this.showPremiumPopup();
    }
  }

  onChangeDisclaimer(editorControl: any) {
  }

  callGA(opt: string) {
    switch (opt) {
      case "REALTIMECHANGE":
        ga('markettingteam.send', 'event', 'Builder', 'Toggle', 'ShowResultInReal');
        _kmq.push(['record', 'Builder Show Result in Realtime Toggle']);
        break;

      case "EDITFORMULA":
        ga('markettingteam.send', 'event', 'Builder', 'Click', 'EditResult');
        _kmq.push(['record', 'Builder Edit Result Click']);
        break;

      case "ADDRESULT":
        ga('markettingteam.send', 'event', 'Builder', 'Click', 'AddResult');
        _kmq.push(['record', 'Builder Add Result Click']);
        break;

      case "SUMMARY":
        ga('markettingteam.send', 'event', 'Builder', 'Toggle', 'Summary');
        _kmq.push(['record', 'Builder Results Summary Toggle']);
        break;

      case "SHARERESULT":
        ga('markettingteam.send', 'event', 'Builder', 'Toggle', 'ShareResult');
        _kmq.push(['record', 'Builder Results Share Toggle']);
        break;

      case "REDOCALC":
        ga('markettingteam.send', 'event', 'Builder', 'Toggle', 'RedoCalculation');
        _kmq.push(['record', 'Builder Results Redo Calculation Toggle']);
        break;

      case "DISCLAIMER":
        ga('markettingteam.send', 'event', 'Builder', 'Toggle', 'DisclaimerToggle');
        _kmq.push(['record', 'Builder Results Disclaimer Toggle']);
        break;
    }
  }


  upload(index: any) {
    let self = this;
    filepicker.setKey(this.filePickerKey);
    filepicker.pick({
      mimetypes: ['image/*'], imageQuality: 50
    }, (InkBlob: any) => {
      this._outcomeService.getSelectedFormula().result = InkBlob.url;
      this._outcomeService.getSelectedFormula().imageName = InkBlob.filename;
      jQuery('#filepicker_dialog_container').find('a').click();
      setTimeout(function () {
        jQuery('.page_2').click();
      }, 500);
    }, (FPError: any) => {
      console.log(FPError.toString());
    });
  }

  uploadNumerical(control: any) {
    let self = this;
    filepicker.setKey(this.filePickerKey);
    filepicker.pick({
      mimetypes: ['image/*'], imageQuality: 50
    }, (InkBlob: any) => {
      control.imageURL = InkBlob.url;
    }, (FPError: any) => {
      console.log(FPError.toString());
    });
  }

  copyToAll(type: string) {
    let text: string;
    if (type == 'header') {
      text = this._outcomeService.getSelectedFormula().decimal;
      this.jsonBuilderHandler.getJSONBuilt().formula.map((formula: any) => {
        formula.decimal = text;
        return formula;
      });
      window.toastNotification('Header has been applied to all outcomes');
    } else if (type == 'button') {
      text = this._outcomeService.getSelectedFormula().units.preValue;
      this.jsonBuilderHandler.getJSONBuilt().formula.map((formula: any) => {
        formula.units.preValue = text;
        return formula;
      });
      window.toastNotification('Button CTA has been applied to all outcomes');
    } else if (type == 'url') {
      text = this._outcomeService.getSelectedFormula().units.postValue;
      this.jsonBuilderHandler.getJSONBuilt().formula.map((formula: any) => {
        formula.units.postValue = text;
        return formula;
      });
      window.toastNotification('URL has been applied to all outcomes');
    } else if (type == 'button_heading') {
      text = this._outcomeService.getSelectedFormula().heading;
      this.jsonBuilderHandler.getJSONBuilt().formula.map((formula: any) => {
        formula.heading = text;
        return formula;
      });
      window.toastNotification('Button Heading has been applied to all outcomes');
    }
  }

  toggleVariableCtaOutcome() {
    if (this._featureAuthService.features.variable_cta.active) {
      this.jsonBuilderHandler.getJSONBuilt().isVariableCta = !this.jsonBuilderHandler.getJSONBuilt().isVariableCta;
    } else {
      if (this.jsonBuilderHandler.getJSONBuilt().isVariableCta) {
        this.jsonBuilderHandler.getJSONBuilt().isVariableCta = false;
      } else {
        this._featureAuthService.setSelectedFeature('variable_cta');
        jQuery('.variable_cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
      }
      this._featureAuthService.setSelectedFeature('variable_cta');
      this.showPremiumPopup();
    }
  }

  updateAllOutcomeCta() {
    if (!this.jsonBuilderHandler.getJSONBuilt().isVariableCta) {
      let index = this.jsonBuilderHandler.getJSONBuilt().formula.indexOf(this._outcomeService.getSelectedFormula());
      this.jsonBuilderHandler.getJSONBuilt().formula.map((formula: any) => {
        formula.links = this.jsonBuilderHandler.getJSONBuilt().formula[index].links;
        formula.units.postfix = this.jsonBuilderHandler.getJSONBuilt().formula[index].units.postfix;
        formula.units.prefix = this.jsonBuilderHandler.getJSONBuilt().formula[index].units.prefix;
        formula.units.preValue = this.jsonBuilderHandler.getJSONBuilt().formula[index].units.preValue;
        formula.units.postValue = this.jsonBuilderHandler.getJSONBuilt().formula[index].units.postValue;
        formula.isValid = this.jsonBuilderHandler.getJSONBuilt().formula[index].isValid;
        formula.heading = this.jsonBuilderHandler.getJSONBuilt().formula[index].heading;
        return formula;
      });
    }
  }

  toggleConditionalCta(control: any) {
    if (this._featureAuthService.features.variable_cta.active) {
      control.optionImageVisible = !control.optionImageVisible;
      this.jsonBuilderHandler.conditional.conditionalVar = false;
      if (control.optionImageVisible) {
        this.editorControl.click_button.visible = true;
        this.editorControl.cta_shares.visible = false;
        this.editorControl.cta_likes.visible = false;
      }
    } else {
      this._featureAuthService.setSelectedFeature('disclaimers');
      jQuery('.disclaimers').addClass('activegreen limited-label');
      this.showPremiumPopup();
      control.optionImageVisible = false;
    }
  }
  editOutcome(event: any) {
    let formulaValue: string = this._outcomeService.getSelectedFormula().value;
    this.jsonBuilderHandler.getTemplateQuestionare().forEach(function (item: any) {
      if (item.type == 'selectbox' || item.type == 'radio_button') {
        item.options.forEach(function (option: any) {
          let type: any = option.value;
          if (type && type != '') {
            let typeArray = type.split(',');
            let valueIndex = typeArray.indexOf(formulaValue);
            if (valueIndex != (-1)) {
              typeArray[valueIndex] = event.target.value.trim();
              option.value = typeArray.toString();
            }
          }
        });
      }
    });
    this._outcomeService.getSelectedFormula().value = event.target.value.trim();
  }

  disableSpace(event: any): any {
    this._outcomeService.getSelectedFormula().value = event.target.value.trim().replace(/\s/g, '');
  }

  textAreaAdjust(event: any) {
    jQuery('.big-text').css('height', jQuery('.big-text').prop('scrollHeight'));
  }

  onNavigateURLChange($event: any) {
    let url = $event.target.value;
    if (!/^(f|ht)tps?:\/\//i.test(url)) {
      url = "http://" + url;
    }
    this.jsonBuilderHandler.getJSONBuilt().navigate_Url = url;
  }

  onRecomNavigateURLChange($event: any) {
    let url = $event.target.value;
    if (!/^(f|ht)tps?:\/\//i.test(url)) {
      url = "http://" + url;
    }
    this._outcomeService.getSelectedFormula().units.postValue = url;
  }

  showMessage() {
    // this.isCtaAccessible = this._featureAuthService.features.cta.active;
    // if (!this.isCtaAccessible) {
    //   this._featureAuthService.setSelectedFeature('cta');
    //   jQuery('.cta').addClass('activegreen limited-label');
    //   this.showPremiumPopup();
    // } else {
      bootbox.dialog({
        closeButton: false, message: `
                      <div class="bootbox-body-left">
                          <div class="mat-icon">
                            <i class="material-icons">error</i>
                          </div>
                      </div>
                      <div class="bootbox-body-right">
                        <p>Social Sharing buttons are disabled as the lead generation form is on the results page!</p>
                      </div>
                  `, buttons: {
          success: {
            label: "OK", className: "btn btn-ok btn-hover", callback: function () { }
          }
        }
      });
    // }
  }

  changeOutcomeType(type: string) {
    this.isCtaAccessible = this._featureAuthService.features.cta.active;
    if (this.isCtaAccessible || type == 'Button') {
      if (type == 'Button' && !this._featureAuthService.features.cta.redirect_url) {
        this._featureAuthService.setSelectedFeature('cta','redirect_url');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }
      else if (type == 'Share' && !this._featureAuthService.features.cta.shares) {
        this._featureAuthService.setSelectedFeature('cta','shares');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }
      else if (type == 'Like' && !this._featureAuthService.features.cta.like_follow) {
        this._featureAuthService.setSelectedFeature('cta','like_follow');
        jQuery('.cta').addClass('activegreen limited-label');
        this.showPremiumPopup();
        return;
      }

      this._outcomeService.getSelectedFormula().units.prefix = false;
      this._outcomeService.getSelectedFormula().isValid = false;
      this._outcomeService.getSelectedFormula().units.postfix = false;

      if (type == 'Button')
        this._outcomeService.getSelectedFormula().isValid = true;
      else if (type == 'Like')
        this._outcomeService.getSelectedFormula().units.prefix = true;
      else if (type == 'Share')
        this._outcomeService.getSelectedFormula().units.postfix = true;
      this.updateAllOutcomeCta();
    } else {
      this._featureAuthService.setSelectedFeature('cta');
      jQuery('.cta').addClass('activegreen limited-label');

      this.showPremiumPopup();
      this.editorControl.cta_shares.visible = false;
      this.editorControl.cta_likes.visible = false;
      this.editorControl.click_button.visible = true;
    }
  }


  addOutcome() {
    let length: string = (this.jsonBuilderHandler.getJSONBuilt().formula.length + 1).toString();
    this.jsonBuilderHandler.getJSONBuilt().addformula('<p>New Outcome</p>',
      'outcome' + ((Math.floor((Math.random() * 1000) + 1)) * parseInt(length)),
      'https://cdn.filepicker.io/api/file/lHqm5ge9RdySNwOzKmGA', 'Outcome description will come here',
      'Page title will come here', 'Button Text', environment.PROTOCOL + environment.APP_EXTENSION, 'true');

    // if (parseInt(length) == 1) {
    let outcomeIndex = this.jsonBuilderHandler.getJSONBuilt().formula.length - 1;
    this._outcomeService.setSelectedFormula(this.jsonBuilderHandler.getJSONBuilt().formula[outcomeIndex]);
    // }
    this.initWysiwyg();
  }

  duplicateOutcome(currentOutcome: any, index: any) {
    //add new formula
    this.jsonBuilderHandler.getJSONBuilt().addformula(
      currentOutcome.name,
      currentOutcome.value + '_' + (Math.floor((Math.random() * Math.random() * 100) + 1)),
      currentOutcome.result,
      currentOutcome.html,
      currentOutcome.decimal,
      currentOutcome.units.preValue,
      currentOutcome.units.postValue,
      currentOutcome.range.status,
    );
    //remove newly added formula and add it adjacent to current
    let removed = this.jsonBuilderHandler.getJSONBuilt().formula.splice(this.jsonBuilderHandler.getJSONBuilt().formula.length - 1, 1);
    this.jsonBuilderHandler.getJSONBuilt().formula.splice(index + 1, 0, removed[0]);
    this._outcomeService.setSelectedFormula(this.jsonBuilderHandler.getJSONBuilt().formula[index + 1]);
    this.initWysiwyg();
  }


  deleteOutcome(formulaIndex: any) {
    let self: any = this;
    bootbox.dialog({
      size: 'small',
      message: `
                <div class="one-line-bootbox">
                    <div class="bootbox-body-left">
                        <div class="mat-icon">
                            <i class="material-icons">error</i>
                        </div>
                    </div>
                    <div class="bootbox-body-right">
                        <p class="">Are you sure you want to delete this outcome?</p>
                    </div>
                </div>
            `,
      buttons: {
        cancel: {
          label: "No",
          className: "btn-cancel btn-cancel-hover"
        },

        success: {
          label: "Yes",
          className: "btn btn-ok btn-hover",
          callback: function () {
            let formulaValue: string = self.jsonBuilderHandler.getJSONBuilt().formula[formulaIndex].value;
            self.jsonBuilderHandler.getTemplateQuestionare().forEach(function (item: any) {
              if (item.type == 'selectbox' || item.type == 'radio_button' || item.type == 'checkbox') {
                item.options.forEach(function (option: any) {
                  let type: any = option.value;
                  if (type && type != '') {
                    let typeArray = type.split(',');
                    let valueIndex = typeArray.indexOf(formulaValue);
                    if (valueIndex != (-1)) {
                      typeArray.splice(valueIndex, 1);
                      option.value = typeArray.toString();
                      self._ItemTrackService.setUnSavedItems(item);
                    }
                  }
                });
              }
            });

            self.jsonBuilderHandler.getJSONBuilt().formula.splice(formulaIndex, 1);
            /* select another model after delete*/
            if (self.jsonBuilderHandler.getJSONBuilt().formula.length == 0) {
              self._outcomeService.setSelectedFormula(undefined);
            }
            else
              self._outcomeService.setSelectedFormula(self.jsonBuilderHandler.getJSONBuilt().formula[formulaIndex - 1]);

            self.initWysiwyg();
          }
        }
      }
    });
  }

  updateHeading() {
    if (this.jsonBuilderHandler.getJSONBuilt().templateType == 'Numerical') {
      this.editorControl.cta_shares.options[0].title = this.editorControl.cta_likes.options[0].title = this.editorControl.click_button.props.unit;
    } else {
      for (let linktype in this.shareLinks) {
        for (let socialType in this.shareLinks[linktype]) {
          this.shareLinks[linktype][socialType].heading = this._outcomeService.getSelectedFormula().heading;
        }
      }
    }
  }
}



